import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";

const StockDetail = () => {
  const { channel_id, company_id, product_unit_id } = useParams();
  const [stocks, setStocks] = useState(null);
  const [filter, setFilter] = useState("");

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getStock = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/stocks/extended/detail/${company_id}/${channel_id}/${product_unit_id}`,
          config
        );
        console.log(res.data.data);
        setStocks(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getStock();
  }, [channel_id, company_id, product_unit_id]);

  return (
    <div className="stock" id="stock">
      <Header title="Stock Detail" back={true} icon="" />
      <div className="d-flex align-items-center">
        <input
          type="text"
          className="form-control"
          name="companys"
          style={{ height: "41px", width: "261px" }}
          placeholder="Search customer name"
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
        />
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No.</th>
              <th>Created At</th>
              <th>Invoice number</th>
              <th>Sales</th>
              <th>Deal At</th>
              <th>Expected Delivery At</th>
              <th>Outstanding Shipment</th>
              <th>Ready</th>
            </tr>
          </thead>
          <tbody>
            {stocks
              ? stocks.map((stock, idx) => {
                  return (
                    <tr>
                      <td>{idx + 1}</td>
                      <td>{stock.created_at}</td>
                      <td>{stock.invoice_number}</td>
                      <td>{stock.sales}</td>
                      <td>{stock.deal_at}</td>
                      <td>{stock.expected_shipping_datetime}</td>
                    </tr>
                  );
                })
              : ""}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default StockDetail;
