import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { pickColor } from "../../../api/pickColor";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import FollowUpPopup from "../../../components/Popup/Popup Component/FollowUpPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";

const SalesRevenueSales = () => {
  const { user_id, month, company_id } = useParams();

  const [popup, setPopup] = useState(false);

  const [salesRevenue, setSalesRevenue] = useState(null);
  const [filterSales, setFilterSales] = useState("");
  const [date, setDate] = useState(month);

  const [userId, setUserId] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesRevenue = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${moment(
            date
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&filter[end_before]=${moment(date)
            .endOf("month")
            .format(
              "YYYY-MM-DD"
            )}&filter[reportable_type]=USER&filter[company_id]=&filter[supervisor_type_level]=&filter[descendant_of]=${user_id}`,
          config
        );
        setSalesRevenue(res.data.data);
        console.log(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getSalesRevenue();
  }, [user_id, date]);

  const handleFollowUpPopup = (user_id) => {
    setPopup(true);
    setUserId(user_id);
  };

  return salesRevenue ? (
    <div>
      <PopupLayout
        Popup={popup}
        setPopup={() => setPopup()}
        component={
          <FollowUpPopup month={date} userId={userId} company_id={company_id} />
        }
      />
      <Header title={"Sales Revenue - Sales"} back={true} notif={true} />
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search Sales"
            onChange={(e) => setFilterSales(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>BUM Name</th>
              <th>Revenue</th>
              <th>Target</th>
              <th>Meet Goal</th>
              <th>Follow Up</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {salesRevenue
              .filter((data) => {
                if (filterSales === "") {
                  return data;
                } else if (
                  data.report.name
                    .toLowerCase()
                    .includes(filterSales.toLowerCase())
                ) {
                  return data;
                }
              })
              .map((data, i) => {
                const percentage = Math.round(
                  (data.value.value / data.target.value) * 100
                );
                return (
                  <tr key={data.id}>
                    <td>{i + 1}.</td>
                    <td>{data.report.name}</td>
                    <td>{data.value.format}</td>
                    <td>{data.target.format}</td>
                    <td
                      style={{
                        color: pickColor(percentage),
                        fontWeight: "bold",
                      }}
                    >
                      {percentage === Infinity || isNaN(percentage)
                        ? "0"
                        : percentage}{" "}
                      %
                    </td>
                    <td>
                      <div
                        onClick={() => handleFollowUpPopup(data.user.id)}
                        style={{
                          background: "#0553B7",
                          textAlign: "center",
                          width: "70%",
                          borderRadius: "8px",
                          color: "#fff",
                          padding: "5px",
                          cursor: "pointer",
                        }}
                      >
                        See Follow Up
                      </div>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/sales_revenue/${data.user.id}/${data.user.channel_id}/${data.user.company_id}/${date}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default SalesRevenueSales;
