import React from "react";
import { formatCurrency } from "../../../api/format";
import { isValidate } from "../../../lib/Validate";
import Button from "../../Button/Button";
import "./SummaryCart.scss";

const SummaryCart = (props) => {
  const { totalPrice, handleClick } = props;
  return (
    <div className="summary">
      <h5>Summary</h5>

      <div className="d-flex justify-content-between">
        <p>Total Price ( {!isValidate(totalPrice.qty) && totalPrice.qty} )</p>
        <p>
          {!isValidate(totalPrice.totalPrice) &&
            formatCurrency(totalPrice.totalPrice)}
        </p>
      </div>
      <hr />
      <div className="d-flex justify-content-between">
        <p>
          <b>Grand Total</b>
        </p>
        <p>
          <b>
            {!isValidate(totalPrice.totalPrice) &&
              formatCurrency(totalPrice.totalPrice)}
          </b>
        </p>
      </div>
      <div className="btn-checkout">
        <div onClick={() => handleClick()}>
          {/* to="/dashboard/product/checkout" */}
          <Button
            title="Checkout"
            btnStyle="primary-btn"
            icon=""
            btnWidth="100%"
          />
        </div>
      </div>
    </div>
  );
};

export default SummaryCart;
