import {
  TARGET_LOADED,
  TARGET_ERROR,
  SETTLEMENT_LOADED,
  SETTLEMENT_ERROR,
  INTERIOR_DESIGN_LOADED,
  INTERIOR_DESIGN_ERROR,
  CHANNELS_LOADED,
  CHANNELS_ERROR,
  USERS_LIST_LOADED,
  USERS_LIST_ERROR,
  SALES_RANKING_LOADED,
  SALES_RANKING_ERROR,
  FOLLOW_UP_LOADED,
  FOLLOW_UP_ERROR,
  FIRST_MONTH_LOADED,
  FIRST_MONTH_ERROR,
  SECOND_MONTH_LOADED,
  SECOND_MONTH_ERROR,
  ACTIVITIES_LOADED,
  ACTIVITIES_ERROR,
  DESIGNERS_LOADED,
  DESIGNERS_ERROR,
} from "../action/types";

const initalState = {
  target: null,
  settlement: null,
  interiorDesign: null,
  channels: null,
  usersList: null,
  salesRanking: null,
  followUp: null,
  loading: true,
  firstMonth: null,
  secondMonth: null,
  activities: null,
  designers: null,
};

export default function foo(state = initalState, action) {
  const { type, payload } = action;

  switch (type) {
    case TARGET_LOADED:
      return {
        ...state,
        loading: false,
        target: payload,
      };
    case SETTLEMENT_LOADED:
      return {
        ...state,
        loading: false,
        settlement: payload,
      };
    case INTERIOR_DESIGN_LOADED:
      return {
        ...state,
        loading: false,
        interiorDesign: payload,
      };
    case CHANNELS_LOADED:
      return {
        ...state,
        loading: false,
        channels: payload,
      };
    case USERS_LIST_LOADED:
      return {
        ...state,
        loading: false,
        usersList: payload,
      };
    case SALES_RANKING_LOADED:
      return {
        ...state,
        loading: false,
        salesRanking: payload,
      };
    case FOLLOW_UP_LOADED:
      return {
        ...state,
        loading: false,
        followUp: payload,
      };
    case FIRST_MONTH_LOADED:
      return {
        ...state,
        loading: false,
        firstMonth: payload,
      };
    case SECOND_MONTH_LOADED:
      return {
        ...state,
        loading: false,
        secondMonth: payload,
      };
    case ACTIVITIES_LOADED:
      return {
        ...state,
        loading: false,
        activities: payload,
      };
    case DESIGNERS_LOADED:
      return {
        ...state,
        loading: false,
        designers: payload,
      };
    case TARGET_ERROR:
    case SETTLEMENT_ERROR:
    case INTERIOR_DESIGN_ERROR:
    case CHANNELS_ERROR:
    case USERS_LIST_ERROR:
    case SALES_RANKING_ERROR:
    case FOLLOW_UP_ERROR:
    case FIRST_MONTH_ERROR:
    case SECOND_MONTH_ERROR:
    case ACTIVITIES_ERROR:
    case DESIGNERS_ERROR:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
}
