import axios from "axios";
import { mainUrl } from "../../api/api";
import {
  ALL_USER_LIST_ERROR,
  ALL_USER_LIST_LOADED,
  CATEGORIES_ERROR,
  CATEGORIES_LOADED,
  CUSTOMERS_ERROR,
  CUSTOMERS_LOADED,
  SUBCATEGORIES_ERROR,
  SUBCATEGORIES_LOADED,
} from "./types";

export const loadCategories = () => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(`${mainUrl}/leads/categories`, config);
    dispatch({
      type: CATEGORIES_LOADED,
      payload: res.data.data,
    });
    // console.log(res.data.data);
  } catch (error) {
    dispatch({
      type: CATEGORIES_ERROR,
    });
    console.error(error);
  }
};

export const loadSubCategories = (category_id) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(
      `${mainUrl}/leads/sub-categories/${category_id ? category_id : 2}`,
      config
    );
    dispatch({
      type: SUBCATEGORIES_LOADED,
      payload: res.data.data,
    });
  } catch (error) {
    dispatch({
      type: SUBCATEGORIES_ERROR,
    });
    console.error(error);
  }
};

export const loadUserList = (user) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(
      `${mainUrl}/users?filter[supervisor_id]=${
        user.as === "Director" ? "" : user.id === null ? "" : null
      }&filter[company_id]=${user.company.id}`,
      config
    );
    dispatch({
      type: ALL_USER_LIST_LOADED,
      payload: res.data.data,
    });
  } catch (error) {
    dispatch({
      type: ALL_USER_LIST_ERROR,
    });
    console.error(error);
  }
};

