import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../../api/api";
import Header from "../../../../components/Header/Header";
import PaymentCard from "../../../../components/Payment/PaymentCard";

const PaymentByType = () => {
  const { orderId, payment_category, payment_category_id } = useParams();
  const [paymentTypes, setPaymentTypes] = useState(null);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getPaymentTypes = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/payment-types?filter[payment_category_id]=${payment_category_id}`,
          config
        );
        console.log(res.data.data);
        setPaymentTypes(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getPaymentTypes();
  }, [payment_category_id]);
  return (
    paymentTypes && (
      <div>
        <Header title={payment_category} icon="" back="true" />
        {paymentTypes.map((data, i) => {
          return (
            <div key={data.id}>
              {i === 0 ? "" : <hr />}
              <PaymentCard
                link={`/dashboard/confirm_payment/${orderId}/${data.id}`}
                bankLogo={data.images[0].url}
                label={data.name}
              />
            </div>
          );
        })}
      </div>
    )
  );
};

export default PaymentByType;
