import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { mainUrl } from "../../api/api";
import Button from "../Button/Button";
import "./FilterLeadsReport.scss";
// import { DateRangePicker, DateRange} from 'react-date-range';
import "react-date-range/dist/styles.css"; // main css file
import "react-date-range/dist/theme/default.css"; // theme css file
// import PopupLayout from "../../components/Popup/PopupLayout";

const FilterLeadsReport = ({
  target: { channels },
  filterCompany,
  setFilterChannels,
  setFilterCompany,
  setFilterDate,
  setFilterSupervisor,
  download,
  setFilterStartDate,
  setFilterEndDate,
  company,
  channel,
  supervisor,
}) => {
  const [supervisors, setSupervisors] = useState(null);
  // const [newCustomerPopup, setNewCustomerPopup] = useState(false);
  // const selectionRange = {
  //   startDate: new Date(),
  //   endDate: new Date(),
  //   key: 'selection',
  // }
  // const [rangedDate, setRangedDate] = useState(selectionRange)
  // const handleSelect = async (ranges) => {
  //   await setRangedDate(ranges?.selection)
  //   await setFilterDate(ranges?.selection)
  //   console.log(ranges?.selection.startDate,'selection')

  // }

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSupervisors = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/users?filter[type]=SUPERVISOR&filter[descendant_of]=10&perPage=50`,
          config
        );
        setSupervisors(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    getSupervisors();
  }, []);

  return (
    <div
      className="filter-leads-report d-flex justify-content-between"
      id="filter-leads-report"
    >
      <div className="report-filter-form d-flex">
        {company && (
          <select
            className="form-select"
            name="companys"
            value={filterCompany}
            onChange={(e) => setFilterCompany(e.target.value)}
          >
            <option value="">Select Company</option>
            <option value={1}>Melandas</option>
            <option value={2}>Dio Living</option>
          </select>
        )}
        {channel && (
          <select
            className="form-select"
            name="channels"
            onChange={(e) => setFilterChannels(parseInt(e.target.value))}
          >
            <option value="">Select Channel</option>
            {channels &&
              channels.data.map((channel) => {
                return (
                  <option value={channel.id} key={channel.id}>
                    {channel.name}
                  </option>
                );
              })}
          </select>
        )}
        {supervisor && (
          <select
            className="form-select"
            name="supervisors"
            onChange={(e) => setFilterSupervisor(parseInt(e.target.value))}
          >
            <option value="">Select Supervisor</option>
            {supervisors &&
              supervisors.data.map((supervisor) => {
                return (
                  <option value={supervisor.id} key={supervisor.id}>
                    {supervisor.name}
                  </option>
                );
              })}
          </select>
        )}

        {/* <div
          onClick={() => setNewCustomerPopup(!newCustomerPopup)}
          style={{ width: "fit-content" }}
        >
          <Button
            title="Select Date"
            icon=""
            btnWidth="150px"
            btnStyle="primary-btn"
          />
        </div>
        <PopupLayout
        Popup={newCustomerPopup}
        setPopup={() => setNewCustomerPopup()}
        component={
          <DateRangePicker
          direction="horizontal"
          ranges={[rangedDate]}
          onChange={handleSelect}
        />
        }
      /> */}

        {/* <input
          type="date"
          id="start"
          name="start"
          className="form-control"
          placeholder="Start Month"
          onChange={(e) => setFilterDate(e.target.value)}
        /> */}
        <div className="d-flex align-items-center ms-1">
          <label>Start: </label>
          <input
            type="date"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start"
            onChange={(e) => setFilterStartDate(e.target.value)}
          />
        </div>
        <div className="d-flex align-items-center ms-1 me-3">
          <label>End: </label>
          <input
            type="date"
            id="end"
            name="end"
            className="form-control"
            placeholder="End"
            onChange={(e) => setFilterEndDate(e.target.value)}
          />
        </div>
      </div>
      {download && (
        <div>
          <div onClick={() => download()} style={{ width: "fit-content" }}>
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = (state) => ({
  target: state.target,
});

export default connect(mapStateToProps)(FilterLeadsReport);
