  const [reportDataDeal, setReportDataDeal] = useState(null);
  const [reportDataHot, setReportDataHot] = useState(null);
  // filter
  const [filterCompany, setFilterCompany] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterSupervisior, setFilterSupervisor] = useState();
  const [filterDate, setFilterDate] = useState();
  const [filterStartDate, setFilterStartDate] = useState(
    start !== "now" ? start : ""
  );
  const [filterEndDate, setFilterEndDate] = useState(end !== "now" ? end : "");

  const [isLoading, setIsLoading] = useState(false);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getReportBrands = async () => {
      setIsLoading(true);
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-leads/details?start_date=${
            filterStartDate !== "now"
              ? moment(filterStartDate).format("YYYY-MM-DD")
              : ""
          }&end_date=${
            filterEndDate !== "now"
              ? moment(filterEndDate).format("YYYY-MM-DD")
              : ""
          }&company_id=${filterCompany ? filterCompany : ""}&${
            user_type === "bum"
              ? "supervisor_id=" + parseInt(user_id)
              : user_type === "channel"
              ? "channel_id=" + parseInt(user_id)
              : user_type === "sales"
              ? "user_id=" + parseInt(user_id)
              : ""
          }`,
          config
        );
        console.log("result");
        setReportData(res.data);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
      }
    };

    getReportBrands();
  }, [
    filterCompany,
    filterChannels,
    filterSupervisior,
    filterStartDate,
    filterEndDate,
    user_id,
    user_type,
  ]);
