              <tr>
                <th></th>
                <th>Drop</th>
                <th>Cold</th>
                <th>Warm</th>
                <th>Hot</th>
                <th>ESTIMATED</th>
                <th>QUOTATION</th>
                <th>Action</th>

                <th>INVOICE PRICE</th>
                <th>AMOUNT PAID</th>
                <th>Action</th>
              </tr>
