export const loadTarget =
  (start, end, company_id, channel_id, user_id) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    //   ?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=2022-02-01&filter[end_before]=2022-02-28
    let startDate = "";
    let endDate = "";
    let reportable_type = "";
    if (start) {
      startDate = `&filter[start_after]=${start}`;
    } else {
      startDate = "";
    }

    if (end) {
      endDate = `&filter[end_before]=${end}`;
    } else {
      endDate = ``;
    }

    if (company_id && !channel_id && !user_id) {
      reportable_type = `&filter[reportable_type]=COMPANY&filter[reportable_ids]=${company_id}`;
    } else if (
      (company_id && channel_id && !user_id) ||
      (!company_id && channel_id && !user_id)
    ) {
      reportable_type = `&filter[reportable_type]=CHANNEL&filter[reportable_ids]=${channel_id}`;
    } else if (
      (company_id && channel_id && user_id) ||
      (!company_id && channel_id && user_id) ||
      (!company_id && !channel_id && user_id)
    ) {
      reportable_type = `&filter[reportable_type]=USER&filter[reportable_ids]=${user_id}`;
    } else {
      reportable_type = "";
    }
    // console.log(reportable_type);
    try {
      const res = await axios.get(
        `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE${startDate}${endDate}${reportable_type}&filter[is_dashboard]=1`,
        config
      );
      // console.log(config);

      dispatch({
        type: TARGET_LOADED,
        payload: res.data,
      });
      // console.log(res.data);
    } catch (error) {
      dispatch({
        type: TARGET_ERROR,
      });
      console.log(error);
    }
  };

