export const loadInteriorDesign =
  (start, end, company_id, channel_id) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    //   ?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=2022-02-01&filter[end_before]=2022-02-28

    let startDate = "";
    let endDate = "";
    let reportable_type = "";

    if (start) {
      startDate = `&start_at=${start}`;
    } else {
      startDate = "";
    }

    if (end) {
      endDate = `&end_at=${end}`;
    } else {
      endDate = ``;
    }

    if (company_id && !channel_id) {
      reportable_type = `company_id=${company_id}`;
    } else if (company_id && channel_id) {
      reportable_type = `channel_id=${channel_id}`;
    } else {
      reportable_type = "";
    }
    try {
      const res = await axios.get(
        `${mainUrl}/dashboard/interior-designs?${reportable_type}${startDate}${endDate}`,
        config
      );
      // console.log(config);

      dispatch({
        type: INTERIOR_DESIGN_LOADED,
        payload: res.data,
      });
      // console.log(res.data);
    } catch (error) {
      dispatch({
        type: INTERIOR_DESIGN_ERROR,
      });
      console.log(error);
    }
  };

export const loadChannels = (company_id) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(
      `${mainUrl}/channels?filter[company_id]=${
        company_id ? company_id : ""
      }&perPage=100`,
      config
    );
    // console.log(config);

    dispatch({
      type: CHANNELS_LOADED,
      payload: res.data,
    });
    // console.log(res.data);
  } catch (error) {
    dispatch({
      type: CHANNELS_ERROR,
    });
    console.log(error);
  }
};

export const loadUsersList =
  (channel_id, user_id, company_id) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    let filter = "";
    let filterChannel;

    if (channel_id) {
      filterChannel = `?filter[channel_id]=${channel_id}`;
    } else {
      filterChannel = "?";
    }

    if (user_id && !company_id) {
      filter = `filter[supervisor_id]=${user_id}`;
    } else if (!user_id && company_id) {
      filter = `filter[company_id]=${company_id}`;
    } else if (user_id && company_id) {
      filter = `filter[supervisor_id]=${user_id}&?filter[company_id]=${company_id}`;
    } else {
      filter = "";
    }

    try {
      const res = await axios.get(
        `${mainUrl}/users${filterChannel}${filter}&filter[type]=SALES&perPage=100`,
        config
      );
      // console.log(config);

      dispatch({
        type: USERS_LIST_LOADED,
        payload: res.data,
      });
      // console.log(res.data);
    } catch (error) {
      dispatch({
        type: USERS_LIST_ERROR,
      });
      console.log(error);
    }
  };

export const loadSalesRanking = (filterRanking) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };
  let ranking = "value";
  if (filterRanking === 1) {
    ranking = "value";
  } else if (filterRanking === 2) {
    ranking = "target";
  }
  // console.log(ranking);
  try {
    const res = await axios.get(
      `${mainUrl}/dashboard/top-sales/${ranking}?start_at=2022-02-01&end_at=2022-02-28`,
      config
    );
    // console.log(config);

    dispatch({
      type: SALES_RANKING_LOADED,
      payload: res.data,
    });
    // console.log(res.data);
  } catch (error) {
    dispatch({
      type: SALES_RANKING_ERROR,
    });
    console.log(error);
  }
};

