export const loadFirstMonth = (month) => async (dispatch) => {
  let firstMonth = {
    start: moment(month).startOf("month").format("YYYY-MM-DD"),
    end: moment(month).endOf("month").format("YYYY-MM-DD"),
    month: month,
  };

  try {
    dispatch({
      type: FIRST_MONTH_LOADED,
      payload: firstMonth,
    });
    // console.log(res.data);
  } catch (error) {
    dispatch({
      type: FIRST_MONTH_ERROR,
    });
    console.log(error);
  }
};

