export const loadActivities = (date, type) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  let startDate = "";
  let endDate = "";
  let followUpType = "";

  // let reportable_type = "";

  if (date) {
    let start = moment(date).startOf("month").format("YYYY-MM-DD");
    let end = moment(date).endOf("month").format("YYYY-MM-DD");
    if (type) {
      startDate = `&filter[follow_up_datetime_before]=${end}`;
    } else {
      startDate = `filter[follow_up_datetime_before]=${end}`;
    }

    endDate = `&filter[follow_up_datetime_after]=${start}`;
  } else {
    startDate = "";
    endDate = "";
  }

  if (type) {
    followUpType = `filter[follow_up_method]=${type}`;
  } else {
    followUpType = "";
  }

  try {
    const res = await axios.get(
      `${mainUrl}/activities?${followUpType}${startDate}${endDate}`,
      config
    );
    dispatch({
      type: ACTIVITIES_LOADED,
      payload: res.data,
    });
    // console.log(res.data);
  } catch (error) {
    dispatch({
      type: ACTIVITIES_ERROR,
    });
    console.log(error);
  }
};

