import React, { useState, useEffect } from "react";
import { connect, useDispatch } from "react-redux";
// import { Link } from "react-router-dom";
import Button from "../../../components/Button/Button";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import ComporationPopUp from "../../../components/Popup/Popup Component/ComporationPopUp";
// import PopupLayout from "../../../components/Popup/PopupLayout";
import Popup from "../../../components/Popup/Popup";
import { loadActivities } from "../../../redux/action/target";
// import User from "../../../components/UserCard/User";
import "./Dashboard.scss";
import Report from "./Report";
import TopPerformance from "./TopPerformance";
import DownloadReport from "../../../components/Popup/Popup Components/DownloadReport/DownloadReport";

const Dashboard = ({ auth: { user, loading } }) => {
  const [comparationPopup, setComparationPopup] = useState(false);
  const [downloadReportPopup, setDownloadReportPopup] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadActivities());
  }, [dispatch]);

  return !loading && user ? (
    <div>
      <Header
        title={"Dashboard"}
        back={false}
        notif={true}
        user={true}
        data={user.data}
      />
      {/* <User data={user.data} /> */}
      {/* Popup */}
      <Popup popup={comparationPopup} setPopup={setComparationPopup}>
        <ComporationPopUp />
      </Popup>
      <Popup popup={downloadReportPopup} setPopup={setDownloadReportPopup}>
        <DownloadReport />
      </Popup>
      <Report />
      {/* <div className="d-flex justify-content-between"> */}
        {/* <div
          onClick={() => setDownloadReportPopup(!downloadReportPopup)}
          style={{ width: "50%", padding: "12px" }}
        >
          <Button
            icon=""
            title="Download Report"
            style={{ width: "100% !important" }}
            btnWidth="100%"
            btnStyle={"primary-btn"}
          />
        </div> */}
        <div
          onClick={() => setComparationPopup(!comparationPopup)}
        >
          <Button
            icon=""
            title="Monthly Comparison"
            style={{ width: "100% !important" }}
            btnWidth="100%"
            btnStyle={"primary-btn"}
          />
        </div>
      {/* </div> */}

      <TopPerformance userData={user} />
      {/* <PopupLayout
        Popup={comparationPopup}
        setPopup={() => setComparationPopup()}
        component={<ComporationPopUp />}
      /> */}
    </div>
  ) : (
    <Loaded />
  );
};
const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(Dashboard);
