import React from "react";
import ReactApexChart from "react-apexcharts";

const ComparationBar = (props) => {
  let data1 = [];
  let data2 = [];
  // let totalRevenue = null;
  let totalRevenue1 = null;
  let totalRevenue2 = null;
  // console.log(totalRevenue);
  if (props.brand1) {
    totalRevenue1 = props.brand1[3].total_price;
    for (let i = 1; i < props.brand1.length; i++) {
      let brand1 = props.brand1[i - 1].total_price.split(" ")[1];
      data1 = [...data1, brand1];
    }
  }

  if (props.brand2) {
    totalRevenue2 = props.brand2[3].total_price;
    for (let i = 1; i < props.brand2.length; i++) {
      let brand2 = props.brand2[i - 1].total_price.split(" ")[1];
      data2 = [...data2, brand2];
    }
  }

  // useEffect(() => {
  //   const config = {
  //     headers: {
  //       "Content-Type": "application/json",
  //       Authorization: `Bearer ${localStorage.token}`,
  //     },
  //   };
  //   console.log(config);
  //   const getBrand = async () => {
  //     const dateFilter = `?end_at=${props.first.end}&start_at=${props.first.start}`;
  //     try {
  //       const res = await axios.get(
  //         `https://melandas.ilios.id/api/v1/dashboard/brand-categories${dateFilter}`,
  //         config
  //       );
  //       console.log(res.data);
  //       setBrand(res.data);
  //       console.log("hello");
  //     } catch (error) {
  //       console.error(error);
  //     }
  //   };
  //   getBrand();
  // }, [props.first]);

  const series = [
    {
      name: props.firstDate,
      data: data1,
    },
    {
      name: props.secondDate,
      data: data2,
    },
  ];

  const options = {
    chart: {
      type: "bar",
      height: 350,
    },
    plotOptions: {
      bar: {
        borderRadius: 10,
        horizontal: false,
        columnWidth: "30%",
        endingShape: "rounded",
      },
    },
    colors: ["#17949D", "#FFD13D"],
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: true,
      width: 2,
      colors: ["transparent"],
    },
    xaxis: {
      categories: [["Sparepart"], ["Premium"], ["Reguler"]],
    },
    yaxis: {
      title: {
        text: "Juta",
      },
    },
    fill: {
      opacity: 1,
    },
    tooltip: {
      y: {
        formatter: function (val) {
          return "$ " + val + " thousands";
        },
      },
    },
  };

  return (
    <div
      className="bar-chart"
      style={{
        background: "#FFFFFF",
        boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.05)",
        borderRadius: "8px",
        padding: "26px 24px",
        margin: "0 0 15px",
        // width: "500px",
      }}
    >
      <div className="d-flex justify-content-between">
        <p>Sales By Category</p>
        <div className="d-flex">
          <div style={{ paddingRight: "20px" }}>
            <p style={{ marginBottom: "8px" }}>{props.firstDate}</p>
            <h2
              style={{
                fontFamily: "'Poppins',sans-serif",
                fontStyle: "normal",
                fontWeight: "700",
                fontSize: "20px",
                lineHeight: "30px",
                /* identical to box height */

                color: "#313132",
              }}
            >
              {totalRevenue1}
            </h2>
          </div>
          <div>
            <p style={{ marginBottom: "8px" }}>{props.secondDate}</p>
            <h2
              style={{
                fontFamily: "'Poppins',sans-serif",
                fontStyle: "normal",
                fontWeight: "700",
                fontSize: "20px",
                lineHeight: "30px",
                /* identical to box height */

                color: "#313132",
              }}
            >
              {totalRevenue2}
            </h2>
          </div>
        </div>
      </div>

      <div id="chart">
        <ReactApexChart
          options={options}
          series={series}
          type="bar"
          height={300}
        />
      </div>
    </div>
  );
};

export default ComparationBar;
