import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import Header from "../../../components/Header/Header";
import { loadActivities } from "../../../redux/action/target";

const Notification = (
  { auth: { user, loading }, target: { activities } },
  props
) => {
  const dispatch = useDispatch();

  const [filterDate, setFilterDate] = useState("");
  const [filterFollowUpType, setFilterFollowUpType] = useState("");

  useEffect(() => {
    dispatch(loadActivities(filterDate, filterFollowUpType));
  }, [dispatch, filterDate, filterFollowUpType]);
  return (
    activities && (
      <div className="notification" id="notification">
        <Header title={"Notification"} back={true} notif={false} />
        <div className="notif-filter d-flex">
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            style={{ width: "25%", marginRight: "10px" }}
            onChange={(e) => setFilterDate(e.target.value)}
          />

          <select
            className="form-select"
            name="companys"
            value={filterFollowUpType}
            style={{ width: "25%", marginRight: "10px" }}
            onChange={(e) => setFilterFollowUpType(e.target.value)}
          >
            <option value="">Select Follow-up type</option>
            <option value="PHONE">Phone</option>
            <option value="WHATSAPP">Whatsapp</option>
            <option value="MEETING">Meeting</option>
            <option value="OTHERS">Others</option>
            <option value="WALK_IN_CUSTOMER">Walk in Customer</option>
            <option value="NEW_ORDER">New Order</option>
          </select>
        </div>
        {activities.data.length !== 0 ? (
          <div>
            <ul className="mt-4">
              {activities.data.map((data) => {
                return (
                  <li key={data.id}>
                    {data.follow_up_method === "NEW_ORDER" ? (
                      <>
                        <p>
                          {moment(data.follow_up_datetime).format("DD MMMM")} |{" "}
                          {moment(data.follow_up_datetime).format("LT")}
                        </p>
                        <p>
                          <b>{`${data.user.name} `}</b>
                          made new order for
                          <b>{` ${data.customer.first_name} ${data.customer.last_name}`}</b>
                        </p>
                      </>
                    ) : (
                      <>
                        <p>
                          {moment(data.follow_up_datetime).format("DD MMMM")} |{" "}
                          {moment(data.follow_up_datetime).format("LT")}
                        </p>
                        <p>
                          <b>{`${data.user.name} `}</b>
                          followed up{" "}
                          <b>
                            {data.customer.first_name} {data.customer.last_name}
                          </b>{" "}
                          by <b>{data.follow_up_method}</b>
                        </p>
                      </>
                    )}
                  </li>
                );
              })}
            </ul>
          </div>
        ) : (
          <div
            className="notif-zero d-flex justify-content-center align-items-center text-center"
            style={{ height: "calc(100vh - 170px)" }}
          >
            <div className="">
              <img src="/Asset/Not Notification.png" alt="notif" />
              <h3>Not Notices Right Now</h3>
              <h4>You're up to date! wold work well</h4>
            </div>
          </div>
        )}
      </div>
    )
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
});

export default connect(mapStateToProps)(Notification);
