import React from "react";

const Filters = (props) => {
  const {
    setFilterCompany,
    setFilterChannels,
    setFilterUser,
    setFilterDate,
    filterCompany,
    channels,
    usersList,
  } = props;
  return (
    <div className="filter-form d-flex justify-content-between">
      <div className="header d-flex align-items-center">
        <img
          src="/Asset/Icon/Report.png"
          alt="report"
          style={{ width: "24px", height: "24px" }}
        />
        <h3>Report</h3>
      </div>
      <div className="report-filter-form d-flex">
        <select
          className="form-select"
          name="companys"
          value={filterCompany}
          onChange={(e) => setFilterCompany(e.target.value)}
        >
          <option value="">Select Company</option>
          <option value={1}>Melandas</option>
          <option value={2}>Dio Living</option>
        </select>
        <select
          className="form-select"
          name="channels"
          onChange={(e) => setFilterChannels(e.target.value)}
        >
          <option value="">Select Channel</option>
          {channels.data.map((channel) => {
            return (
              <option value={channel.id} key={channel.id}>
                {channel.name}
              </option>
            );
          })}
        </select>
        <select
          className="form-select"
          name="users"
          onChange={(e) => setFilterUser(e.target.value)}
        >
          <option value="">Select User</option>
          {usersList.data.map((user) => {
            return (
              <option value={user.id} key={user.id}>
                {user.name}
              </option>
            );
          })}
        </select>
        <input
          type="month"
          id="start"
          name="start"
          className="form-control"
          placeholder="Start Month"
          onChange={(e) => setFilterDate(e.target.value)}
        />
      </div>
    </div>
  );
};

export default Filters;
