import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { mainUrl } from "../../api/api";
import { isValidate } from "../../lib/Validate";
import { loadCustomers } from "../../redux/action/customer";
import Button from "../Button/Button";

const AddLeadForm = ({
  customer: { customers, categories },
  newCustomer,
  setPopup,
  setUpdate,
  update,
  setIsNewCustomer,
}) => {
  const dispatch = useDispatch();
  const [customersData, setCustomersData] = useState();
  const [type, setType] = useState("");
  const [label, setLabel] = useState("");
  const [interest, setInterest] = useState("");
  const [leadCategory, setLeadCategory] = useState("");
  const [isUnhandled, setIsUnhandled] = useState(false);
  const [customerId, setCustomerId] = useState();

  const [search, setSearch] = useState("");

  //   validation
  const [customerIdValidation, setCustomerIdValidation] = useState();
  const [typeValidation, setTypeValidation] = useState();
  const [leadCategoryValidation, setLeadCategoryValidation] = useState();

  useEffect(() => {
    dispatch(loadCustomers());
  }, [dispatch]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    console.log(newCustomer);
    newCustomer && setCustomerId(newCustomer.id);

    const getCustomer = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/customers?filter[search]=${
            search !== ""
              ? search
              : newCustomer
              ? newCustomer.phone || newCustomer.email || newCustomer.first_name
              : ""
          }`,
          config
        );
        setCustomersData(res.data.data);
        console.log(res.data.data);
        setCustomerId(res.data.data ? res.data.data[0].id : "");
      } catch (error) {}
    };

    getCustomer();
  }, [search, newCustomer]);

  const addLead = async () => {
    const body = {
      type: type,
      label: label,
      customer_id: parseInt(customerId),
      is_unhandles: isUnhandled,
      lead_category_id: parseInt(leadCategory),
      interest: interest,
    };
    console.log(body);
    setPopup(false);
    setUpdate(!update);
    setIsNewCustomer(false);
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    try {
      const res = await axios.post(`${mainUrl}/leads`, body, config);
      console.log(res.data.data);
    } catch (error) {
      console.error(error);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    isValidate(customerId)
      ? setCustomerIdValidation(true)
      : setCustomerIdValidation(false);
    isValidate(type) ? setTypeValidation(true) : setTypeValidation(false);
    isValidate(leadCategory)
      ? setLeadCategoryValidation(true)
      : setLeadCategoryValidation(false);

    if (
      !isValidate(customerId) &&
      !isValidate(type) &&
      !isValidate(leadCategory)
    ) {
      addLead();
    }
  };
  return (
    customers && (
      <form
        onSubmit={(e) => {
          handleSubmit(e);
        }}
      >
        <h3>Add Lead</h3>
        <hr />
        <div className=" mt-4 mb-4">
          <div className="mt-3">
            <label>
              Customer<span>*</span>
            </label>
            <div className="d-flex">
              <input
                type="text"
                className="form-control"
                name="customer"
                style={{ height: "41px", width: "40%", fontSize: "11px" }}
                placeholder="Search by name,email,phone"
                onChange={(e) => setSearch(e.target.value)}
                autoComplete="off"
              />

              <select
                className={`form-select ${
                  customerIdValidation && isValidate(customerId) && "is-invalid"
                }`}
                name="title"
                id="title"
                style={{ height: "41px" }}
                onChange={(e) => setCustomerId(e.target.value)}
              >
                {customersData &&
                  customersData.map((customer) => {
                    return (
                      <option value={customer.id} key={customer.id}>
                        {`${customer.first_name} ${customer.last_name}  (${customer.email})  (${customer.phone})`}
                      </option>
                    );
                  })}
              </select>
              {isValidate(customerId) && (
                <div className="invalid-feedback">
                  Please select a valid customer.
                </div>
              )}
            </div>
          </div>
          <div className="mt-3">
            <label>
              Type<span>*</span>
            </label>
            <select
              className={`form-select ${
                typeValidation && isValidate(type) && "is-invalid"
              }`}
              name="type"
              id="type"
              style={{ height: "41px" }}
              onChange={(e) => setType(e.target.value)}
            >
              <option value="">Please Select Type</option>
              <option value="PROSPECT">Prospect</option>
              <option value="DEAL">DEAL</option>
              <option value="LEADS">Leads</option>
            </select>
            {isValidate(type) && (
              <div className="invalid-feedback">
                Please select a valid type.
              </div>
            )}
          </div>
          <div className="mt-3">
            <label>
              Label<span>{"(optional)"}</span>
            </label>
            <input
              type="text"
              className="form-control"
              name="label"
              style={{ height: "41px" }}
              placeholder="Input your label here"
              onChange={(e) => setLabel(e.target.value)}
            />
          </div>
          <div className="mt-3">
            <label>
              Interest<span>{"(optional)"}</span>
            </label>
            <input
              type="text"
              className="form-control"
              name="interest"
              style={{ height: "41px" }}
              placeholder="Input your lead interest"
              onChange={(e) => setInterest(e.target.value)}
            />
          </div>
          <div className="mt-3">
            <label>
              Lead Category<span>*</span>
            </label>
            <select
              className={`form-select ${
                leadCategoryValidation &&
                isValidate(leadCategory) &&
                "is-invalid"
              }`}
              name="type"
              id="type"
              style={{ height: "41px" }}
              onChange={(e) => setLeadCategory(e.target.value)}
            >
              <option value="">Please Select Type</option>
              {categories.map((category) => {
                return (
                  <option value={category.id} key={category.id}>
                    {category.name}
                  </option>
                );
              })}
            </select>
            {isValidate(leadCategory) && (
              <div className="invalid-feedback">
                Please select a valid Lead Category.
              </div>
            )}
          </div>
          <div className="mt-3">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value="true"
                id="flexCheckDefault"
                onChange={(e) => setIsUnhandled(!isUnhandled)}
              />
              <label className="form-check-label" htmlFor="flexCheckDefault">
                Unhandled
              </label>
            </div>
          </div>
        </div>

        <div>
          <Button
            title={"Submit"}
            icon={""}
            btnWidth={"100%"}
            btnStyle={"primary-btn"}
          />
        </div>
      </form>
    )
  );
};

const mapStateToProps = (state) => ({
  customer: state.customer,
});

export default connect(mapStateToProps)(AddLeadForm);
