import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";
import axios from "axios";
import moment from "moment";
import Loaded from "../../../components/Loaded/Loaded";

const TopSales = () => {
  const { company_id, month, id, type } = useParams();

  const [topSales, setTopSales] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    console.log(month);

    const getTopSales = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/index-top-sales/target?company_id=${company_id}&${
            type !== "sales" ? type + "=" : ""
          }${id !== 0 ? id : ""}&start_at=${moment(month)
            .startOf("month")
            .format("YYYY-MM-DD")}&end_at=${moment(month)
            .endOf("month")
            .format("YYYY-MM-DD")}&limit=20`,
          config
        );
        console.log(res.data.sales);
        setTopSales(res.data.sales);
      } catch (error) {
        console.error(error);
      }
    };
    getTopSales();
  }, [company_id, month, type, id]);

  return topSales ? (
    <div>
      <Header title={"Top Sales"} back={true} notif={true} />
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>Supervisor Name</th>
              <th>Revenue</th>
              <th>Target</th>
              <th>Meet Goal</th>
            </tr>
          </thead>
          <tbody>
            {topSales.map((data, i) => {
              return (
                <tr key={i}>
                  <td>{i + 1}.</td>
                  <td>{data.model.name}</td>
                  <td>{data.value}</td>
                  <td>{data.target}</td>
                  <td style={{ color: "#2DCC70" }}>{data.percentage}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default TopSales;
