import axios from "axios";
import moment from "moment";
import React, { Fragment, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { isValidate } from "../../lib/Validate";
import { loadChannels } from "../../redux/action/target";
import Activity from "../ActivityDetail/Activity";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import LoadedComponent from "../Loaded/LoadedComponent";

const ReportLeadStatus = () => {
  const params = useParams();
  const { user_type, user_id, detail_type, start, end } = params;

  const [activityDetail, setActivityDetail] = useState(false);
  const [customerActivities, setCustomerActivities] = useState(null);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [orderId, setOrderId] = useState(null);

  const [reportDataStatus, setReportDataStatus] = useState(null);
  const [reportDataClosed, setReportDataClosed] = useState(null);
  const [reportDataHot, setReportDataHot] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const dispatch = useDispatch();
  // filter
  const [filterCompany, setFilterCompany] = useState();
  // const [filterChannels, setFilterChannels] = useState();
  const [filterDate, setFilterDate] = useState();
  // const [filterSupervisior, setFilterSupervisor] = useState();
  const [filterStartDate, setFilterStartDate] = useState(
    start !== "now" ? start : ""
  );
  const [filterEndDate, setFilterEndDate] = useState(end !== "now" ? end : "");
  useEffect(() => {
    dispatch(loadChannels(filterCompany));
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getReportDeal = async () => {
      setIsLoading(true);
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-leads/status?start_date=${
            filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
          }&end_date=${
            filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
          }&company_id=${filterCompany ? filterCompany : ""}&${
            user_type === "bum"
              ? "supervisor_id=" + parseInt(user_id)
              : user_type === "channel"
              ? "channel_id=" + parseInt(user_id)
              : user_type === "sales"
              ? "sales_id=" + parseInt(user_id)
              : ""
          }&status=${detail_type.toUpperCase()}`,
          config
        );
        console.log(res.data);
        setReportDataStatus(res.data);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
      }
    };

    getReportDeal();
  }, [
    filterDate,
    dispatch,
    user_type,
    user_id,
    detail_type,
    filterCompany,
    filterStartDate,
    filterEndDate,
  ]);

  const handleClickDetail = async (activity) => {
    try {
      const res = await axios.get(
        `${mainUrl}/activities?filter[id]=${activity.id}`,
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      );
      setCustomerActivity(res.data.data[0]);
      console.log(res.data);
    } catch (error) {
      console.error(error);
    }
    setActivityDetail(!activityDetail);
    // setCustomerActivity(activity);
    setOrderId(activity.order ? activity.order.id : null);
    console.log(activity.activity);
  };

  return (
    <div>
      <Header
        icon=""
        title={`Report Leads - ${detail_type.toUpperCase()}`}
        back={false}
        user={false}
      />

      <FilterLeadsReport
        company={false}
        channel={false}
        supervisor={false}
        download={null}
        setFilterStartDate={setFilterStartDate}
        setFilterEndDate={setFilterEndDate}
      />
      {isLoading ? (
        <LoadedComponent />
      ) : (
        <div style={{ overflow: "scroll" }}>
          <table className="" style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>Date</th>
                <th>Lead</th>
                <th>Invoice Number</th>
                <th>Follow Up</th>
                <th>Store Location</th>
                <th>Payment</th>
                <th>Quotation</th>
                <th>Sales</th>
                <th>Status</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {reportDataStatus &&
                reportDataStatus?.data.map((activity, idx) => {
                  return (
                    <tr key={activity.id}>
                      <td>
                        {!isValidate(activity.follow_up_datetime)
                          ? moment(activity.follow_up_datetime).format(
                              "MM/DD/YYYY"
                            )
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.customer)
                          ? `${activity.customer.first_name} ${
                              activity.customer.last_name || ""
                            }`
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.order)
                          ? activity.order.invoice_number
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.follow_up_method)
                          ? activity.follow_up_method
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.channel.name)
                          ? activity.channel.name
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.order)
                          ? activity.order.payment_status
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.order)
                          ? activity.order.status
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.user.name)
                          ? activity.user.name
                          : ""}
                      </td>
                      <td>
                        {!isValidate(activity.status) ? activity.status : ""}
                      </td>
                      <td>
                        <div
                          onClick={() => handleClickDetail(activity)}
                          className="activity-detail-btn"
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </div>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      )}
      {/* {reportByLeads.last_page && (
        <div className="d-flex justify-content-between">
          <ReactPaginate
            previousLabel={"Prev"}
            nextLabel={"Next"}
            breakLabel={"..."}
            pageCount={reportByLeads.last_page}
            marginPagesDisplayed={3}
            // pageRangeDisplayed={3}
            onPageChange={handlePageClick}
            containerClassName={"pagination"}
            pageClassName={"page-item"}
            pageLinkClassName={"page-link"}
            previousClassName={"page-item"}
            previousLinkClassName={"page-link"}
            nextClassName={"page-item"}
            nextLinkClassName={"page-link"}
            breakClassName={"page-item"}
            breakLinkClassName={"page-link"}
            activeClassName={"active"}
          />
          <div
            onClick={() => exportReportsByLeads()}
            style={{ width: "fit-content" }}
          >
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div>
        </div>
      )} */}
      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  );
};

export default ReportLeadStatus;
