import React from "react";
import { connect } from "react-redux";
import "./ActivityDetail.scss";

import OrderDetail from "./OrderDetail";
import ActivityDetails from "./ActivityDetails";
import Comments from "../Comments/Comments";
import moment from "moment";

const Activity = ({
  auth: { user, loading },
  activityDetail,
  setActivityDetail,
  customerActivity,
  orderId,
}) => {
  return (
    <div
      className={`detail-activity ${activityDetail ? "activity-active" : ""}`}
    >
      <div className="detail-activity-header d-flex justify-content-between align-items-center">
        <h1>Detail Activity</h1>
        <img
          src="/Asset/Times.png"
          alt="close"
          onClick={() => setActivityDetail(!activityDetail)}
        />
      </div>
      <ActivityDetails customerActivity={customerActivity} user={user} />
      <hr />
      <OrderDetail
        orderId={orderId}
        user={user}
        activity_id={customerActivity.id}
      />
      <div>
        <div>
          <p>Feedback</p>
        </div>
        <div style={{ border: "1px solid #000", padding: "5px 10px" }}>
          <p>{customerActivity.feedback ? customerActivity.feedback : "-"}</p>
        </div>
      </div>
      <hr />
      {customerActivity.reminder_note && (
        <div>
          <div className="d-flex justify-content-between align-items-center">
            <div>
              <p>Reminder</p>
            </div>
            <div>
              <p>
                {customerActivity.reminder_datetime &&
                  moment(customerActivity.reminder_datetime).format(
                    "DD MMMM YYYY, h:mm"
                  )}
              </p>
            </div>
          </div>

          <div style={{ border: "1px solid #000", padding: "5px 10px" }}>
            <p>
              {customerActivity.reminder_note && customerActivity.reminder_note}
            </p>
          </div>
        </div>
      )}
      <hr />
      <Comments activity_id={customerActivity.id ? customerActivity.id : ""} />
    </div>
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(Activity);
