import { temporaryCart } from "../action/product";
import {
  ACTIVE_CARTS_ERROR,
  ACTIVE_CARTS_LOADED,
  ADD_TEMPORARY_CART,
  BRAND_ERROR,
  BRAND_LOADED,
  CARTS_ERROR,
  CARTS_LOADED,
  ERROR_TEMPORARY_CART,
} from "../action/types";

const initialState = {
  loading: true,
  brands: null,
  temporaryCart: [],
  carts: null,
  activeCarts: null,
};

export default function foo(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case BRAND_LOADED:
      return {
        ...state,
        loading: false,
        brands: payload,
      };
    case CARTS_LOADED:
      return {
        ...state,
        loading: false,
        carts: payload,
      };
    case ADD_TEMPORARY_CART:
      return {
        ...state,
        loading: false,
        temporaryCart: [payload],
      };
    case ERROR_TEMPORARY_CART:
      return {
        ...state,
        loading: false,
        temporaryCart: [],
      };
    case ACTIVE_CARTS_LOADED:
      return {
        ...state,
        loading: false,
        activeCarts: payload,
      };
    case BRAND_ERROR:
    case CARTS_ERROR:
    case ACTIVE_CARTS_ERROR:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
}
