import React, { useState } from "react";
import { formatCurrency } from "../../api/format";
import Popup from "../Popup/Popup";
import ProductOrder from "../Popup/Popup Components/UploadImg/ProductOrder";

const ProductListsOrder = (props) => {
  const { order_details, user } = props;
  const [uploadImg, setUploadImg] = useState(false);
  const [unit_id, setUnit_id] = useState(null);

  const handleUploadImg = (unit_id) => {
    setUploadImg(!uploadImg);
    setUnit_id(unit_id);
  };
  return (
    order_details && (
      <div>
        <Popup popup={uploadImg} setPopup={setUploadImg}>
          <ProductOrder item_id={unit_id} />
        </Popup>
        <h3>Detail SKU</h3>
        {order_details.map((unit) => {
          return (
            <div className="d-flex" key={unit.id}>
              <img
                src={
                  unit.photo[unit.photo.length - 1]
                    ? unit.photo[unit.photo.length - 1].url
                    : unit.images[0]
                    ? unit.images[0].url
                    : "/Asset/no image.jpg"
                }
                alt="detailsku"
                style={{
                  width: "122px",
                  height: "122px",
                  marginRight: "16px",
                }}
                onClick={() => handleUploadImg(unit.id)}
              />
              {/* {console.log(unit.images[0].url)} */}
              <div>
                <h3>{unit.product_unit ? unit.product_unit.name : ""}</h3>
                <p style={{ margin: "0" }}>
                  {`${formatCurrency(unit.product_unit.price)} x ${
                    unit.quantity
                  }`}
                </p>
                {user.data.as === "Director" && (
                  <p style={{ color: "#17949D" }}>
                    <b>
                      Hpp: {formatCurrency(unit.product_unit.production_cost)}
                    </b>
                  </p>
                )}

                <ul>
                  <li>
                    <b>{unit.model.name}</b>
                  </li>
                  <li>{unit.category_code.name}</li>
                  <li>
                    {unit.colour.name} - {unit.covering.name}
                  </li>
                </ul>
              </div>
            </div>
          );
        })}
      </div>
    )
  );
};

export default ProductListsOrder;
