import axios from "axios";
import { mainUrl } from "../../api/api";
import { PROMO_CATEGORIES_ERROR, PROMO_CATEGORIES_LOADED } from "./types";

export const loadPromoCategories = () => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(`${mainUrl}/promo-categories`, config);

    dispatch({
      type: PROMO_CATEGORIES_LOADED,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: PROMO_CATEGORIES_ERROR,
    });
    console.error(error);
  }
};
