import axios from "axios";
import React, { useEffect, useState } from "react";
import { mainUrl } from "../../api/api";
import { isValidate } from "../../lib/Validate";
import Button from "../Button/Button";
import { UploadProofPayment } from "../UploadProofPayment/UploadProofPayment";

const PaymentDetail = (props) => {
  const { orderId } = props;
  const [paymentData, setPaymentData] = useState(null);
  const [paymentDetailActive, setPaymentDetailActive] = useState(false);
  const [proof, setProof] = useState(false);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getPayment = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/payments?filter[order_id]=${orderId}`,
          config
        );
        console.log(res.data.data[0]);
        setPaymentData(res.data.data[0]);
      } catch (error) {
        console.error(error);
      }
    };
    getPayment();
  }, [orderId]);
  return (
    !isValidate(paymentData) && (
      <div className="mb-2">
        <p
          className=""
          style={{ color: "blue", cursor: "pointer" }}
          onClick={() => setPaymentDetailActive(!paymentDetailActive)}
        >
          See Payment Details
        </p>
        <div
          className="justify-content-between"
          style={{ display: paymentDetailActive ? "" : "none" }}
        >
          <div className="d-flex justify-content-between">
            <div>
              <p>ID:</p>
              <p>Amount:</p>
              <p>Payment Type:</p>
              <p>Status:</p>
              <p>Reference:</p>
            </div>

            <div className="text-end">
              <p>{!isValidate(paymentData.id) ? paymentData.id : "-"}</p>
              <p>
                {!isValidate(paymentData.amount) ? paymentData.amount : "-"}
              </p>
              <p>
                {!isValidate(paymentData.payment_type.name)
                  ? paymentData.payment_type.name
                  : "-"}
              </p>
              <p>
                {!isValidate(paymentData.status) ? paymentData.status : "-"}
              </p>
              {/* <p>{paymentData ? paymentData.id : "-"}</p> */}
            </div>
          </div>
          <div className="row">
            <div className="col-6" onClick={() => setProof(!proof)}>
              <Button
                title={"Proof of Payment"}
                btnStyle={"secondary-btn"}
                btnWidth={"100%"}
              />
            </div>
            <div className="col-6">
              <Button
                title={"Receipt"}
                btnStyle={"secondary-btn"}
                btnWidth={"100%"}
              />
            </div>
          </div>
          {proof ? (
            <div className="mt-4 mb-5">
              <h5>Proof of Payment</h5>
              <div className="text-center">
                <p>- Histori -</p>
                {paymentData.proof.length > 0 ? (
                  paymentData.proof.map((data) => {
                    return (
                      <img
                        src={data.url}
                        alt="proof_of_payment"
                        style={{ width: "100%" }}
                      />
                    );
                  })
                ) : (
                  <p> - </p>
                )}
              </div>

              <UploadProofPayment paymentId={paymentData.id} />
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    )
  );
};

export default PaymentDetail;
