import axios from "axios";
import React, { useEffect, useState } from "react";
import { mainUrl } from "../../api/api";
import LoadedComponent from "../Loaded/LoadedComponent";
import UpdateCustomerPopup from "../Popup/Popup Component/UpdateCustomerPopup";
import PopupLayout from "../Popup/PopupLayout";
import "./CustomerCard.css";

const CustomerCard = (props) => {
  const { leads_id } = props;

  const [refreshCustomer, setRefreshCustomer] = useState(false);
  const [customer, setCustomer] = useState(null);
  const [customerAddress, setCustomerAddress] = useState(null);
  const [updateCustomer, setUpdateCustomer] = useState(false);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getCustomer = async () => {
      try {
        const res = await axios.get(`${mainUrl}/leads/${leads_id}`, config);
        console.log(res.data);
        setCustomer(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getCustomer();
  }, [leads_id, refreshCustomer]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getCustomerAddress = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/addresses/${
            customer.customer.default_address_id
              ? customer.customer.default_address_id
              : ""
          }`,
          config
        );
        setCustomerAddress(res.data.data);
        console.log(res.data);
      } catch (error) {
        console.error(error);
      }
    };
    if (customer) {
      getCustomerAddress();
    }
  }, [customer]);

  return customer && customerAddress ? (
    <div id="customer-card" className="customer-card">
      <PopupLayout
        Popup={updateCustomer}
        setPopup={() => setUpdateCustomer()}
        component={
          <UpdateCustomerPopup
            customer={customer}
            address={customerAddress}
            refreshCustomer={refreshCustomer}
            setRefreshCustomer={setRefreshCustomer}
          />
        }
      />
      <div className="d-flex justify-content-between align-items-center">
        <div className="d-flex align-items-center">
          <div className="customer-img d-flex justify-content-center align-items-center">
            <div>
              <h2>TS</h2>
            </div>
          </div>
          <div className="">
            <h2>{`${customer.customer.first_name} ${
              customer.customer.last_name ? customer.customer.last_name : ""
            }`}</h2>
          </div>
        </div>
        <img
          src="/Asset/Edit.png"
          alt="edit"
          style={{ width: "24px", height: "24px" }}
          onClick={() => setUpdateCustomer(!updateCustomer)}
        />
      </div>
      <div className="customer-desc row mt-4">
        <div className="col-5">
          <h4>Basic Information</h4>
          <div className="row">
            <div className="col-5">
              <p>Name</p>
              <p>Phone Number</p>
              <p>Email</p>
              <p>Priority</p>
            </div>
            <div className="col-7">
              <p>{`${customer.customer.first_name} ${
                customer.customer.last_name ? customer.customer.last_name : ""
              }`}</p>
              <p>{`${customer.customer.phone}`}</p>
              <p>{`${customer.customer.email}`}</p>
              <p>{`${customer.status}`}</p>
            </div>
          </div>
        </div>
        <div className="col-5">
          <h4>Address Information</h4>
          <div className="row">
            <div className="col-5">
              <p>Address Line 1</p>
              <p>Sub Distric</p>
              <p>City/Distric</p>
              <p>Province</p>
              <p>ZIP Code</p>
            </div>
            <div className="col-7">
              <p>{`${
                customerAddress.address_line_1
                  ? customerAddress.address_line_1
                  : "-"
              }`}</p>
              <p>-</p>
              <p>{`${customerAddress.city ? customerAddress.city : "-"}`}</p>
              <p>{`${
                customerAddress.province ? customerAddress.province : "-"
              }`}</p>
              <p>{`${
                customerAddress.postcode ? customerAddress.postcode : "-"
              }`}</p>
            </div>
          </div>
        </div>
        <div className="col-2"></div>
      </div>
    </div>
  ) : (
    <LoadedComponent />
  );
};

export default CustomerCard;
