import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import LoadedComponent from "../Loaded/LoadedComponent";

const NewArrivals = () => {
  const [newProducts, setNewProducts] = useState(null);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getNewProducts = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/models?page=1&perPage=10`,
          config
        );
        // console.log(res.data.data);
        setNewProducts(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getNewProducts();
  }, []);

  const SampleNextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <div
        onClick={onClick}
        className={className}
        style={{
          ...style,
          top: "90px",
        }}
      >
        <div
          className="d-flex justify-content-center align-items-center"
          style={{
            width: "36px",
            height: "36px",
            background: "#fff",
            borderRadius: "50%",
            boxShadow: "2px 3px 8px rgba(0, 0, 0, 0.1)",
          }}
        >
          <img
            src="/Asset/Arrow Icon/RightArrow.png"
            alt="prev"
            style={{ width: "24px", height: "24px" }}
          />
        </div>
      </div>
    );
  };

  const SamplePrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <div
        onClick={onClick}
        className={className}
        style={{
          ...style,
          left: "-45px",
          top: "90px",
        }}
      >
        <div
          className="d-flex justify-content-center align-items-center"
          style={{
            width: "36px",
            height: "36px",
            background: "#fff",
            borderRadius: "50%",
            boxShadow: "2px 3px 8px rgba(0, 0, 0, 0.1)",
          }}
        >
          <img
            src="/Asset/Arrow Icon/LeftArrow.png"
            alt="prev"
            style={{ width: "24px", height: "24px" }}
          />
        </div>
      </div>
    );
  };

  var settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 2,
    initialSlide: 0,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    // variableWidth: true,
    responsive: [
      {
        breakpoint: 1224,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          dots: true,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="new-arrival">
      <h3>New Arrival</h3>
      <div className="new-arrival-carousel">
        <Slider {...settings}>
          {newProducts ? (
            newProducts.map((product) => {
              return (
                <Link
                  to={`/dashboard/product/product_detail/${product.id}`}
                  key={product.id}
                >
                  <div className="new-arrival-carousel-card">
                    <img src={product.images[0].url} alt="product1" />
                    <h4>{product.name}</h4>
                    <p>
                      {formatCurrency(product.price_min)} s/d{" "}
                      {formatCurrency(product.price_max)}
                    </p>
                  </div>
                </Link>
              );
            })
          ) : (
            <LoadedComponent />
          )}
        </Slider>
      </div>
    </div>
  );
};

export default NewArrivals;
