import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Button from "../../../components/Button/Button";
import parse from "html-react-parser";

const PromoCategory = () => {
  const [categoryPromo, setCategoryPromo] = useState(null);
  const { category_id } = useParams();

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getCategoryPromo = async () => {
      try {
        const res = await axios.get(
          `https://melandas.ilios.id/api/v1/promo-categories/${category_id}`,
          config
        );
        console.log(res.data.data);
        setCategoryPromo(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getCategoryPromo();
  }, [category_id]);

  return (
    categoryPromo && (
      <div className="PromoDetail" id="PromoDetail">
        <div
          style={{
            backgroundImage: `url(${categoryPromo.images[0].url})`,
            height: "443px",
            width: "100%",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            borderRadius: "8px",
            backgroundPosition: "center",
          }}
        ></div>
        <div className="mt-3">
          <h4>{categoryPromo.name}</h4>
          <p>{parse(categoryPromo.description)}</p>
        </div>
        <Button
          title="Download Catalogue"
          btnStyle="primary-btn"
          btnWidth="100%"
          icon=""
        />
      </div>
    )
  );
};

export default PromoCategory;
