import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { loadChannels } from "../../redux/action/target";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import * as XLSX from "xlsx/xlsx.mjs";
import "./ReportBrands.scss";
import Button from "../Button/Button";
import { Link } from "react-router-dom";
import LoadedComponent from "../Loaded/LoadedComponent";
import NoData from "../Loaded/NoData";

const ReportBrands = () => {
  const [reportData, setReportData] = useState(null);
  const dispatch = useDispatch();
  // filter
  const [filterCompany, setFilterCompany] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterDate, setFilterDate] = useState();
  const [filterStartDate, setFilterStartDate] = useState();
  const [filterEndDate, setFilterEndDate] = useState();
  const [filterSupervisior, setFilterSupervisor] = useState();

  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    setIsLoading(true);
    dispatch(loadChannels(filterCompany));
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getReportBrands = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-brands?start_date=${
            filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
          }&end_date=${
            filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
          }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
            filterChannels ? filterChannels : ""
          }&supervisor_id=${filterSupervisior ? filterSupervisior : ""}`,
          config
        );
        console.log(res.data);
        setReportData(res.data);
        console.log(res.data.length);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
      }
    };

    getReportBrands();
  }, [
    filterDate,
    filterCompany,
    filterChannels,
    filterSupervisior,
    dispatch,
    filterStartDate,
    filterEndDate,
  ]);

  const exportReportBrands = async () => {
    axios
      .get(
        `${mainUrl}/dashboard/report-brands?start_date=${
          filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
        }&end_date=${
          filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
        }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
          filterChannels ? filterChannels : ""
        }&supervisor_id=${
          filterSupervisior ? filterSupervisior : ""
        }&is_export=1`,
        {
          method: "GET",
          responseType: "blob", // important
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      )
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `Report Brands${filterDate}.xlsx`);
        document.body.appendChild(link);
        link.click();
      })
      .catch((err) => {
        console.log();
      });
  };
  return (
    <section>
      <Header title={"Report Brands"} back={true} icon="" />

      <FilterLeadsReport
        filterCompany={filterCompany}
        setFilterCompany={setFilterCompany}
        setFilterSupervisor={setFilterSupervisor}
        setFilterChannels={setFilterChannels}
        setFilterDate={setFilterDate}
        download={exportReportBrands}
        setFilterStartDate={setFilterStartDate}
        setFilterEndDate={setFilterEndDate}
      />
      {isLoading ? (
        <LoadedComponent />
      ) : !reportData ? (
        <NoData />
      ) : reportData.length !== 0 ? (
        <div style={{ overflow: "scroll" }}>
          <table className="table-report-brands" style={{ margin: "10px 0" }}>
            <thead className="text-center">
              <tr>
                <th>Sales</th>
                <th>Store</th>
                <th>BUM</th>
                <th>Total Leads</th>
                <th
                  colSpan={reportData[0].product_brands.length + 1}
                  className="parent-head"
                >
                  Estimated
                </th>
                <th
                  colSpan={reportData[0].product_brands.length + 1}
                  className="parent-head"
                >
                  Quotation
                </th>
              </tr>
              <tr>
                <th></th>

                <th></th>
                <th></th>
                <th></th>
                {reportData[0].product_brands.map((brand, idx) => {
                  return (
                    <th className="parent-head" key={idx}>
                      {brand.product_brand}
                    </th>
                  );
                })}
                <th className="parent-head">Total Estimated</th>
                {reportData[0].product_brands.map((brand, idx) => {
                  return (
                    <th className="parent-head" key={idx}>
                      {brand.product_brand}
                    </th>
                  );
                })}
                <th className="parent-head">Total Quotation</th>
              </tr>
            </thead>
            <tbody>
              {reportData.map((data, idx) => {
                return (
                  <tr key={idx}>
                    <td>
                      <Link
                        to={`/dashboard/report_brands/detail/${data.user_id}/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}`}
                      >
                        {data?.sales}
                      </Link>
                    </td>
                    <td>{data?.channel}</td>
                    <td>{data?.bum}</td>
                    <td>{data?.total_leads}</td>
                    {data?.product_brands.map((brand, idx) => {
                      return (
                        <td
                          key={idx}
                          style={{
                            backgroundColor:
                              brand.estimated_value > 1000
                                ? "#2DCC70"
                                : brand.estimated_value > 0
                                ? "#FF0000"
                                : "",
                          }}
                        >
                          {formatCurrency(brand.estimated_value)}
                        </td>
                      );
                    })}
                    <td>
                      {data?.total_estimated &&
                        formatCurrency(data?.total_estimated)}
                    </td>
                    {data?.product_brands.map((brand, idx) => {
                      return (
                        <td
                          key={idx}
                          style={{
                            backgroundColor:
                              brand.order_value > 1000
                                ? "#2DCC70"
                                : brand.order_value > 0
                                ? "#FF0000"
                                : "",
                          }}
                        >
                          {formatCurrency(brand.order_value)}
                        </td>
                      );
                    })}
                    <td>
                      {data?.total_quotation &&
                        formatCurrency(data?.total_quotation)}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          {/* <div onClick={() => exportData()} style={{ width: "fit-content" }}>
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div> */}
        </div>
      ) : (
        <NoData />
      )}
    </section>
  );
};

export default ReportBrands;
