import React, { useState } from "react";
import Button from "../../../components/Button/Button";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import AllBrands from "../../../components/Product-Components/AllBrands";
import NewArrivals from "../../../components/Product-Components/NewArrivals";
import SearchModel from "../../../components/Product-Components/SearchModel";
import SearchProductUnit from "../../../components/Product-Components/SearchProductUnit";
import "./Product.scss";

const Product = ({ product: { carts } }) => {
  const [search, setSearch] = useState("");

  return (
    <section className="product" id="product">
      <img
        src="/Asset/Products/benner product.jpg"
        alt="Product Benner"
        className="benner"
      />

      <div className="d-flex justify-content-end" style={{ marginTop: "20px" }}>
        <input
          className="form-control me-3"
          type="text"
          placeholder="Search Product"
          aria-label="default input example"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        ></input>
        <div>
          <Link to="/dashboard/product/stocks">
            <Button
              title="Product Stock"
              icon="/Asset/Button Icon/Stock.png"
              btnStyle="third-btn"
            />
          </Link>
        </div>
        <div style={{ marginLeft: "10px" }}>
          <Link to="/dashboard/product/cart">
            <Button
              title={`Cart (${carts ? carts.data.items.length : 0})`}
              icon="/Asset/Button Icon/Cart.png"
              btnStyle="third-btn"
            />
          </Link>
        </div>
      </div>
      {search ? (
        <div>
          <SearchModel search={search} />
          <SearchProductUnit search={search} />
        </div>
      ) : (
        <div>
          <NewArrivals />
          <AllBrands />
        </div>
      )}
    </section>
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(Product);
