import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { loadChannels } from "../../redux/action/target";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import * as XLSX from "xlsx/xlsx.mjs";
import "./ReportBrands.scss";
import Button from "../Button/Button";
import { useParams } from "react-router-dom";

const ReportBrandsDetail = () => {
  const { user_id, date } = useParams();
  const [reportData, setReportData] = useState(null);
  const dispatch = useDispatch();
  // filter
  const [filterCompany, setFilterCompany] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterDate, setFilterDate] = useState(date === "now" ? "" : date);
  const [filterSupervisior, setFilterSupervisor] = useState();

  useEffect(() => {
    dispatch(loadChannels(filterCompany));
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getReportBrands = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-brands/details?start_date=${
            filterDate
              ? moment(filterDate).startOf("month").format("YYYY-MM-DD")
              : ""
          }&end_date=${
            filterDate
              ? moment(filterDate).endOf("month").format("YYYY-MM-DD")
              : ""
          }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
            filterChannels ? filterChannels : ""
          }&supervisor_id=${
            filterSupervisior ? filterSupervisior : ""
          }&user_id=${user_id}`,
          config
        );
        console.log(res.data);
        setReportData(res.data);
        console.log(res.data.length);
      } catch (error) {
        console.error(error);
      }
    };

    getReportBrands();
  }, [
    filterDate,
    filterCompany,
    filterChannels,
    filterSupervisior,
    dispatch,
    user_id,
  ]);

  const exportData = async () => {
    axios
      .get(
        `${mainUrl}/dashboard/report-brands/details?start_date=${
          filterDate
            ? moment(filterDate).startOf("month").format("YYYY-MM-DD")
            : ""
        }&end_date=${
          filterDate
            ? moment(filterDate).endOf("month").format("YYYY-MM-DD")
            : ""
        }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
          filterChannels ? filterChannels : ""
        }&supervisor_id=${
          filterSupervisior ? filterSupervisior : ""
        }&user_id=${user_id}&is_export=1`,
        {
          method: "GET",
          responseType: "blob", // important
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      )
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `ReportLeads Detail ${filterDate}.xlsx`);
        document.body.appendChild(link);
        link.click();
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <section>
      <Header
        title={`${
          reportData && reportData.length !== 0 ? reportData[0].sales : ""
        } - ${
          reportData && reportData.length !== 0 ? reportData[0].channel : ""
        }`}
        back={true}
        icon=""
      />

      <FilterLeadsReport
        filterCompany={filterCompany}
        setFilterCompany={setFilterCompany}
        setFilterSupervisor={setFilterSupervisor}
        setFilterChannels={setFilterChannels}
        setFilterDate={setFilterDate}
        download={exportData}
      />
      {!reportData ? (
        ""
      ) : reportData.length !== 0 ? (
        <div style={{ overflow: "scroll" }}>
          <table className="table-report-brands" style={{ margin: "10px 0" }}>
            <thead className="text-center">
              <tr>
                <th>Leads</th>
                <th>Phone</th>
                <th>Source</th>
                <th
                  colSpan={reportData[0].product_brands.length + 1}
                  className="parent-head"
                >
                  Estimated
                </th>
                <th
                  colSpan={reportData[0].product_brands.length + 1}
                  className="parent-head"
                >
                  Quotation
                </th>
              </tr>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                {reportData[0].product_brands.map((brand, idx) => {
                  return (
                    <th className="parent-head" key={idx}>
                      {brand.product_brand}
                    </th>
                  );
                })}
                <th className="parent-head">Total Estimated</th>
                {reportData[0].product_brands.map((brand, idx) => {
                  return (
                    <th className="parent-head" key={idx}>
                      {brand.product_brand}
                    </th>
                  );
                })}
                <th className="parent-head">Total Quotation</th>
              </tr>
            </thead>
            <tbody>
              {reportData.map((data, idx) => {
                return (
                  <tr key={idx}>
                    <td>{data?.customer}</td>
                    <td>{data?.phone}</td>
                    <td>{data?.source}</td>
                    {data?.product_brands.map((brand) => {
                      return (
                        <td
                          style={{
                            backgroundColor:
                              brand.estimated_value > 1000
                                ? "#2DCC70"
                                : brand.estimated_value > 0
                                ? "#FF0000"
                                : "",
                          }}
                        >
                          {formatCurrency(brand.estimated_value)}
                        </td>
                      );
                    })}
                    <td>
                      {data?.total_estimated &&
                        formatCurrency(data?.total_estimated)}
                    </td>
                    {data?.product_brands.map((brand) => {
                      return (
                        <td
                          style={{
                            backgroundColor:
                              brand.order_value > 1000
                                ? "#2DCC70"
                                : brand.order_value > 0
                                ? "#FF0000"
                                : "",
                          }}
                        >
                          {formatCurrency(brand.order_value)}
                        </td>
                      );
                    })}
                    <td>
                      {data?.total_quotation &&
                        formatCurrency(data?.total_quotation)}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          {/* <div onClick={() => exportData()} style={{ width: "fit-content" }}>
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div> */}
        </div>
      ) : (
        ""
      )}
    </section>
  );
};

export default ReportBrandsDetail;
