import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { mainUrl } from "../../../api/api";
import ComparationBar from "../../../components/Chart/ComparationBar";
// import { Link } from "react-router-dom";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import ReportCard from "../../../components/Report Card/ReportCard";
import { loadFirstMonth, loadSecondMonth } from "../../../redux/action/target";
// import Skill from "./Skill";
import "./Comparationv2.scss";
import Skill from "./Skill";

const Comparationv2 = (
  { auth: { user, loading }, target: { followUp, firstMonth, secondMonth } },
  props
) => {
  const dispatch = useDispatch();

  const [firstTarget, setFirstTarget] = useState(null);
  const [firstSettlement, setFirstSettlement] = useState(null);
  const [firstInteriorDesign, setFirstInteriorDesign] = useState(null);
  const [firstFollowUp, setFirstFollowUp] = useState(null);
  const [secondTarget, setSecondTarget] = useState(null);
  const [secondSettlement, setSecondSettlement] = useState(null);
  const [secondInteriorDesign, setSecondInteriorDesign] = useState(null);
  const [secondFollowUp, setSecondFollowUp] = useState(null);
  const [brand1, setBrand1] = useState(null);
  const [brand2, setBrand2] = useState(null);

  // let today = new Date();
  let today = new Date();
  let currentMonth = `${today.getFullYear()}-${(
    "0" +
    (today.getMonth() + 1)
  ).slice(-2)}`;
  console.log(currentMonth);
  // let firstMonthVal = moment(firstMonth.start).format("YYYY-MM");
  // let secondMonth.month = moment(secondMonth.start).format("YYYY-MM");

  const [strFirstMonth, setStrFirstMonth] = useState(
    moment(currentMonth).format("MMMM YY")
  );
  const [strSecondMonth, setStrSecondMonth] = useState(
    moment(currentMonth).format("MMMM YY")
  );

  // console.log(firstMonthVal);
  if (!firstMonth) {
    dispatch(loadFirstMonth(currentMonth));
    dispatch(loadSecondMonth(currentMonth));
  }

  const handleChangeFirstMonth = (e) => {
    dispatch(loadFirstMonth(e.target.value));
    setStrFirstMonth(moment(e.target.value).format("MMMM YY"));
  };

  const handleChangeSecondMonth = (e) => {
    dispatch(loadSecondMonth(e.target.value));
    setStrSecondMonth(moment(e.target.value).format("MMMM YY"));
  };

  useEffect(() => {
    if (firstMonth !== null) {
      setStrFirstMonth(moment(firstMonth.month).format("MMMM YY"));
    }

    if (secondMonth !== null) {
      setStrSecondMonth(moment(secondMonth.month).format("MMMM YY"));
    }
  }, [firstMonth, secondMonth]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    // Get Target
    const getFirstTarget = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${firstMonth.start}&filter[end_before]=${firstMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        console.log(res.data);
        setFirstTarget(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getSecondTarget = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${secondMonth.start}&filter[end_before]=${secondMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        console.log(res.data);
        setSecondTarget(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    // get Settlement

    const getFirstSettlement = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ORDER_SETTLEMENT_COUNT&filter[start_after]=${firstMonth.start}&filter[end_before]=${firstMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        setFirstSettlement(res.data);
      } catch (error) {
        console.log(error);
      }
    };

    const getSecondSettlement = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ORDER_SETTLEMENT_COUNT&filter[start_after]=${secondMonth.start}&filter[end_before]=${secondMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        setSecondSettlement(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    //get interior design
    const getFirstInteriorDesign = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/interior-designs?&start_at=${firstMonth.start}&end_at=${firstMonth.end}`,
          config
        );
        setFirstInteriorDesign(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getSecondInteriorDesign = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/interior-designs?&start_at=${secondMonth.start}&end_at=${secondMonth.end}`,
          config
        );
        setSecondInteriorDesign(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    // get follow up
    const getFirstFollowUp = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ACTIVITY_COUNT&filter[start_after]=${firstMonth.start}&filter[end_before]=${firstMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        setFirstFollowUp(res.data.data[0]);
      } catch (error) {
        console.error(error);
      }
    };

    const getSecondFollowUp = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ACTIVITY_COUNT&filter[start_after]=${secondMonth.start}&filter[end_before]=${secondMonth.end}&filter[is_dashboard]=1&filter[reportable_type]=COMPANY`,
          config
        );
        setSecondFollowUp(res.data.data[0]);
      } catch (error) {
        console.error(error);
      }
    };

    // get brand

    const getBrandFirst = async () => {
      const dateFilter = `?end_at=${firstMonth.end}&start_at=${firstMonth.start}`;
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/brand-categories${dateFilter}`,
          config
        );
        console.log(res.data);
        setBrand1(res.data);
        // console.log("hello");
      } catch (error) {
        console.error(error);
      }
    };

    const getBrandSecond = async () => {
      const dateFilter = `?end_at=${secondMonth.end}&start_at=${secondMonth.start}`;
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/brand-categories${dateFilter}`,
          config
        );
        console.log(res.data);
        setBrand2(res.data);
        // console.log("hello");
      } catch (error) {
        console.error(error);
      }
    };

    getFirstTarget();
    getSecondTarget();
    getFirstSettlement();
    getSecondSettlement();
    getFirstInteriorDesign();
    getSecondInteriorDesign();
    getFirstFollowUp();
    getSecondFollowUp();
    getBrandFirst();
    getBrandSecond();
  }, [firstMonth, secondMonth]);

  // const pickColor = (value) => {
  //   if (value === "HOT") {
  //     return "#E53935";
  //   } else if (value === "WARM") {
  //     return "#FFD13D";
  //   } else if (value === "COLD") {
  //     return "#0553B7";
  //   } else if (value === "CLOSE") {
  //     return "#313132";
  //   }
  // };

  return firstTarget &&
    secondTarget &&
    firstSettlement &&
    secondSettlement &&
    firstInteriorDesign &&
    secondInteriorDesign &&
    firstFollowUp &&
    secondFollowUp ? (
    <div className="comparationv2" id="comparationv2">
      <Header
        title={"Comparation"}
        back={true}
        notif={true}
        user={false}
        data={""}
      />

      <div
        className="d-flex"
        style={{ marginTop: "10px", marginBottom: "10px" }}
      >
        <div className="d-flex" style={{ marginLeft: "auto" }}>
          <input
            type="month"
            id="firstMonth"
            name="firstMonth"
            className="form-control"
            placeholder="Start Month"
            value={firstMonth ? firstMonth.month : currentMonth}
            onChange={(e) => handleChangeFirstMonth(e)}
            style={{ width: "50%", marginRight: "10px" }}
          />

          <input
            type="month"
            id="secondMonth"
            name="secondMonth"
            className="form-control"
            value={secondMonth ? secondMonth.month : currentMonth}
            onChange={(e) => handleChangeSecondMonth(e)}
            placeholder="Start Month"
            style={{ width: "50%" }}
          />
        </div>
      </div>

      <div className="report-cards row">
        <div className="col-md-4">
          <div className="sales-revenue">
            <ReportCard
              data={firstTarget.data}
              title="Sales Revenue"
              strdate={strFirstMonth}
              dateBg="#17949D"
            />
            <hr style={{ width: "85%", margin: "0 auto" }} />
            <ReportCard
              data={secondTarget.data}
              title="Sales Revenue"
              strdate={strSecondMonth}
              dateBg="#FFD13D"
            />
          </div>
          <div className="sattlement">
            <ReportCard
              data={firstSettlement.data}
              title="Settlement"
              strdate={strFirstMonth}
              dateBg="#17949D"
            />
            <hr style={{ width: "85%", margin: "0 auto" }} />
            <ReportCard
              data={secondSettlement.data}
              title="Settlement"
              strdate={strSecondMonth}
              dateBg="#FFD13D"
            />
          </div>
          <div className="interior-design">
            <div className="report-card col-md-4">
              <div className="report-card-wrapper">
                <h5>
                  Interior Design{" "}
                  <span
                    style={{
                      background: "#17949D",
                    }}
                  >
                    {strFirstMonth}
                  </span>
                </h5>
                <h4>{firstInteriorDesign.total}</h4>
                <p></p>
              </div>
            </div>
            <hr style={{ width: "85%", margin: "0 auto" }} />
            <div className="report-card col-md-4">
              <div className="report-card-wrapper">
                <h5>
                  Interior Design{" "}
                  <span
                    style={{
                      background: "#FFD13D",
                    }}
                  >
                    {strSecondMonth}
                  </span>
                </h5>
                <h4>{secondInteriorDesign.total}</h4>
                <p></p>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-8">
          <ComparationBar
            brand1={brand1}
            brand2={brand2}
            firstDate={strFirstMonth}
            secondDate={strSecondMonth}
          />

          <div className="follow-up">
            <div className="skills-content p-4 " data-aos="fade-up">
              <div className="d-flex justify-content-between mb-4">
                <p>Sales By Category</p>
                <div className="d-flex">
                  <div style={{ paddingRight: "20px", marginRight: "80px" }}>
                    <p style={{ marginBottom: "8px" }}>January</p>
                    <h2
                      style={{
                        fontFamily: "'Poppins',sans-serif",
                        fontStyle: "normal",
                        fontWeight: "700",
                        fontSize: "20px",
                        lineHeight: "30px",
                        /* identical to box height */

                        color: "#313132",
                      }}
                    >
                      {firstFollowUp.value.value}
                    </h2>
                  </div>
                  <div style={{ paddingRight: "20px", marginRight: "40px" }}>
                    <p style={{ marginBottom: "8px" }}>February</p>
                    <h2
                      style={{
                        fontFamily: "'Poppins',sans-serif",
                        fontStyle: "normal",
                        fontWeight: "700",
                        fontSize: "20px",
                        lineHeight: "30px",
                        /* identical to box height */

                        color: "#313132",
                      }}
                    >
                      {secondFollowUp.value.value}
                    </h2>
                  </div>
                </div>
              </div>

              {/* {console.log(followUp)} */}
              <div>
                {/* {followUpComparation()} */}
                {firstFollowUp === undefined ||
                firstFollowUp === null ||
                !firstFollowUp.breakdown[0] ? (
                  ""
                ) : (
                  <Skill
                    // key={firstFollowUp.breakdown[0].enum_value}
                    title={firstFollowUp.breakdown[0].enum_value}
                    progress={
                      (firstFollowUp.breakdown[0].value /
                        firstFollowUp.value.value) *
                      100
                    }
                    color={"#17949D"}
                    value={firstFollowUp.breakdown[0].value}
                  />
                )}
                {secondFollowUp === undefined ||
                secondFollowUp === null ||
                !secondFollowUp.breakdown[0] ? (
                  ""
                ) : (
                  <Skill
                    // key={secondFollowUp.breakdown[0].enum_value}
                    title={""}
                    progress={
                      (secondFollowUp.breakdown[0].value /
                        secondFollowUp.value.value) *
                      100
                    }
                    color={"#FFD13D"}
                    value={secondFollowUp.breakdown[0].value}
                  />
                )}
                {firstFollowUp === undefined ||
                firstFollowUp === null ||
                !firstFollowUp.breakdown[1] ? (
                  ""
                ) : (
                  <Skill
                    // key={firstFollowUp.breakdown[1].enum_value}
                    title={firstFollowUp.breakdown[1].enum_value}
                    progress={
                      (firstFollowUp.breakdown[1].value /
                        firstFollowUp.value.value) *
                      100
                    }
                    color={"#17949D"}
                    value={firstFollowUp.breakdown[1].value}
                  />
                )}
                {secondFollowUp === undefined ||
                secondFollowUp === null ||
                !secondFollowUp.breakdown[1] ? (
                  ""
                ) : (
                  <Skill
                    // key={secondFollowUp.breakdown[1].enum_value}
                    title={""}
                    progress={
                      (secondFollowUp.breakdown[1].value /
                        secondFollowUp.value.value) *
                      100
                    }
                    color={"#FFD13D"}
                    value={secondFollowUp.breakdown[1].value}
                  />
                )}
                {firstFollowUp === undefined ||
                firstFollowUp === null ||
                !firstFollowUp.breakdown[2] ? (
                  ""
                ) : (
                  <Skill
                    // key={firstFollowUp.breakdown[2].enum_value}
                    title={firstFollowUp.breakdown[2].enum_value}
                    progress={
                      (firstFollowUp.breakdown[2].value /
                        firstFollowUp.value.value) *
                      100
                    }
                    color={"#17949D"}
                    value={firstFollowUp.breakdown[2].value}
                  />
                )}
                {secondFollowUp === undefined ||
                secondFollowUp === null ||
                !secondFollowUp.breakdown[2] ? (
                  ""
                ) : (
                  <Skill
                    // key={secondFollowUp.breakdown[2].enum_value}
                    title={""}
                    progress={
                      (secondFollowUp.breakdown[2].value /
                        secondFollowUp.value.value) *
                      100
                    }
                    color={"#FFD13D"}
                    value={secondFollowUp.breakdown[2].value}
                  />
                )}
                {firstFollowUp === undefined ||
                firstFollowUp === null ||
                !firstFollowUp.breakdown[3] ? (
                  ""
                ) : (
                  <Skill
                    // key={firstFollowUp.breakdown[3].enum_value}
                    title={firstFollowUp.breakdown[3].enum_value}
                    progress={
                      (firstFollowUp.breakdown[3].value /
                        firstFollowUp.value.value) *
                      100
                    }
                    color={"#17949D"}
                    value={firstFollowUp.breakdown[3].value}
                  />
                )}
                {secondFollowUp === undefined ||
                secondFollowUp === null ||
                !secondFollowUp.breakdown[3] ? (
                  ""
                ) : (
                  <Skill
                    // key={secondFollowUp.breakdown[3].enum_value}
                    title={""}
                    progress={
                      (secondFollowUp.breakdown[3].value /
                        secondFollowUp.value.value) *
                      100
                    }
                    color={"#FFD13D"}
                    value={secondFollowUp.breakdown[3].value}
                  />
                )}
              </div>
              {/* {firstFollowUp === undefined || firstFollowUp === null
                ? ""
                : firstFollowUp.breakdown.map((breakdown, i) => {
                    return (
                      <>
                        <Skill
                          key={breakdown.enum_value}
                          title={breakdown.enum_value}
                          progress={
                            (breakdown.value / firstFollowUp.value.value) * 100
                          }
                          color={"#17949D"}
                          value={breakdown.value}
                        />
                        <Skill
                          key={secondFollowUp.breakdown[i].enum_value}
                          title={secondFollowUp.breakdown[i].enum_value}
                          progress={
                            (secondFollowUp.breakdown[i].value /
                              secondFollowUp.value.value) *
                            100
                          }
                          color={"#FFD13D"}
                          value={breakdown.value}
                        />
                      </>
                    );
                  })} */}
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
});

export default connect(mapStateToProps)(Comparationv2);
