import React, { useEffect } from "react";
import "./Loaded.scss";

const NoData = () => {
  return (
    <div className="nodata-screen">
      <div className="text-center">
        <img src="/Asset/LoadImg/NoData.png" alt="No-Data" />
        <h3
          style={{
            fontFamily: "Poppins",
            fontStyle: "normal",
            fontWeight: "700",
            fontSize: "16px",
            lineHeight: "24px",
            /* identical to box height */

            color: "#17949D",
          }}
        >
          No Data Yet
        </h3>
        <h4
          style={{
            fontFamily: "Poppins",
            fontStyle: "normal",
            fontWeight: "400",
            fontSize: "14px",
            lineHeight: "21px",
            /* identical to box height */

            color: "#C4C4C4",
          }}
        >
          Data not yet available
        </h4>
      </div>
    </div>
  );
};

export default NoData;
