import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import "./SearchModel.scss";

const SearchModel = (props) => {
  const { search } = props;
  const [modelBySearch, setModelBySearch] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getModels = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/models?filter[name]=${search}&perPage=12`,
          config
        );
        setModelBySearch(res.data.data);
      } catch (error) {}
    };
    getModels();
  }, [search]);

  return (
    <div className="row mt-4" id="search-model">
      <h3>Product by Model</h3>
      {modelBySearch ? (
        modelBySearch.map((model, idx) => {
          return (
            <div className="col-md-2 product-card" key={idx}>
              <Link to={`/dashboard/product/product_detail/${model.id}`}>
                <img src={model.images[0].url} alt="product" />
                <h3>{model.name}</h3>
                <p>
                  {formatCurrency(model.price_min)} s/d{" "}
                  {formatCurrency(model.price_max)}
                </p>
              </Link>
            </div>
          );
        })
      ) : (
        <h3>Product is not found</h3>
      )}
    </div>
  );
};

export default SearchModel;
