import {
  ALL_USER_LIST_ERROR,
  ALL_USER_LIST_LOADED,
  CATEGORIES_ERROR,
  CATEGORIES_LOADED,
  CUSTOMERS_ERROR,
  CUSTOMERS_LOADED,
  SUBCATEGORIES_ERROR,
  SUBCATEGORIES_LOADED,
} from "../action/types";

const initialState = {
  categories: null,
  subCategories: null,
  allUsers: null,
  customers: null,
};

export default function foo(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case CATEGORIES_LOADED:
      return {
        ...state,
        loading: false,
        categories: payload,
      };
    case SUBCATEGORIES_LOADED:
      return {
        ...state,
        loading: false,
        subCategories: payload,
      };
    case ALL_USER_LIST_LOADED:
      return {
        ...state,
        loading: false,
        allUsers: payload,
      };
    case CUSTOMERS_LOADED:
      return {
        ...state,
        loading: false,
        customers: payload,
      };
    case CATEGORIES_ERROR:
    case SUBCATEGORIES_ERROR:
    case ALL_USER_LIST_ERROR:
    case CUSTOMERS_ERROR:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
}
