import axios from "axios";
import { mainUrl } from "../../api/api";
import {
  ACTIVE_CARTS_ERROR,
  ACTIVE_CARTS_LOADED,
  ADD_TEMPORARY_CART,
  BRAND_ERROR,
  BRAND_LOADED,
  CARTS_ERROR,
  CARTS_LOADED,
  ERROR_TEMPORARY_CART,
  UPDATE_TEMPORARY_CART,
} from "./types";

export const loadBrands = () => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(`${mainUrl}/brands`, config);

    dispatch({
      type: BRAND_LOADED,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: BRAND_ERROR,
    });
    console.error(error);
  }
};

export const temporaryCartLoad = (data) => async (dispatch) => {
  console.log(data);
  if (data) {
    dispatch({
      type: ADD_TEMPORARY_CART,
      payload: data,
    });
  }
  // else if (data.id !== product_id) {
  //   dispatch({
  //     type: UPDATE_TEMPORARY_CART,
  //     payload: data,
  //   });
  // }
  else {
    dispatch({
      type: ERROR_TEMPORARY_CART,
    });
  }
};

export const loadCarts = () => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${localStorage.token}`,
    },
  };

  try {
    const res = await axios.get(`${mainUrl}/carts`, config);
    console.log(res.data);
    dispatch({
      type: CARTS_LOADED,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: CARTS_ERROR,
    });
  }
};

export const loadActiveCarts = (data) => async (dispatch) => {
  // console.log(data);
  let items = { items: data };
  if (data) {
    dispatch({
      type: ACTIVE_CARTS_LOADED,
      payload: items,
    });
    console.log(data);
  } else {
    dispatch({
      type: ACTIVE_CARTS_ERROR,
    });
  }
};
