import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { mainUrl } from "../../../api/api";
import Popup from "../../../components/Popup/Popup";
import CartPopup from "../../../components/Popup/Popup Component/CartPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";
import ProductCart from "../../../components/Product-Components/Cart-Components/ProductCart";
import SummaryCart from "../../../components/Product-Components/Cart-Components/SummaryCart";
import { loadActiveCarts, loadCarts } from "../../../redux/action/product";

const Cart = ({ product: { carts } }) => {
  const dispatch = useDispatch();
  const [deleteCarts, setDeleteCarts] = useState(false);
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [isCheck, setIsCheck] = useState([]);
  const [emptyCart, setEmptyCart] = useState("");
  const [totalPrice, setTotalPrice] = useState(0);
  const [leadPopup, setLeadPopup] = useState(false);

  useEffect(() => {
    dispatch(loadCarts());
  }, [dispatch, deleteCarts]);

  // Delete All Carts
  const handleDeleteCarts = async () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const data = {
      items: [],
    };
    try {
      await axios.put(`${mainUrl}/carts`, data, config);
      // console.log(res.data.data);
      setDeleteCarts(!deleteCarts);
    } catch (error) {
      console.error(error);
    }
  };

  // Delete Cart
  const handleDeleteCart = async (item_id) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const items = [];
    carts.data.items
      .filter((item) => item.id !== item_id)
      .map((item) => items.push(item));
    const data = { items: items };
    // console.log(data);

    try {
      await axios.put(`${mainUrl}/carts`, data, config);
      // console.log(res.data.data);
      setDeleteCarts(!deleteCarts);
    } catch (error) {
      console.error(error);
    }
  };

  // increase quantity item
  const handleAddQuantity = async (item, i) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const quantity = item.quantity + 1;
    const addQuantity = { id: item.id, quantity: quantity };
    const left = [];
    const right = [];
    const center = [addQuantity];
    carts.data.items
      .filter((data) => data.id !== item.id)
      .map((data, idx) => (idx < i ? left.push(data) : right.push(data)));

    const items = left.concat(center).concat(right);

    const data = { items };
    try {
      await axios.put(`${mainUrl}/carts`, data, config);
      // console.log(res.data.data);
      setDeleteCarts(!deleteCarts);
    } catch (error) {
      console.error(error);
    }
  };

  // decrease quantity item
  const handleDecreseQuantity = async (item, i) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const quantity = item.quantity - 1;
    const decreaseQuantity = { id: item.id, quantity: quantity };
    const left = [];
    const right = [];
    const center = [decreaseQuantity];
    carts.data.items
      .filter((data) => data.id !== item.id)
      .map((data, idx) => (idx < i ? left.push(data) : right.push(data)));

    const items = left.concat(center).concat(right);
    const data = { items };
    try {
      await axios.put(`${mainUrl}/carts`, data, config);
      // console.log(res.data.data);
      setDeleteCarts(!deleteCarts);
    } catch (error) {
      console.error(error);
    }
  };

  // to check all items checked
  useEffect(() => {
    if (carts) {
      if (carts.data.items.length === isCheck.length) {
        setIsCheckAll(true);
      }
    }
  }, [isCheck, carts]);

  // make all items checked
  const handleCheckedAll = (e) => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(carts.data.items.map((li) => li));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  // checked an item
  const handleCheck = (e, item) => {
    const { checked } = e.target;

    setIsCheck([...isCheck, item]);
    if (!checked) {
      setIsCheck(isCheck.filter((data) => data !== item));
      setIsCheckAll(false);
    }
  };

  // validate if user checked item before choose lead/prospact
  const handleClick = () => {
    if (isCheck.length === 0) {
      setEmptyCart("No item selected!");
    } else {
      setLeadPopup(!leadPopup);
      dispatch(loadActiveCarts(isCheck));
    }
  };

  // sum total price
  useEffect(() => {
    let sum = 0;
    let items = 0;
    if (isCheck.length > 0) {
      for (let i = 0; i < isCheck.length; i++) {
        sum += isCheck[i].total_price;
        items += isCheck[i].quantity;
      }
    } else {
      sum = 0;
    }
    setTotalPrice({ totalPrice: sum, qty: items });
  }, [isCheck]);

  return carts ? (
    <div className="cart" id="cart">
      <Popup popup={leadPopup} setPopup={() => setLeadPopup()}>
        <CartPopup />
      </Popup>
      <div className="row">
        <div className="col-md-8">
          <div className="d-flex justify-content-between">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value=""
                id="flexCheckDefault"
                onChange={(e) => handleCheckedAll(e)}
                name="selectAll"
                checked={isCheckAll}
              />
              <label className="form-check-label">Select All</label>
            </div>
            <div
              className="delete-all"
              style={{ cursor: "pointer" }}
              onClick={() => handleDeleteCarts()}
            >
              Hapus
            </div>
          </div>
          <hr />
          {emptyCart ? (
            <div className="alert alert-warning" role="alert">
              {emptyCart}
            </div>
          ) : (
            ""
          )}

          {carts.data.items.map((item, idx) => {
            return (
              <ProductCart
                item={item}
                idx={idx}
                isCheck={isCheck}
                handleCheck={handleCheck}
                handleDecreseQuantity={handleDecreseQuantity}
                handleAddQuantity={handleAddQuantity}
                handleDeleteCart={handleDeleteCart}
                key={idx}
              />
            );
          })}
        </div>
        <div className="col-md-4">
          <SummaryCart totalPrice={totalPrice} handleClick={handleClick} />
        </div>
      </div>
    </div>
  ) : (
    <div
      className="d-flex justify-content-center align-items-center"
      style={{ height: "100vh" }}
    >
      <div className="text-center">
        <img src="/Asset/Empty Cart.svg" alt="cart empty" />
        <h4>Cart is Empty</h4>
        <h5>Look like you haven't made your choice yet</h5>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(Cart);
