import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import Button from "../Button/Button";
import LoadedComponent from "../Loaded/LoadedComponent";
import Popup from "../Popup/Popup";
import DiscountPopup from "../Popup/Popup Component/DiscountPopup";
import CancelOrder from "../Popup/Popup Components/CancelOrder/CancelOrder";
import UpdateDiscounts from "../Popup/Popup Components/UpdateDiscounts/UpdateDiscounts";
import Invoice from "./Invoice";
import NewProduct from "./NewProduct";
import PaymentDetail from "./PaymentDetail";
import ProductListsOrder from "./ProductListsOrder";
import Quotation from "./Quotation";

const OrderDetail = (props) => {
  const { orderId, user } = props;
  const [orderData, setOrderData] = useState(null);
  const [loadOrder, setLoadOrder] = useState(false);

  const [updateDiscountsPopup, setUpdateDiscountPopup] = useState(false);
  const [updateDiscount, setUpdateDiscount] = useState(false);
  const [cancelPopup, setCancelPopup] = useState(false);

  const [allDiscount, setAllDiscount] = useState([]);

  const [discount_ids, setDiscount_ids] = useState([]);

  const [percentageForm, setPercentageForm] = useState(false);
  const [packing, setPacking] = useState(0);
  const [shipping, setShipping] = useState(null);
  const [addDiscount, setAddDiscount] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    console.log(orderId);
    setLoadOrder(false);
    const getOrderData = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/orders?filter[id]=${orderId}`,
          config
        );
        console.log(res.data.data[0]);
        setOrderData(res.data.data[0]);
        setLoadOrder(true);
      } catch (error) {
        console.error(error);
        setLoadOrder(true);
      }
    };

    getOrderData();
  }, [orderId]);
  return orderData && loadOrder ? (
    <div>
      {orderData.cart_demand && (
        <NewProduct cart_demand={orderData.cart_demand} />
      )}

      <ProductListsOrder order_details={orderData.order_details} user={user} />

      <hr />
      <div>
        <div className="d-flex justify-content-between">
          <div>
            <p>Packing Fee:</p>
            <p>Shipping Fee:</p>
            <p>Discount:</p>
            <p>Discount Name:</p>
            <p>Sub total:</p>
            <p>Additional Discount:</p>
          </div>

          <div className="text-end">
            <p>
              {orderData.packing_fee
                ? formatCurrency(orderData.packing_fee)
                : formatCurrency(0)}
            </p>
            <p>
              {orderData.shipping_fee
                ? formatCurrency(orderData.shipping_fee)
                : formatCurrency(0)}
            </p>
            <p>{orderData.discount ? orderData.discount.value : "Rp 0"}</p>
            <p>{orderData.discount ? orderData.discount.name : "-"}</p>
            <p>
              {formatCurrency(
                orderData.original_price - orderData.total_discount
              )}
            </p>
            <p>
              {orderData.additional_discount
                ? formatCurrency(orderData.additional_discount)
                : formatCurrency(0)}
              (
              {orderData.additional_discount_ratio
                ? orderData.additional_discount_ratio
                : 0}
              %)
            </p>
          </div>
        </div>
        <hr />
        <div className="d-flex justify-content-between">
          <div>
            <p>Total Price:</p>
            <p>Expected Delivery Date:</p>
          </div>
          <div className="text-end">
            <p>{formatCurrency(orderData.total_price)}</p>
            <p>
              {moment(orderData.expected_shipping_datetime).format(
                "DD MMMM YYYY"
              )}
            </p>
          </div>
        </div>
        <hr />
        <div className="d-flex justify-content-between">
          <div>
            <p>Payment Status:</p>
            <p>Total Paid:</p>
            <p>Total Unpaid:</p>
          </div>
          <div className="text-end">
            <p>{orderData.payment_status}</p>
            <p>{formatCurrency(orderData.amount_paid)}</p>
            <p>
              {formatCurrency(orderData.total_price - orderData.amount_paid)}
            </p>
          </div>
        </div>
        <hr />

        {orderData.approval_status === "WAITING_APPROVAL" ? (
          <div className="mb-2">
            <div className="justify-content-between">
              <div className="d-flex justify-content-between">
                <div>
                  <p>Request by:</p>
                  <p>Channel Name:</p>
                  <p>Status:</p>
                </div>

                <div className="text-end">
                  <p>{orderData.user.name}</p>
                  <p>{orderData.channel.name}</p>
                  <p>
                    {orderData.approval_status && orderData.approval_status}
                  </p>
                </div>
              </div>
            </div>
          </div>
        ) : orderData.approval_status === "REJECTED" ? (
          ""
        ) : (
          <PaymentDetail orderId={orderId ? orderId : ""} />
        )}

        {orderData.payment_status === "SETTLEMENT" ? (
          <Invoice orderId={orderId ? orderId : ""} />
        ) : (
          <div>
            {orderData.approval_status !== "WAITING_APPROVAL" ? (
              orderData.approval_status === "REJECTED" ? (
                <div className="mt-2">
                  <div>
                    <Button
                      title={"Add Payment"}
                      btnStyle={"primary-btn"}
                      btnWidth={"100%"}
                      disabled={true}
                    />
                  </div>
                </div>
              ) : (
                <div className="mt-2">
                  <Link to={`/dashboard/payment/${orderId}`}>
                    <Button
                      title={"Add Payment"}
                      btnStyle={"primary-btn"}
                      btnWidth={"100%"}
                      disabled={false}
                    />
                  </Link>
                </div>
              )
            ) : (
              ""
            )}
            <Quotation orderId={orderId ? orderId : ""} />
          </div>
        )}

        {orderData.payment_status !== "NONE" ? (
          ""
        ) : user.data.type === "SALES" ? (
          <div className="d-flex justify-content-between">
            <div
              style={{ width: "48%" }}
              onClick={() => setUpdateDiscount(!updateDiscount)}
            >
              <Button
                title={"Edit Discounts"}
                btnStyle={"outline-btn"}
                btnWidth={"100%"}
                disabled={false}
              />
            </div>
            <div
              style={{ width: "48%" }}
              onClick={() => setCancelPopup(!cancelPopup)}
            >
              <Button
                title={"Cancel Order"}
                btnStyle={"btn-danger"}
                btnWidth={"100%"}
                disabled={false}
              />
            </div>
          </div>
        ) : (
          ""
        )}
        {updateDiscount && (
          <div className="mt-3 mb-3" style={{padding: '15px', border: '1px solid #000'}}>
            <h3>Discount:</h3>

            <div
              className="p-2 d-flex justify-content-between"
              style={{
                border: "1px solid #000",
                marginBottom: "5px",
                borderRadius: "4px",
              }}
              onClick={() => setUpdateDiscountPopup(!updateDiscountsPopup)}
            >
              <div>
                <div className="d-flex align-items-center">
                  <img src="/Asset/Discount.svg" alt="discount" />
                  <h5 style={{ margin: "0 5px" }}>Discount</h5>
                </div>
              </div>
              <img src="/Asset/Arrow Icon/RightArrow.png" alt="rightarrow" />
            </div>

            <div className="mt-3">
              <label>Additional Discount</label>
              <div className="form-check">
                <label htmlFor="checkboxForPercentageForm">
                  Discount in Percent
                </label>
                <input
                  className="form-check-input"
                  type="checkbox"
                  value=""
                  // onChange={(e) => handleCheckBox(e)}
                  id="checkboxForPercentageForm"
                />
              </div>
              {percentageForm ? (
                <input
                  type="text"
                  id="delivery"
                  name="delivery"
                  className="form-control"
                  placeholder="Input %"
                  onChange={(e) => setAddDiscount(e.target.value)}
                  style={{ height: "41px" }}
                />
              ) : (
                <input
                  type="text"
                  id="delivery"
                  name="delivery"
                  className="form-control"
                  placeholder="Input here"
                  onChange={(e) => setAddDiscount(e.target.value)}
                  style={{ height: "41px" }}
                />
              )}
            </div>
            <div className="mt-2">
              <Button
                title={"Update Discount"}
                icon={""}
                btnWidth={"100%"}
                btnStyle="primary-btn"
              />
            </div>
          </div>
        )}
      </div>
      <Popup popup={cancelPopup} setPopup={setCancelPopup}>
        <CancelOrder orderId={orderId} setPopup={setCancelPopup} />
      </Popup>
      <Popup popup={updateDiscountsPopup} setPopup={setUpdateDiscountPopup}>
        <DiscountPopup
          setDiscount_ids={setDiscount_ids}
          discount_ids={discount_ids}
          setPopup={setUpdateDiscountPopup}
          setAllDiscount={setAllDiscount}
          allDiscount={allDiscount}
        />
      </Popup>
    </div>
  ) : orderData === undefined ? (
    ""
  ) : (
    <LoadedComponent />
  );
};

export default OrderDetail;
