import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Activity from "../../../components/ActivityDetail/Activity";
import Button from "../../../components/Button/Button";
import CustomerActivities from "../../../components/CustomerActivities/CustomerActivities";
import CustomerDeals from "../../../components/CustomerActivities/CustomerDeals";
import CustomerCard from "../../../components/CustomerCard/CustomerCard";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import Popup from "../../../components/Popup/Popup";
import AddActivityPopup from "../../../components/Popup/Popup Component/AddActivityPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";
import { isValidate } from "../../../lib/Validate";
import "./CustomerDetail.css";

const CustomerDetail = ({ auth: { user, loading } }) => {
  const { leads_id, customer_id } = useParams();
  const [addActivity, setAddActivity] = useState(false);
  // const [orderId, setOrderId] = useState(null);
  const [refresh, setRefresh] = useState(false);

  return leads_id && customer_id ? (
    <div className="customer-detail" id="customer-detail">
      <div>
        <Header
          title="Customer Detail"
          icon=""
          back={true}
          user={false}
          notif={false}
        />
      </div>
      <CustomerCard leads_id={leads_id} />
      <CustomerActivities
        customer_id={customer_id}
        leads_id={leads_id}
        refresh={refresh}
      />
      <div
        onClick={() => setAddActivity(!addActivity)}
        style={{ width: "fit-content" }}
      >
        <Button title="+Activity" icon="" btnStyle="primary-btn" />
      </div>
      <Popup popup={addActivity} setPopup={setAddActivity}>
        <AddActivityPopup
          lead_id={leads_id}
          refresh={refresh}
          setRefresh={setRefresh}
          popup={addActivity}
          setPopup={setAddActivity}
        />
      </Popup>
      <CustomerDeals
        customer_id={customer_id}
        leads_id={leads_id}
        refresh={refresh}
      />
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(CustomerDetail);
