import axios from "axios";
import React, { useEffect, useState } from "react";
import ReactPaginate from "react-paginate";
import moment from "moment";
import { connect, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Button from "../../../components/Button/Button";
import Header from "../../../components/Header/Header";
import AddLead from "../../../components/Popup/Popup Component/AddLead";
import NewCustomerPopup from "../../../components/Popup/Popup Component/NewCustomerPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";
import {
  loadSubCategories,
  loadUserList,
} from "../../../redux/action/customer";
import { loadChannels } from "../../../redux/action/target";
import Loaded from "../../../components/Loaded/Loaded";
import LoadedComponent from "../../../components/Loaded/LoadedComponent";
import { pickColorStatus } from "../../../api/pickColor";
import { isValidate } from "../../../lib/Validate";
import Popup from "../../../components/Popup/Popup";

const CustomersScreen = (
  {
    auth: { user, loading },
    target: { channels },
    customer: { categories, subCategories, allUsers },
  },
  props
) => {
  const dispatch = useDispatch();
  const { customers_type } = useParams();

  // loading
  const [isLoading, setIsLoading] = useState(false);

  // filter
  const [filterCustomer, setFilterCustomer] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterStatus, setFilterStatus] = useState();
  const [filterCategory, setFilterCategory] = useState();
  const [filterSubCategory, setFilterSubCategory] = useState();
  const [filterUser, setFilterUser] = useState();
  const [filterHasActivity, setFilterHasActivity] = useState();

  // data
  const [leadsData, setLeadsData] = useState(null);

  // pagination
  const [page, setPage] = useState(0);
  const [pageData, setPageData] = useState(0);

  // popup toggle & toggle
  const [addLeadPopup, setAddLeadPopup] = useState(false);
  const [newCustomerPopup, setNewCustomerPopup] = useState(false);
  const [update, setUpdate] = useState(false);

  useEffect(() => {
    setIsLoading(true);
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    if (user) {
      dispatch(loadChannels(user.id));
      dispatch(loadUserList(user.data));
    }
    dispatch(loadSubCategories(filterCategory));

    const getleadsData = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/leads?filter[type]=${customers_type.toUpperCase()}&filter[user_id]=${
            filterUser ? filterUser : ""
          }&filter[status]=${
            filterStatus ? filterStatus : ""
          }&filter[lead_category_id]=${
            filterCategory ? filterCategory : ""
          }&filter[sub_lead_category_id]=${
            filterCategory ? (filterSubCategory ? filterSubCategory : "") : ""
          }&filter[channel_name]=${
            filterChannels ? filterChannels : ""
          }&filter[customer_has_activity]=${
            filterHasActivity ? filterHasActivity : ""
          }&perPage=10&page=${page}&filter[customer_search]=${
            filterCustomer ? filterCustomer : ""
          }`,
          config
        );
        setLeadsData(res.data.data);
        console.log(res.data.data);
        setPageData(res.data.meta);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
      }
    };

    getleadsData();
  }, [
    filterChannels,
    filterStatus,
    filterUser,
    filterHasActivity,
    filterCategory,
    filterSubCategory,
    filterCustomer,
    page,
    dispatch,
    user,
    customers_type,
    update,
  ]);

  const handlePageClick = (data) => {
    setPage(data.selected + 1);
  };

  return channels && leadsData && categories && allUsers ? (
    <div className="customers" id="customers">
      <Header
        icon=""
        title={`Customers - ${customers_type.toUpperCase()}`}
        back={false}
        user={false}
      />
      <div className="customers-filter-form d-flex justify-content-between">
        <div className="d-flex align-items-center">
          <input
            type="text"
            className="form-control"
            name="companys"
            style={{ height: "41px", width: "261px" }}
            placeholder="Search customer name"
            value={filterCustomer}
            onChange={(e) => setFilterCustomer(e.target.value)}
          />
        </div>
        <div className="d-flex">
          <select
            className="form-select"
            name="channels"
            onChange={(e) => setFilterChannels(e.target.value)}
          >
            <option value="">Select Channel</option>
            {channels &&
              channels.data.map((channel) => {
                return (
                  <option value={channel.name} key={channel.id}>
                    {channel.name}
                  </option>
                );
              })}
          </select>
          <select
            className="form-select"
            name="status"
            style={{ marginLeft: "10px", height: "41px" }}
            onChange={(e) => setFilterStatus(e.target.value)}
          >
            <option value="">Select Status</option>
            <option value={"GREEN"}>Green</option>
            <option value={"YELLOW"}>Yellow</option>
            <option value={"RED"}>Red</option>
            <option value={"EXPIRED"}>Expired</option>
            <option value={"SALES"}>Sales</option>
            <option value={"OTHER SALES"}>Other Sales</option>
          </select>

          <select
            className="form-select"
            name="category"
            style={{ marginLeft: "10px", height: "41px" }}
            onChange={(e) => setFilterCategory(e.target.value)}
          >
            <option value="">Select Category</option>
            {categories &&
              categories.map((category) => {
                return (
                  <option value={category.id} key={category.id}>
                    {category.name}
                  </option>
                );
              })}
          </select>
          <select
            className="form-select"
            name="sub-category"
            style={{ marginLeft: "10px", height: "41px" }}
            onChange={(e) => setFilterSubCategory(e.target.value)}
          >
            <option value="">Select Sub Category</option>
            {subCategories &&
              subCategories.map((subCategory) => {
                return (
                  <option value={subCategory.id} key={subCategory.id}>
                    {subCategory.name}
                  </option>
                );
              })}
          </select>
          <select
            className="form-select"
            name="user"
            style={{ marginLeft: "10px", height: "41px" }}
            onChange={(e) => setFilterUser(e.target.value)}
          >
            <option value="">Select User</option>
            {allUsers &&
              allUsers.map((user) => {
                return (
                  <option value={user.id} key={user.id}>
                    {user.name}
                  </option>
                );
              })}
          </select>
          <select
            className="form-select"
            name="has-activity"
            style={{ marginLeft: "10px", height: "41px" }}
            onChange={(e) => setFilterHasActivity(e.target.value)}
          >
            <option value="">Has Activity</option>
            <option value="true">Yes</option>
            <option value="false">No</option>
          </select>
        </div>
      </div>
      <div>
        {leadsData && !isLoading ? (
          <table className="data" style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>No</th>
                <th>Customer Name</th>
                <th>Last Follow Up</th>
                <th>Customer From</th>
                <th>Store Location</th>
                <th>Sales</th>
                <th>Priority</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {leadsData.map((data, idx) => {
                return (
                  <tr key={data.id}>
                    <td>
                      {!isValidate(pageData.from) ? pageData.from + idx : idx}
                    </td>
                    <td>{`${
                      !isValidate(data.customer.first_name)
                        ? data.customer.first_name
                        : ""
                    } ${
                      !isValidate(data.customer.last_name)
                        ? data.customer.last_name
                        : ""
                    }`}</td>
                    <td>
                      {data.has_activity
                        ? moment(data.updated_at).format("DD/MM/YYYY")
                        : "No Activity Yet"}
                    </td>
                    {/* <td>{data.lead_category ? data.lead_category.name : ""}</td> */}
                    <td>
                      {!isValidate(data.lead_sub_category)
                        ? data.lead_sub_category.name
                        : ""}
                    </td>
                    <td>
                      {!isValidate(data.channel.name) ? data.channel.name : ""}
                    </td>
                    <td>{!isValidate(data.user.name) ? data.user.name : ""}</td>
                    <td>
                      <div
                        style={{
                          color: "#fff",
                          padding: "5px",
                          backgroundColor: pickColorStatus(data.status),
                          textAlign: "center",
                          borderRadius: "8px",
                        }}
                      >
                        {!isValidate(data.status) && data.status}
                      </div>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/customers/customer_detail/${data.id}/${data.customer.id}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "11px" }}
                          alt="eye"
                        />
                      </Link>
                      {!isValidate(data.customer.phone) && (
                        <a
                          href={`https://api.whatsapp.com/send?phone=0${parseInt(
                            data.customer.phone
                          )}`}
                          target="_blank"
                          rel="noreferrer"
                        >
                          <img
                            src="/Asset/Action Icon/Wa.png"
                            style={{ marginRight: "11px" }}
                            alt="eye"
                          />
                        </a>
                      )}

                      {!isValidate(data.customer.email) && (
                        <a href={`mailto: ${data.customer.email}`}>
                          <img
                            src="/Asset/Action Icon/email.png"
                            style={{ marginRight: "11px" }}
                            alt="eye"
                          />
                        </a>
                      )}
                      {!isValidate(data.customer.phone) && (
                        <a href={`tel:${data.customer.phone}`}>
                          <img
                            src="/Asset/Action Icon/phone.svg"
                            style={{ marginRight: "11px" }}
                            alt="eye"
                          />
                        </a>
                      )}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        ) : (
          <LoadedComponent />
        )}
        {pageData.last_page && (
          <div>
            <ReactPaginate
              previousLabel={"Prev"}
              nextLabel={"Next"}
              breakLabel={"..."}
              pageCount={pageData.last_page}
              marginPagesDisplayed={3}
              // pageRangeDisplayed={3}
              onPageChange={handlePageClick}
              containerClassName={"pagination"}
              pageClassName={"page-item"}
              pageLinkClassName={"page-link"}
              previousClassName={"page-item"}
              previousLinkClassName={"page-link"}
              nextClassName={"page-item"}
              nextLinkClassName={"page-link"}
              breakClassName={"page-item"}
              breakLinkClassName={"page-link"}
              activeClassName={"active"}
            />
          </div>
        )}
      </div>
      <div className="d-flex">
        <div
          onClick={() => setAddLeadPopup(!addLeadPopup)}
          style={{ width: "fit-content", marginRight: "10px" }}
        >
          <Button
            title="Existing Customer"
            icon=""
            btnWidth="150px"
            btnStyle="primary-btn"
          />
        </div>
        <div
          onClick={() => setNewCustomerPopup(!newCustomerPopup)}
          style={{ width: "fit-content" }}
        >
          <Button
            title="New Customer"
            icon=""
            btnWidth="150px"
            btnStyle="primary-btn"
          />
        </div>
      </div>

      <Popup popup={newCustomerPopup} setPopup={() => setNewCustomerPopup()}>
        <NewCustomerPopup
          Popup={newCustomerPopup}
          setPopup={setNewCustomerPopup}
          setUpdate={setUpdate}
          update={update}
        />
      </Popup>

      <Popup popup={addLeadPopup} setPopup={() => setAddLeadPopup()}>
        <AddLead
          Popup={addLeadPopup}
          setPopup={setAddLeadPopup}
          setUpdate={setUpdate}
          update={update}
        />
      </Popup>
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
  customer: state.customer,
});

export default connect(mapStateToProps)(CustomersScreen);
