import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import Loaded from "../Loaded/Loaded";

const SalesEstimation = () => {
  const { user_type, user_id, start, end } = useParams();

  //   filter
  const [filterStartDate, setFilterStartDate] = useState(
    start === "now" ? "" : start
  );
  const [filterEndDate, setFilterEndDate] = useState(end === "now" ? "" : end);

  const [salesEstimationData, setSalesEstimationData] = useState(null);

  useEffect(() => {
    const getSalesEstimation = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/sales-estimation/all?start_at=${
            filterStartDate && moment(filterStartDate).format("YYYY-MM-DD")
          }&end_at=${
            filterEndDate && moment(filterEndDate).format("YYYY-MM-DD")
          }&${
            user_type === "bum"
              ? "supervisor_id=" + user_id
              : user_type === "channel"
              ? "channel_id=" + user_id
              : "sales_id=" + user_id
          }`,
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        );
        console.log(res.data);
        const salesEstimation = [];
        for (const [key, value1] of Object.entries(res.data)) {
          let data = [];
          for (const [key, value] of Object.entries(value1)) {
            data.push(value);
          }
          salesEstimation.push({ data: data });
        }
        console.log(salesEstimation);
        setSalesEstimationData(salesEstimation);
      } catch (error) {
        console.error(error);
      }
    };
    getSalesEstimation();
  }, [filterStartDate, filterEndDate, user_type, user_id]);

  const sortUp = () => {
    // salesEstimationData
    // salesEstimationData.sort(function(a, b){return a-b});
    setSalesEstimationData(salesEstimationData.sort());
  };

  return salesEstimationData ? (
    <div>
      <Header title={`Sales Estimation`} back={false} notif={true} />

      <FilterLeadsReport
        company={false}
        channel={false}
        supervisor={false}
        setFilterStartDate={setFilterStartDate}
        setFilterEndDate={setFilterEndDate}
      />

      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>Date</th>
              <th onClick={() => sortUp()}>Sales</th>
              <th>Customer Name</th>
              <th>Brand</th>
              <th>Estimated Value</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {salesEstimationData &&
              salesEstimationData.map((data, i) => {
                return (
                  <tr key={i}>
                    <td>{i + 1}.</td>
                    <td>
                      {moment(data.data[0].created_at).format("YYYY-MM-DD")}
                    </td>
                    <td>{data.data[0].sales}</td>
                    <td>{data.data[0].customer}</td>
                    <td>
                      <ul>
                        {data.data.map((d, i) => {
                          return <li key={i}>{d.brand}</li>;
                        })}
                      </ul>
                    </td>
                    <td>
                      <ul>
                        {data.data.map((d, i) => {
                          return (
                            <li key={i}>{formatCurrency(d.estimated_value)}</li>
                          );
                        })}
                      </ul>
                    </td>
                    <td>
                      <Link to={`/dashboard/customers/customer_detail/${data.data[0].lead_id}/${data.data[0].customer_id}`}>
                        Leads Detail
                      </Link>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default SalesEstimation;
