import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";
import axios from "axios";
import moment from "moment";
import Loaded from "../../../components/Loaded/Loaded";

const TopChannel = () => {
  const { company_id, month } = useParams();

  const [topChannel, setTopChannel] = useState(null);

  //   const [date, setDate] = useState(month);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesRevenueBUM = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/index-top-sales/target?company_id=${company_id}&start_at=${moment(
            month
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&end_at=${moment(month)
            .endOf("month")
            .format("YYYY-MM-DD")}&limit=20`,
          config
        );
        setTopChannel(res.data.channel);
        console.log(res.data.channel);
      } catch (error) {
        console.error(error);
      }
    };
    getSalesRevenueBUM();
  }, [company_id, month]);
  return topChannel ? (
    <div>
      <Header title={"Top Channel"} back={true} notif={true} />
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>Supervisor Name</th>
              <th>Revenue</th>
              <th>Target</th>
              <th>Meet Goal</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {topChannel.map((data, i) => {
              return (
                <tr key={i}>
                  <td>{i + 1}.</td>
                  <td>{data.model.name}</td>
                  <td>{data.value}</td>
                  <td>{data.target}</td>
                  <td style={{ color: "#2DCC70" }}>{data.percentage}</td>
                  <td>
                    <Link
                      to={`/dashboard/top-sales/channel_id/${company_id}/${month}/${data.model.id}`}
                    >
                      <img
                        src="/Asset/Action Icon/eye.png"
                        style={{ marginRight: "5px" }}
                        alt="eye"
                      />
                      See Detail
                    </Link>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default TopChannel;
