export function isValidate(value) {
  if (
    value === "" ||
    value === null ||
    value === undefined ||
    value === "undefined"
  ) {
    return true;
  } else {
    return false;
  }
}

export function isValidateNumb(value) {
  if (
    value === "" ||
    value === null ||
    value === undefined ||
    value === "undefined" ||
    value === 0 ||
    value < 0
  ) {
    return true;
  } else {
    return false;
  }
}
