import React from "react";
import ReactApexChart from "react-apexcharts";
import { Link } from "react-router-dom";

const Bar = (props) => {
  let data = [];
  // let totalRevenue = null;
  let dataName = [];
  let totalRevenue = 0;
  let revenueName = "";


  if (props.brand) {
    revenueName = props.brand[3].name;
    // totalRevenue = props.brand[3].total_price;
    for (let i = 0; i < props.brand.length; i++) {
      // let brandLength = props.brand[i].total_price.split(" ").length
      let temp = props.brand[i].total_price.split(" ")[1];
      let brand = parseInt(temp.replaceAll(".", ""));
      let brand_name = props.brand[i].name;
      data = [...data, brand];
      dataName = [...dataName, [brand_name]];
      // totalRevenue =
      //   totalRevenue + parseInt(props.brand[i].total_price.split(" ")[1]);
    }
  }

  // console.log(data);

  const series = [
    {
      name: "Sales",
      data: data,
    },
  ];

  const options = {
    chart: {
      height: 350,
      type: "bar",
      events: {
        click: function (chart, w, e) {
          // console.log(chart, w, e)
        },
      },
    },
    colors: ["#17949D"],
    plotOptions: {
      bar: {
        borderRadius: 10,
        columnWidth: "25%",
        distributed: true,
      },
    },
    dataLabels: {
      enabled: false,
    },
    legend: {
      show: false,
    },
    xaxis: {
      categories: dataName,
      labels: {
        style: {
          colors: ["#000"],
          fontSize: "12px",
        },
      },
    },
    yaxis: {
      title: {
        text: "Rupiah",
      },
    },
  };

  const colorBorder = (idx) => {
    if (idx === 0) return "#FF0000";
    if (idx === 1) return "#FFD13D";
    if (idx === 2) return "#0553B7";
    if (idx === 3) return "#2DCC70";
  };
  return (
    <div
      className="bar-chart"
      style={{
        background: "#FFFFFF",
        boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.05)",
        borderRadius: "8px",
        padding: "20px",
        margin: "15px 0",
        // width: "500px",
      }}
    >
      <div className="d-flex justify-content-between mb-2">
        <p>Brand Category</p>
        {/* <Link to={"/dashboard/report_brands"}>
          <div
            style={{
              textDecoration: "none",
              color: "#fff",
              padding: "8px 16px",
              borderRadius: "4px",
              fontSize: "11px",
              margin: "0 5px",
              height: "fit-content",
              background: "#17949d",
            }}
          >
            <span style={{ fontSize: "14px" }}>Report</span>
          </div>
        </Link> */}
      </div>

      <div className="d-flex">
        {props.brand &&
          props.brand.map((data, i) => {
            return (
              <Link
                key={i}
                style={{
                  textDecoration: "none",
                  color: "#000",
                  padding: "5px",
                  border: `0.5px solid ${colorBorder(i)}`,
                  width: "50%",
                  borderRadius: "4px",
                  fontSize: "11px",
                  margin: "0 5px",
                }}
                to={`/dashboard/brand_category/${data.name}/${data.id}/${props.company_id}/${props.month}`}
              >
                {data.name}
              </Link>
            );
          })}
      </div>

      {/* <h4>{revenueName}</h4>*/}
      {/* <h2>{totalRevenue}</h2> */}

      <div id="chart">
        <ReactApexChart
          options={options}
          series={series}
          type="bar"
          height={227}
        />
      </div>
    </div>
  );
};

export default Bar;
