import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";

const SelectChannel = () => {
  const [stocks, setStocks] = useState(null);
  const [filterChannel, setFilterChannel] = useState("");

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getStock = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/stocks/indexNew?name=${
            filterChannel ? filterChannel : ""
          }`,
          config
        );
        console.log(res.data.data);
        setStocks(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getStock();
  }, [filterChannel]);

  return (
    <div className="stock" id="stock">
      <Header title="Select Channel" back={true} icon="" />
      <div className="d-flex align-items-center">
        <input
          type="text"
          className="form-control"
          name="companys"
          style={{ height: "41px", width: "261px" }}
          placeholder="Search customer name"
          value={filterChannel}
          onChange={(e) => setFilterChannel(e.target.value)}
        />
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>Product Unit</th>
              <th>Channel</th>
              <th>Total Stock</th>
              <th>See Detail</th>
            </tr>
          </thead>
          <tbody>
            {stocks &&
              stocks.map((stock, idx) => {
                return (
                  <tr key={idx}>
                    <td>{idx + 1}</td>
                    <td>{stock.name}</td>
                    <td>{stock.channel_stocks_sum_stock}</td>
                    <td>
                      <Link
                        to={`/dashboard/product/stocks/${stock.company_id}/${stock.channel_category_id}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "11px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                      
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default SelectChannel;
