import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Bar from "../../../components/Chart/Bar";
import LoadedComponent from "../../../components/Loaded/LoadedComponent";
import ReportCard from "../../../components/Report Card/ReportCard";
import { loadChannels, loadUsersList } from "../../../redux/action/target";
import Skill from "./Skill";

import ReportExports from "../../../components/Report/ReportExports";
import Filters from "../../../components/Report/Filters";

const Report = (
  { auth: { user, loading }, target: { channels, usersList } },
  props
) => {
  const dispatch = useDispatch();

  const userData = props.userData;
  let today = new Date();
  let currentMonth = ("0" + (today.getMonth() + 1)).slice(-2);

  const [filterDate, setFilterDate] = useState(
    `${today.getFullYear()}-${currentMonth}`
  );
  const [filterCompany, setFilterCompany] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterUser, setFilterUser] = useState();
  const [filterUserType, setFilterUserType] = useState();

  const [target, setTarget] = useState(null);
  const [settlement, setSettlement] = useState(null);
  const [interiorDesign, setInteriorDesigns] = useState(null);
  const [followUp, setFollowUp] = useState(null);
  const [activitiesReport, setActivitiesReport] = useState(0);

  const startDate = moment(filterDate).startOf("month").format("YYYY-MM-DD");
  const endDate = moment(filterDate).endOf("month").format("YYYY-MM-DD");

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    dispatch(loadChannels(filterCompany));

    let reportable_type = "";
    if (filterCompany && !filterChannels && !filterUser) {
      reportable_type = `&filter[reportable_type]=COMPANY&filter[reportable_ids]=${filterCompany}`;
    } else if (
      (filterCompany && filterChannels && !filterUser) ||
      (!filterCompany && filterChannels && !filterUser)
    ) {
      reportable_type = `&filter[reportable_type]=CHANNEL&filter[reportable_ids]=${filterChannels}`;
    } else if (
      (filterCompany && filterChannels && filterUser) ||
      (!filterCompany && filterChannels && filterUser) ||
      (!filterCompany && !filterChannels && filterUser)
    ) {
      reportable_type = `&filter[reportable_type]=USER&filter[reportable_ids]=${filterUser}`;
    } else {
      reportable_type = "";
    }

    const getTarget = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${
            startDate && startDate
          }&filter[end_before]=${endDate && endDate}${
            reportable_type
              ? reportable_type
              : "&filter[reportable_type]=COMPANY"
          }&filter[is_dashboard]=1&filter[company_id]=${user.data.company.id}`,
          config
        );
        // console.log(config);
        setTarget(res.data);
        // dispatch({
        //   type: TARGET_LOADED,
        //   payload: res.data,
        // });
        // console.log(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    getTarget();

    const getSettlement = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ORDER_SETTLEMENT_COUNT&filter[start_after]=${
            startDate && startDate
          }&filter[end_before]=${endDate && endDate}${
            reportable_type
              ? reportable_type
              : "&filter[reportable_type]=COMPANY"
          }&filter[is_dashboard]=1&filter[company_id]=${user.data.company.id}`,
          config
        );
        // console.log(config);
        console.log(res.data);
        setSettlement(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    getSettlement();

    const getInteriorDesign = async () => {
      let reportable_type = "";

      if (filterCompany && !filterChannels) {
        reportable_type = `company_id=${filterCompany}`;
      } else if (filterCompany && filterChannels) {
        reportable_type = `channel_id=${filterChannels}`;
      } else {
        reportable_type = "";
      }

      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/interior-designs?${reportable_type}&start_at=${startDate}&end_at=${endDate}`,
          config
        );
        setInteriorDesigns(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    getInteriorDesign();

    const getFollowUp = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=ACTIVITY_COUNT&filter[start_after]=${
            startDate && startDate
          }&filter[end_before]=${endDate && endDate}${
            reportable_type
              ? reportable_type
              : "&filter[reportable_type]=COMPANY"
          }&filter[is_dashboard]=1&filter[company_id]=${user.data.company.id}`,
          config
        );
        setFollowUp(res.data.data[0]);
      } catch (error) {
        console.log(error);
      }
    };
    getFollowUp();

    if (userData) {
      dispatch(
        loadUsersList(
          filterChannels,
          userData.data.supervisor_id,
          userData.data.company.id
        )
      );
    } else {
      dispatch(loadUsersList());
    }

    const getSalesRevenueLink = () => {
      if (user.data.as === "Director") {
        setFilterUserType(
          `/dashboard/sales_revenue_bum/${
            user ? user.data.company.id : ""
          }/${filterDate}`
        );
      } else if (user.data.as === "BUM") {
        setFilterUserType(
          `/dashboard/sales_revenue_store_leader/${
            user ? user.data.company.id : ""
          }/${user.data.id}/${filterDate}`
        );
      } else if (user.data.as === "Store Leader") {
        setFilterUserType(
          `/dashboard/sales_revenue_sales/${user ? user.data.company.id : ""}/${
            user.data.id
          }/${filterDate}`
        );
      } else if (user.data.as === "Sales") {
        setFilterUserType(
          `/dashboard/sales_revenue/${user.data.id}/${user.data.channel_id}/${user.data.company_id}/${filterDate}`
        );
      }
    };

    getSalesRevenueLink();

    const getActivitiescReport = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities/report?company_id=${
            user ? user.data.company.id : ""
          }&channel_id=${filterChannels && filterChannels}&user_id=${
            filterUser && filterUser
          }&start_at=${
            filterDate &&
            moment(filterDate).startOf("month").format("YYYY-MM-DD")
          }&end_at=${
            filterDate && moment(filterDate).endOf("month").format("YYYY-MM-DD")
          }`,
          config
        );
        console.log(res.data);
        setActivitiesReport(res.data);
      } catch (error) {
        console.error(error);
      }
    };
    getActivitiescReport();
  }, [
    userData,
    startDate,
    endDate,
    filterCompany,
    filterChannels,
    filterUser,
    filterDate,
    user,
    dispatch,
  ]);

  const [brand, setBrand] = useState();
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getBrand = async () => {
      const dateFilter = `?end_at=${endDate}&start_at=${startDate}`;
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/brand-categories${dateFilter}`,
          config
        );
        setBrand(res.data);
      } catch (error) {
        console.error(error);
      }
    };
    getBrand();
  }, [endDate, startDate]);

  const pickColor = (value) => {
    if (value === "HOT") {
      return "#E53935";
    } else if (value === "WARM") {
      return "#FFD13D";
    } else if (value === "COLD") {
      return "#0553B7";
    } else if (value === "CLOSE") {
      return "#313132";
    }
  };

  return !loading &&
    user &&
    target &&
    settlement &&
    interiorDesign &&
    channels &&
    usersList ? (
    <div className="report" id="report">
      <Filters
        setFilterCompany={setFilterCompany}
        setFilterChannels={setFilterChannels}
        setFilterUser={setFilterUser}
        setFilterDate={setFilterDate}
        filterCompany={filterCompany}
        channels={channels}
        usersList={usersList}
      />

      <div className="report-cards row">
        <ReportCard
          data={target.data}
          title="Sales Revenue"
          date=""
          link={filterUserType}
        />

        <ReportCard
          data={settlement.data}
          title="Settlement"
          date=""
          link={`/dashboard/settlement/${
            user ? user.data.company.id : ""
          }/${filterDate}`}
        />

        <div className="report-card col-md-3">
          <Link
            to={`/dashboard/interior_design/${user.data.company_id}/${filterDate}`}
            style={{ textDecoration: "none" }}
          >
            <div className="report-card-wrapper">
              <p>Interior Design</p>
              <h4>{interiorDesign.total}</h4>
              <p></p>
            </div>
          </Link>
        </div>

        <div className="report-card col-md-3">
          <Link
            to={`/dashboard/interior_design/${user.data.company_id}/${filterDate}`}
            style={{ textDecoration: "none" }}
          >
            <div className="report-card-wrapper">
              <p>Activity</p>
              <h4>{activitiesReport}</h4>
              <p></p>
            </div>
          </Link>
        </div>

        {/* <div className="report-card col-md-4">
          <Link to={`/dashboard/report`} style={{ textDecoration: "none" }}>
            <div className="report-card-wrapper">
              <p>Report Leads</p>
              <h4>0</h4>
              <p></p>
            </div>
          </Link>
        </div> */}
      </div>

      <div>
        <div className="row ">
          <div className="col-md-6">
            <Bar
              brand={brand}
              company_id={user.data.company_id}
              month={filterDate}
            />
          </div>
          <div className="col-md-6">
            <div className="follow-up">
              <div className="skills-content p-3" data-aos="fade-up">
                <h3 className="mb-3">Total Follow Up</h3>

                {followUp === undefined || followUp === null
                  ? ""
                  : followUp.breakdown.map((breakdown) => {
                      return (
                        <Link
                          to={`/dashboard/activities/${breakdown.enum_value}/${
                            user ? user.data.company_id : "1"
                          }/${filterDate}`}
                          style={{ textDecoration: "none", color: "#000" }}
                        >
                          <div>
                            <Skill
                              key={breakdown.enum_value}
                              title={breakdown.enum_value}
                              progress={
                                (breakdown.value / followUp.value.value) * 100
                              }
                              color={pickColor(breakdown.enum_value)}
                              value={breakdown.value}
                            />
                          </div>
                        </Link>
                      );
                    })}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* <ReportExports
        filterCompany={filterCompany}
        filterChannels={filterChannels}
        filterUser={filterUser}
        filterDate={filterDate}
      /> */}
    </div>
  ) : (
    <LoadedComponent />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
});

export default connect(mapStateToProps)(Report);
