import axios from "axios";
import moment from "moment";
import React, { Fragment, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { isValidate } from "../../lib/Validate";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import LoadedComponent from "../Loaded/LoadedComponent";
import NoData from "../Loaded/NoData";
// import Button from "../Button/Button";
import "./ReportBum.scss";

const PipeLine = () => {
  const [reportData, setReportData] = useState(null);
  // const [reportDataClosed, setReportDataClosed] = useState(null);
  // const [reportDataHot, setReportDataHot] = useState(null);
  // filter
  const [filterCompany, setFilterCompany] = useState();
  const [filterChannels, setFilterChannels] = useState();
  const [filterDate, setFilterDate] = useState();
  const [filterStartDate, setFilterStartDate] = useState();
  const [filterEndDate, setFilterEndDate] = useState();
  const [filterSupervisior, setFilterSupervisor] = useState();

  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    setIsLoading(true);
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getReportBrands = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-leads?start_date=${
            filterStartDate
              ? moment(filterStartDate).format("YYYY-MM-DD")
              : filterDate
              ? moment(filterDate).startOf("month").format("YYYY-MM-DD")
              : ""
          }&end_date=${
            filterEndDate
              ? moment(filterEndDate).format("YYYY-MM-DD")
              : filterDate
              ? moment(filterDate).endOf("month").format("YYYY-MM-DD")
              : ""
          }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
            filterChannels ? filterChannels : ""
          }&supervisor_id=${filterSupervisior ? filterSupervisior : ""}`,
          config
        );
        console.log(res.data);
        setReportData(res.data);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
      }
    };

    getReportBrands();
  }, [
    filterDate,
    filterCompany,
    filterChannels,
    filterSupervisior,
    filterStartDate,
    filterEndDate,
  ]);

  const exportReportLeads = async () => {
    axios
      .get(
        `${mainUrl}/dashboard/report-leads?start_date=${
          filterStartDate
            ? moment(filterStartDate).format("YYYY-MM-DD")
            : filterDate
            ? moment(filterDate).startOf("month").format("YYYY-MM-DD")
            : ""
        }&end_date=${
          filterEndDate
            ? moment(filterEndDate).format("YYYY-MM-DD")
            : filterDate
            ? moment(filterDate).endOf("month").format("YYYY-MM-DD")
            : ""
        }&company_id=${filterCompany ? filterCompany : ""}&channel_id=${
          filterChannels ? filterChannels : ""
        }&supervisor_id=${
          filterSupervisior ? filterSupervisior : ""
        }&is_export=1`,
        {
          method: "GET",
          responseType: "blob", // important
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      )
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `ReportLeads ${filterDate}.xlsx`);
        document.body.appendChild(link);
        link.click();
      })
      .catch((err) => {
        console.log();
      });
  };
  return (
    <div>
      <Header icon="" title={`Report Leads`} back={true} user={false} />

      <FilterLeadsReport
        filterCompany={filterCompany}
        setFilterCompany={setFilterCompany}
        setFilterSupervisor={setFilterSupervisor}
        setFilterChannels={setFilterChannels}
        setFilterDate={setFilterDate}
        download={exportReportLeads}
        setFilterStartDate={setFilterStartDate}
        setFilterEndDate={setFilterEndDate}
        company={true}
        channel={true}
        supervisor={true}
      />
      {isLoading ? (
        <LoadedComponent />
      ) : !reportData || reportData.length === 0 ? (
        <NoData />
      ) : (
        <div style={{ overflow: "scroll" }}>
          <table className="table-report" style={{ margin: "10px 0" }}>
            <thead style={{ textAlign: "center" }}>
              <tr>
                <th></th>
                <th colSpan="12">Total Leads</th>
              </tr>
              <tr>
                <th>Name</th>
                <th className="leadshead">Leads</th>
                <th colSpan="4">Status</th>
                <th colSpan="3">Hot</th>
                <th colSpan="4">Closing Deals</th>
              </tr>
              <tr>
                <th></th>
                <th className="leadshead"></th>
                <th>Drop</th>
                <th>Cold</th>
                <th>Warm</th>
                <th>ESTIMATED</th>
                <th>No of LEADS</th>

                <th>QUOTATION</th>
                <th>Action</th>
                <th>No of LEADS</th>
                <th>INVOICE PRICE</th>
                <th>AMOUNT PAID</th>
                <th>Action</th>
              </tr>
            </thead>
            {reportData.data.map((data, idx) => {
              return (
                <tbody key={idx}>
                  <tr style={{ background: "rgba(137, 189, 255, 0.7)" }}>
                    <td>{data.name ? data.name : ""}</td>
                    <td>
                      <Link
                        to={`/dashboard/report/detail/bum/${
                          data.supervisor_id
                        }/cold/${filterStartDate ? filterStartDate : "now"}/${
                          filterEndDate ? filterEndDate : "now"
                        }`}
                      >
                        {data.total_leads}
                      </Link>
                    </td>
                    <td>{data.drop_leads ? data.drop_leads : 0}</td>
                    <td>
                      <Link
                        to={`/dashboard/report/bum/${data.supervisor_id}/cold/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}/status`}
                      >
                        {data.cold}
                      </Link>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/report/bum/${data.supervisor_id}/warm/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}/status`}
                      >
                        {data.warm}
                      </Link>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/sales_estimation/bum/${
                          data.supervisor_id
                        }/${filterStartDate ? filterStartDate : "now"}/${
                          filterEndDate ? filterEndDate : "now"
                        }`}
                      >
                        {!isValidate(data.estimated_value)
                          ? formatCurrency(data.estimated_value)
                          : "0"}
                      </Link>
                    </td>

                    <td>
                      <Link
                        to={`/dashboard/report/bum/${data.supervisor_id}/hot/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}/status`}
                      >
                        {data.hot}
                      </Link>
                    </td>

                    <td>
                      {data.quotation ? formatCurrency(data.quotation) : "0"}
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/report/bum/${data.supervisor_id}/hot/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}`}
                      >
                        See Detail
                      </Link>
                    </td>
                    <td>{data.total_lead_deals}</td>
                    <td>
                      {data.invoice_price
                        ? formatCurrency(data.invoice_price)
                        : "0"}
                    </td>
                    <td>
                      {data.amount_paid
                        ? formatCurrency(data.amount_paid)
                        : "0"}
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/report/bum/${data.supervisor_id}/deal/${
                          filterStartDate ? filterStartDate : "now"
                        }/${filterEndDate ? filterEndDate : "now"}`}
                      >
                        See Detail
                      </Link>
                    </td>
                  </tr>
                  {data.channels &&
                    data.channels.map((data, idx) => {
                      return (
                        <Fragment key={idx}>
                          <tr
                            style={{ background: "rgba(137, 189, 255, 0.3)" }}
                          >
                            <td>{data.name}</td>
                            <td>
                              <Link
                                to={`/dashboard/report/detail/channel/${
                                  data.channel_id
                                }/cold/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${filterEndDate ? filterEndDate : "now"}`}
                              >
                                {data.total_leads}
                              </Link>
                            </td>
                            <td>{data.drop_leads ? data.drop_leads : 0}</td>
                            <td>
                              <Link
                                to={`/dashboard/report/channel/${
                                  data.channel_id
                                }/cold/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${
                                  filterEndDate ? filterEndDate : "now"
                                }/status`}
                              >
                                {data.cold}
                              </Link>
                            </td>
                            <td>
                              <Link
                                to={`/dashboard/report/channel/${
                                  data.channel_id
                                }/warm/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${
                                  filterEndDate ? filterEndDate : "now"
                                }/status`}
                              >
                                {data.warm}
                              </Link>
                            </td>
                            <td>
                              <Link
                                to={`/dashboard/sales_estimation/bum/${
                                  data.supervisor_id
                                }/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${filterEndDate ? filterEndDate : "now"}`}
                              >
                                {!isValidate(data.estimated_value)
                                  ? formatCurrency(data.estimated_value)
                                  : "0"}
                              </Link>
                            </td>
                            <td>
                              <Link
                                to={`/dashboard/report/channel/${
                                  data.channel_id
                                }/hot/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${
                                  filterEndDate ? filterEndDate : "now"
                                }/status`}
                              >
                                {data.hot}
                              </Link>
                            </td>

                            <td>
                              {data.quotation
                                ? formatCurrency(data.quotation)
                                : "0"}
                            </td>
                            <td>
                              <Link
                                to={`/dashboard/report/channel/${
                                  data.channel_id
                                }/hot/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${filterEndDate ? filterEndDate : "now"}`}
                              >
                                See Detail
                              </Link>
                            </td>
                            <td>{data.total_lead_deals}</td>
                            <td>
                              {data.invoice_price
                                ? formatCurrency(data.invoice_price)
                                : "0"}
                            </td>
                            <td>
                              {data.amount_paid
                                ? formatCurrency(data.amount_paid)
                                : "0"}
                            </td>
                            <td>
                              <Link
                                to={`/dashboard/report/channel/${
                                  data.channel_id
                                }/deal/${
                                  filterStartDate ? filterStartDate : "now"
                                }/${filterEndDate ? filterEndDate : "now"}`}
                              >
                                See Detail
                              </Link>
                            </td>
                          </tr>
                          {data.sales &&
                            data.sales.map((data, idx) => {
                              return (
                                <tr style={{ background: "#fff" }} key={idx}>
                                  <td>{data.name}</td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/detail/sales/${
                                        data.user_id
                                      }/cold/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }`}
                                    >
                                      {data.total_leads}
                                    </Link>
                                  </td>
                                  <td>
                                    {data.drop_leads ? data.drop_leads : 0}
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/sales/${
                                        data.user_id
                                      }/cold/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }/status`}
                                    >
                                      {data.cold}
                                    </Link>
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/sales/${
                                        data.user_id
                                      }/warm/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }/status`}
                                    >
                                      {data.warm}
                                    </Link>
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/sales_estimation/sales/${
                                        data.user_id
                                      }/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }`}
                                    >
                                      {!isValidate(data.estimated_value)
                                        ? formatCurrency(data.estimated_value)
                                        : "0"}
                                    </Link>
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/sales/${
                                        data.user_id
                                      }/hot/${
                                        filterDate ? filterDate : "now"
                                      }/status`}
                                    >
                                      {data.hot}
                                    </Link>
                                  </td>

                                  <td>
                                    {data.quotation
                                      ? formatCurrency(data.quotation)
                                      : "0"}
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/sales/${
                                        data.user_id
                                      }/hot/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }`}
                                    >
                                      See Detail
                                    </Link>
                                  </td>
                                  <td>{data.total_lead_deals}</td>
                                  <td>
                                    {data.invoice_price
                                      ? formatCurrency(data.invoice_price)
                                      : "0"}
                                  </td>
                                  <td>
                                    {data.amount_paid
                                      ? formatCurrency(data.amount_paid)
                                      : "0"}
                                  </td>
                                  <td>
                                    <Link
                                      to={`/dashboard/report/sales/${
                                        data.user_id
                                      }/deal/${
                                        filterStartDate
                                          ? filterStartDate
                                          : "now"
                                      }/${
                                        filterEndDate ? filterEndDate : "now"
                                      }`}
                                    >
                                      See Detail
                                    </Link>
                                  </td>
                                </tr>
                              );
                            })}
                        </Fragment>
                      );
                    })}
                </tbody>
              );
            })}
            <tfoot style={{ background: "yellow" }}>
              <tr>
                <td>Total</td>
                <td>{reportData.total.total_leads}</td>
                <td>{reportData.total.drop_leads}</td>
                <td>{reportData.total.cold}</td>
                <td>{reportData.total.warm}</td>
                <td>{formatCurrency(reportData.total.estimated_value)}</td>
                <td>{reportData.total.hot}</td>
                <td>{formatCurrency(reportData.total.quotation)}</td>
                <td>-</td>
                <td>{reportData.total.total_lead_deals}</td>
                <td>{formatCurrency(reportData.total.invoice_price)}</td>
                <td>{formatCurrency(reportData.total.amount_paid)}</td>
                <td>-</td>
              </tr>
            </tfoot>
          </table>
        </div>
      )}

      {/* {reportByLeads.last_page && (
        <div className="d-flex justify-content-between">
          <ReactPaginate
            previousLabel={"Prev"}
            nextLabel={"Next"}
            breakLabel={"..."}
            pageCount={reportByLeads.last_page}
            marginPagesDisplayed={3}
            // pageRangeDisplayed={3}
            onPageChange={handlePageClick}
            containerClassName={"pagination"}
            pageClassName={"page-item"}
            pageLinkClassName={"page-link"}
            previousClassName={"page-item"}
            previousLinkClassName={"page-link"}
            nextClassName={"page-item"}
            nextLinkClassName={"page-link"}
            breakClassName={"page-item"}
            breakLinkClassName={"page-link"}
            activeClassName={"active"}
          />
          <div
            onClick={() => exportReportsByLeads()}
            style={{ width: "fit-content" }}
          >
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div>
        </div>
      )} */}
    </div>
  );
};

export default PipeLine;
