import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Button from "../../../components/Button/Button";
import QtyForm from "../../../components/form/QtyForm";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import { temporaryCartLoad } from "../../../redux/action/product";

const ProductOption = () => {
  const dispatch = useDispatch();
  const { model_id } = useParams();
  const history = useHistory();
  const [qty, setQty] = useState(1);
  const [version, setVersion] = useState({});
  const [versions, setVersions] = useState(null);
  const [categories, setCategories] = useState(null);
  const [products, setProducts] = useState(null);
  const [colours, setColours] = useState(null);
  const [covers, setCovers] = useState(null);

  const [cover, setCover] = useState(null);
  const [colour, setColour] = useState(null);

  const [productUnit, setProductUnit] = useState(null);

  const [totalPrice, setTotalPrice] = useState(0);
  // console.log(qty);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getVersions = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/versions?filter[product_model_id]=${model_id}`,
          config
        );
        console.log(res.data.data);
        setVersions(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getCategory = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/category-codes?filter[product_model_id]=${model_id}&filter[product_version_id]=${version.id}`,
          config
        );
        console.log(res.data.data);
        setCategories(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getProduct = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/products?filter[product_model_id]=${model_id}&filter[product_version_id]=${
            version ? version.id : ""
          }`,
          config
        );
        console.log(res.data.data);
        setProducts(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getVersions();
    getProduct();
    if (version) {
      getCategory();
    }
  }, [model_id, version]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getColour = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/colours?filter[product_id]=${
            products ? products.length !== 0 && products[0].id : ""
          }`,
          config
        );
        console.log(res.data.data);
        setColours(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    const getCovers = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/coverings?filter[product_id]=${
            products ? products.length !== 0 && products[0].id : ""
          }`,
          config
        );
        console.log(res.data.data);
        setCovers(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getProductUnit = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/product-units?filter[product_id]=${
            products ? products.length !== 0 && products[0].id : ""
          }&filter[colour_id]=${colour}&filter[covering_id]=${cover}`,
          config
        );
        console.log(res.data.data);
        setProductUnit(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getColour();
    getCovers();
    console.log(colour);
    console.log(cover);
    if (colour && cover) {
      getProductUnit();
    }
  }, [products, colour, cover]);

  useEffect(() => {
    if (productUnit) {
      console.log(productUnit);
      setTotalPrice(
        productUnit.length !== 0 ? productUnit[0].price * qty : "0"
      );

      // const data = {
      //   id: productUnit[0].id,
      //   qty: qty,
      // };
      // console.log(data);
    }
  }, [qty, productUnit]);

  const handleClick = () => {
    if (productUnit) {
      const data = {
        id: productUnit[0].id,
        quantity: qty,
      };
      console.log(data);

      dispatch(temporaryCartLoad(data));
    }
    history.go(-1);
  };

  // const handleChange = (e) => {
  //   if (productUnit) {

  //   }
  // };

  // useEffect(() => {
  //   const config = {
  //     headers: {
  //       "Content-Type": "application/json",
  //       Authorization: `Bearer ${localStorage.token}`,
  //     },
  //   };

  //   const getCategory = async () => {
  //     try {
  //       const res = await axios.get(
  //         `${mainUrl}/category-codes?filter[product_model_id]=${model_id}&filter[product_version_id]=${version.id}`,
  //         config
  //       );
  //       console.log(res.data.data);
  //       setCategories(res.data.data);
  //     } catch (error) {
  //       console.error(error);
  //     }
  //   };

  //   getCategory();
  // }, [version, model_id]);

  return versions ? (
    <div className="product-option" id="product-option">
      <Header title={"Product Options"} back={true} />
      <div className="version">
        <h4>Select Option</h4>
        <div className="row">
          {versions.map((v) => {
            return (
              <div className="col-md-3" key={v.id}>
                <button
                  className={`btn ${version.name === v.name && "active"}`}
                  style={{ width: "100%", padding: "0" }}
                  onClick={() => setVersion(v)}
                >
                  <img
                    src={v.images[0].url}
                    alt="version"
                    style={{ width: "100%" }}
                  />
                  <p>{v.name}</p>
                </button>
              </div>
            );
          })}
        </div>
      </div>

      {/* <div className="category">
          <h4>Select Category</h4>
          <div className="row">
            <div className="col-md-3">
              <img
                src="/Asset/New 1.jpg"
                alt="version"
                style={{ width: "100%" }}
              />
              <div className="d-flex justify-content-between">
                <p>Category 1</p>
                <QtyForm qty={qty} setQty={setQty} />
              </div>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/New 1.jpg"
                alt="version"
                style={{ width: "100%" }}
              />
              <div className="d-flex justify-content-between">
                <p>Category 1</p>
                <QtyForm qty={qty} setQty={setQty} />
              </div>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/New 1.jpg"
                alt="version"
                style={{ width: "100%" }}
              />
              <div className="d-flex justify-content-between">
                <p>Category 1</p>
                <QtyForm qty={qty} setQty={setQty} />
              </div>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/New 1.jpg"
                alt="version"
                style={{ width: "100%" }}
              />
              <div className="d-flex justify-content-between">
                <p>Category 1</p>
                <QtyForm qty={qty} setQty={setQty} />
              </div>
            </div>
          </div>
        </div> */}
      <div className="category">
        <label>Select Category :</label>

        <select
          className="form-select"
          name="color"

          // value={filterCompany}
          // onChange={(e) => setFilterCompany(e.target.value)}
        >
          <option value="">Select Category Here</option>
          {categories &&
            categories.map((category) => {
              return (
                <option value={category.id} key={category.id}>
                  {category.name}
                </option>
              );
            })}
        </select>
      </div>

      <div className="covering">
        <label>Select Covering :</label>

        <select
          className="form-select"
          name="covering"
          // value={filterCompany}
          onChange={(e) => setCover(parseInt(e.target.value))}
        >
          <option value="">Search Covering</option>
          {covers &&
            covers.map((cover) => {
              return (
                <option value={cover.id} key={cover.id}>
                  {cover.name}
                </option>
              );
            })}
        </select>
      </div>

      <div className="color">
        <label>Select Color :</label>

        <select
          className="form-select"
          name="color"
          // value={filterCompany}
          onChange={(e) => setColour(parseInt(e.target.value))}
        >
          <option value="">Search Color Here</option>
          {colours &&
            colours.map((colour) => {
              return (
                <option value={colour.id} key={colour.id}>
                  {colour.name}
                </option>
              );
            })}
        </select>
      </div>

      <div className="d-flex justify-content-between">
        <div>
          <h4 style={{ marginBottom: "5px" }}>Total</h4>
          <h5>
            <b>Rp {totalPrice ? totalPrice : "0"}</b>
          </h5>
        </div>
        <div className="qty-form d-flex align-items-center text-center">
          <button className="decrease" onClick={() => setQty(qty - 1)}>
            -
          </button>
          {/* <input
              value={qty}
              className="qty-input"
              type="number"
              // onChange={(e) => handleChange(e)}
            /> */}
          <div style={{ width: "10px" }}>{qty}</div>
          <button className="increase" onClick={() => setQty(qty + 1)}>
            +
          </button>
        </div>
      </div>

      <div onClick={() => handleClick()}>
        <Button title="Apply" btnStyle="primary-btn" btnWidth="100%" icon="" />
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default ProductOption;
