import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../../api/api";
import { formatCurrency } from "../../../../api/format";
import Button from "../../../../components/Button/Button";
import Header from "../../../../components/Header/Header";
import Loaded from "../../../../components/Loaded/Loaded";
import "./Payment.scss";

const Payment = () => {
  const { orderId } = useParams();
  const [orderData, setOrderData] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    console.log(orderId);
    // setLoadOrder(false);
    const getOrderData = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/orders?filter[id]=${orderId}`,
          config
        );
        console.log(res.data.data[0]);
        setOrderData(res.data.data[0]);
        // setLoadOrder(true);
      } catch (error) {
        console.error(error);
      }
    };
    getOrderData();
  }, [orderId]);
  return orderData ? (
    <div className="payment" id="payment">
      <Header back="true" title="Payment" icon="" />
      {/* {alert && (
        <div class="alert alert-warning" role="alert">
          {alert}
        </div>
      )} */}
      <div className="row">
        <div className="col-md-8">
          <div className="billing">
            <div className="d-flex justify-content-between">
              <h5>Shipping Address</h5>
            </div>
            <div className="address">
              <p>{orderData.shipping_address.address_line_1}</p>
            </div>
          </div>

          <hr />

          <div className="shipping">
            <div className="d-flex justify-content-between">
              <h5>Billing Address</h5>
            </div>
            <div className="address">
              <p>{orderData.billing_address.address_line_1}</p>
            </div>
          </div>

          <hr />

          {/* <div className="interior-design">
            <div className="d-flex justify-content-between">
              <h5>Interior Design</h5>
            </div>
            <div className="mt-2">
              <select
                className="form-select"
                name="companys"
                // style={{ marginLeft: "10px", height: "41px" }}
                // value={filterCompany}
                // onChange={(e) => setFilterCompany(e.target.value)}
              >
                <option value="">Select Company</option>
                {interiorDesigns &&
                  interiorDesigns.map((interiorDesign) => {
                    return (
                      <option value={interiorDesign.id}>
                        {interiorDesign.name}
                      </option>
                    );
                  })}
              </select>
            </div>
          </div> */}

          {/* <hr /> */}

          <div className="product-card">
            <div className="d-flex justify-content-between">
              <h5>Product</h5>
            </div>
            {orderData.order_details &&
              orderData.order_details.map((item, i) => {
                return (
                  <div
                    className="d-flex justify-content-between mt-3 mb-3"
                    key={item.id}
                  >
                    <div className="d-flex">
                      <img
                        src={
                          item.images[0]
                            ? item.images[0].url
                            : "/Asset/New 5.jpg"
                        }
                        alt="product_img"
                      />
                      <div className="desc">
                        <h5>{item.product_unit.name}</h5>
                        <ul>
                          <li>{item.covering.name}</li>
                          <li>{item.colour.name}</li>
                        </ul>
                        <h5>{formatCurrency(item.total_price)}</h5>
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>

          {/* <div>
            <div
              className="pointer"
              onClick={() => setSelectStockPopup(!selectStockPopup)}
            >
              <h5>+ Tambah Product</h5>
            </div>
          </div> */}

          {/* <div className="row mt-3">
            <div
              className="col-6 pointer"
              onClick={() => setDiscountPopup(!discountPopup)}
            >
              <div className="discount d-flex justify-content-between align-items-center">
                <div>
                  <div className="d-flex align-items-center">
                    <img src="/Asset/Discount.svg" alt="discount" />
                    <h5 style={{ margin: "0 5px" }}>
                      {discount ? discount.name : "Discount"}
                    </h5>
                  </div>

                  {console.log(discount)}
                  {discount && (
                    <div className="mt-2">
                      <p>{discount.description}</p>
                    </div>
                  )}
                </div>

                <img src="/Asset/Arrow Icon/RightArrow.png" alt="rightarrow" />
              </div>
            </div>
            <div
              className="col-6 pointer"
              onClick={() => setAdditionalPopup(!additionalPopup)}
            >
              <div className="discount d-flex justify-content-between align-items-center">
                <div>
                  <div className="d-flex align-items-center">
                    <img src="/Asset/Delivery.svg" alt="delivery" />
                    <h5 style={{ margin: "0 5px" }}>Addtional Fee</h5>
                  </div>

                  <div className="mt-2">
                    <p>{packingFee && `Packing fee: ${packingFee}`}</p>
                    <p>{shippingFee && `Shipping fee: ${shippingFee}`}</p>
                    <p>
                      {additionalDiscount &&
                        `Additional Discount: ${additionalDiscount}`}
                    </p>
                  </div>
                </div>

                <img src="/Asset/Arrow Icon/RightArrow.png" alt="rightarrow" />
              </div>
            </div>
          </div> */}

          <hr />

          {/* <div className="delivery-date mt-3">
            <h5>Expected Delivery Date</h5>

            <div className="mt-2">
              <input
                className="form-control"
                type="date"
                placeholder="Default input"
                aria-label="default input example"
                onChange={(e) => setExpectedShippingDatetime(e.target.value)}
              />
            </div>
          </div> */}

          {/* <div className="delivery-date mt-3">
            <h5>Valid Quotation Date</h5>

            <div className="mt-2">
              <input
                className="form-control"
                type="date"
                placeholder="Default input"
                aria-label="default input example"
                onChange={(e) => setQuotationValidUntilDatetime(e.target.value)}
              />
            </div>
          </div> */}

          <div className="notes mt-3">
            <h5>Notes</h5>

            <div className="mt-2 note">
              <p>{orderData.note ? orderData.note : "-- Empty --"}</p>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="billing-detail">
            <h5>Billing Detail</h5>

            <div className="d-flex justify-content-between">
              <p>Price</p>
              <p>{formatCurrency(orderData.original_price)}</p>
            </div>
            {/* {tempOrder && (
              <div className="d-flex justify-content-between">
                <p>Product Non Existing</p>
                <p>Rp 0</p>
              </div>
            )} */}
            {orderData && (
              <div className="d-flex justify-content-between">
                <p>Discount</p>
                <p>- {formatCurrency(orderData.total_discount)}</p>
              </div>
            )}

            {orderData && (
              <div className="d-flex justify-content-between">
                <p>Packing</p>
                <p>{formatCurrency(orderData.packing_fee)}</p>
              </div>
            )}

            {orderData && (
              <div className="d-flex justify-content-between">
                <p>Shipping</p>
                <p>{formatCurrency(orderData.shipping_fee)}</p>
              </div>
            )}

            {orderData && (
              <div className="d-flex justify-content-between">
                <p>Additional Discount</p>
                <p>- {formatCurrency(orderData.additional_discount)}</p>
              </div>
            )}

            <hr />
            {/* <div className="d-flex justify-content-between">
              <p>
                <b>Total</b>
              </p>
              <p>
                <b>Rp 0</b>
              </p>
            </div>
            <div className="d-flex justify-content-between">
              <p>50% discount all product</p>
              <p>- Rp 0</p>
            </div>
            <hr /> */}

            <div className="d-flex justify-content-between">
              <p>
                <b>Sub Total</b>
              </p>
              <p>
                <b>
                  {orderData.total_price
                    ? formatCurrency(orderData.total_price)
                    : formatCurrency(orderData.original_price)}
                </b>
              </p>
            </div>

            <div className="btn-payment">
              <Link to={`/dashboard/payment/${orderId}/payment_categories`}>
                <Button
                  title="Proceed to payment"
                  btnStyle="primary-btn"
                  icon=""
                  btnWidth="100%"
                />
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default Payment;
