import React, { useRef, useState } from "react";
import { Redirect } from "react-router-dom";

import Button from "../../components/Button/Button";
import { connect, useDispatch, useSelector } from "react-redux";
import "./Login.css";
import { login } from "../../redux/action/auth";
import { loadTarget } from "../../redux/action/target";
import "./Login.css";

const LoginV2 = ({ auth: { failedLogin } }) => {
  const dispatch = useDispatch();
  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);

  const emailRef = useRef();
  const passRef = useRef();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const data = {
      email: emailRef.current.value,
      password: passRef.current.value,
    };
    dispatch(login(data));
    dispatch(loadTarget());
    // setEmail("");
    // setPassword("");
  };
  return (
    <section className="gradient-form" style={{ backgroundColor: "#fff" }}>
      {isAuthenticated ? <Redirect to="/dashboard" /> : ""}
      <div className="">
        <div className="row g-0" style={{ height: "100vh" }}>
          <div className="col-lg-7 d-flex align-items-center gradient-custom-2 justify-content-center">
            <div
              className="text-white px-3 py-4 p-md-1 mx-md-4"
              style={{ width: "70%" }}
            >
              <img
                src="/Asset/Auth Images/login image 3.png"
                alt="img bg"
                style={{ width: "100%" }}
              />
            </div>
          </div>
          <div
            className="col-lg-5 login-form d-flex justify-content-center align-items-center"
            style={{
              background: `url('/Asset/Auth Images/background auth.png'), linear-gradient(#17949d, #21b8c3)`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
          >
            <div className="card-body p-md-5 mx-md-4 ">
              <div>
                <div className="text-center">
                  <img
                    src="/Asset/Auth Images/MovesLogo.png"
                    alt="logo"
                    className="mt-1 mb-5 pb-1"
                    style={{ width: "300px" }}
                  />
                </div>

                <form onSubmit={handleSubmit}>
                  <p>Please login to your account</p>
                  {failedLogin && (
                    <div
                      className="alert alert-danger"
                      role="alert"
                      style={{ textAlign: "center" }}
                    >
                      Your email or password is incorrect. <br />
                      Please try again.
                    </div>
                  )}
                  <div className="form-outline mb-4">
                    <input
                      type="email"
                      id="email"
                      className="form-control"
                      name="email"
                      // value={email}
                      placeholder="Enter your email"
                      ref={emailRef}
                    />
                  </div>

                  <div className="form-outline mb-4">
                    <input
                      type="password"
                      id="password"
                      name="password"
                      className="form-control"
                      // value={password}
                      placeholder="Enter your password"
                      // onChange={(e) => setPassword(e.target.value)}
                      ref={passRef}
                    />
                  </div>

                  <div className="text-center pt-1 mb-5 pb-1">
                    <button
                      className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3 form-control"
                      type="submit"
                      style={{ backgroundColor: "#006870" }}
                    >
                      Log in
                    </button>
                  </div>
                  {/* <div className="d-flex align-items-center justify-content-center pb-4">
                    <p className="mb-0 me-2">Don't have an account?</p>
                    <button type="button" className="btn btn-outline-danger">
                      Create new
                    </button>
                  </div> */}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(LoginV2);
