import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "./Header.scss";

const Header = (props) => {
  const dataUser = props.data;
  const history = useHistory();
  const [notifActive, setNotifActive] = useState(false);
  return (
    <div className="header d-flex justify-content-between align-items-center">
      <div className="header-text">
        <h1>
          {props.back ? (
            <span
              style={{ cursor: "pointer", marginRight: "16px" }}
              onClick={() => history.go(-1)}
            >
              <img src="/Asset/Back.png" alt="back" />
            </span>
          ) : (
            ""
          )}
          {props.title}
        </h1>
      </div>
      <div className="d-flex align-items-center">
        {props.notif ? (
          <div className="header-notification">
            <img
              src="/Asset/Notif Icon/Notif Message off.png"
              alt="notif"
              style={{ width: "26px", height: "30px", cursor: "pointer" }}
              onClick={() => setNotifActive(!notifActive)}
            />
          </div>
        ) : (
          ""
        )}
        {props.user ? (
          <div className="header-user d-flex align-items-center">
            <div className="user-img d-flex justify-content-center align-items-center">
              <div>
                <h2>{dataUser.initial}</h2>
              </div>
            </div>
            <div className="user-desc">
              <h2>{dataUser.name ? dataUser.name : ""}</h2>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
      <div className={`notification-card ${notifActive ? "notif-active" : ""}`}>
        <h4>Recent Activity</h4>
        <ul>
          <li>
            <p>8 Desember | 14:00 PM</p>
            <p>
              <b>User 1</b> follow up customer one
            </p>
          </li>
          <li>
            <p>8 Desember | 14:00 PM</p>
            <p>
              <b>User 1</b> follow up customer one
            </p>
          </li>
        </ul>
        <Link to={"/dashboard/notification"}>See All</Link>
      </div>
    </div>
  );
};

export default Header;
