export const pickColor = (value) => {
  if (value > 70) {
    return "#2DCC70";
  } else if (value > 40 || value <= 70) {
    return "#FFD13D";
  } else if (value <= 40 || value >= 0 || isNaN(value) || value.isInFinite()) {
    return "#FF0000";
  }
};

export const pickColorStatus = (status) => {
  if (status === "GREEN") {
    return "#2DCC70";
  } else if (status === "YELLOW") {
    return "#FFD13D";
  } else if (status === "RED") {
    return "#FF0000";
  } else if (status === "EXPIRED") {
    return "#313132";
  } else {
    return "#0553B7";
  }
};

export const activityStatusColor = (status) => {
  if (status === "HOT") return "#E53935";
  else if (status === "WARM") return "#FFD13D";
  else if (status === "COLD") return "#0553B7";
  else if (status === "DEAL") return "#313132";
};
