import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Activity from "../../../components/ActivityDetail/Activity";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";

const Activities = () => {
  const { status, company_id, month } = useParams();
  const [activitiesData, setActivitiesData] = useState(null);

  const [activityDetail, setActivityDetail] = useState(false);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [orderId, setOrderId] = useState(null);
  const [orderData, setOrderData] = useState(null);
  //   const [commentsData, setCommentsData] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getActivities = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities?filter[company_id]=${company_id}&filter[status]=${status}&filter[follow_up_datetime_before]=${moment(
            month
          )
            .endOf("month")
            .format("YYYY-MM-DD")}&filter[follow_up_datetime_after]=${moment(
            month
          )
            .startOf("month")
            .format("YYYY-MM-DD")}`,
          config
        );
        console.log(res.data.data);
        setActivitiesData(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getActivities();
  }, [company_id, status, month]);

  const handleClickDetail = (activity) => {
    setActivityDetail(!activityDetail);
    setCustomerActivity(activity);
    setOrderId(activity.order ? activity.order.id : undefined);
    // return(

    // )
    console.log(activity);
  };

  return activitiesData ? (
    <div>
      <Header title={"Activities"} back={true} notif={true} />
      {/* {salesRevenue.length !== 0 && ( */}
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search customer name/inv number"
            //   onChange={(e) => setFilterSales(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            //   value={date}
            //   onChange={(e) => setDate(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>
      {/* )} */}

      {/* {salesRevenue.length === 0 ? (
        <NoData />
      ) : ( */}
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>Date</th>
              <th>Invoice Number</th>
              <th>Follow Up</th>
              <th>Store Location</th>
              <th>Payment</th>
              <th>Quotation</th>
              <th>Sales</th>
              <th>Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {activitiesData.map((activity, i) => {
              return (
                <tr key={i}>
                  <td>
                    {moment(activity.follow_up_datetime).format("MM/DD/YYYY")}
                  </td>
                  <td>{activity.order ? activity.order.invoice_number : ""}</td>
                  <td>{activity.follow_up_method}</td>
                  <td>{activity.channel.name}</td>
                  <td>{activity.order ? activity.order.payment_status : ""}</td>
                  <td>{activity.order ? activity.order.status : ""}</td>
                  <td>{activity.user.name}</td>
                  <td>{activity.status}</td>
                  <td>
                    <div
                      onClick={() => handleClickDetail(activity)}
                      className="activity-detail-btn"
                    >
                      <img
                        src="/Asset/Action Icon/eye.png"
                        style={{ marginRight: "5px" }}
                        alt="eye"
                      />
                      See Detail
                    </div>
                  </td>
                </tr>
              );
            })}
            {/* {salesRevenue
                .filter((data) => {
                  if (filterSales === "") {
                    return data;
                  } else if (
                    data.order.invoice_number
                      .toLowerCase()
                      .includes(filterSales.toLowerCase())
                  ) {
                    return data;
                  } else if (
                    `${data.customer.first_name} ${
                      data.customer.last_name ? data.customer.last_name : ""
                    }`
                      .toLowerCase()
                      .includes(filterSales.toLowerCase())
                  ) {
                    return data;
                  }
                })
                .map((data, i) => {
                  return (
                    <tr key={data.id}>
                      <td>{i + 1}.</td>
                      <td>{data.order.invoice_number}</td>
                      <td>{`${data.customer.first_name} ${
                        data.customer.last_name ? data.customer.last_name : ""
                      }`}</td>
                      <td>{formatCurrency(data.order.total_price)}</td>

                      <td>
                        <div
                          onClick={() => handleClickDetail(data)}
                          className="activity-detail-btn"
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </div>
                      </td>
                    </tr>
                  );
                })} */}
          </tbody>
        </table>
      </div>
      {/* )} */}
      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          // orderData={orderData}
          // user={user}
          //   comments={commentsData}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  ) : (
    <Loaded />
  );
};

export default Activities;
