import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";

const Stock = () => {
  const { channel_id } = useParams();
  const [stocks, setStocks] = useState(null);
  const [filter, setFilter] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getStock = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/stocks/extendedNew/${channel_id}`,
          config
        );
        console.log(res.data.data);
        setStocks(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getStock();
  }, [channel_id]);

  return (
    <div className="stock" id="stock">
      <Header title="Stock" back={true} icon="" />
      <div className="d-flex align-items-center">
        <input
          type="text"
          className="form-control"
          name="companys"
          style={{ height: "41px", width: "261px" }}
          placeholder="Search customer name"
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
        />
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No.</th>
              <th>Product Name</th>
              <th>Brand</th>
              <th>Ready Stock</th>
              <th>Indent</th>
              <th>Outstanding Order</th>
              <th>Outstanding Shipment</th>
              <th>Ready</th>
              <th>See Detail</th>
            </tr>
          </thead>
          <tbody>
            {stocks
              ? stocks.map((stock, idx) => {
                  return (
                    <tr>
                      <td>{idx + 1}</td>
                      <td>{stock.product_unit.name}</td>
                      <td>{stock.product_unit.product.brand.name}</td>
                      <td>{stock.stock}</td>
                      <td>{stock.indent}</td>
                      <td>{stock.outstanding_order}</td>
                      <td>{stock.outstanding_shipment}</td>
                      <td>{stock.real_stock}</td>
                      <td>
                        <Link
                          to={`/dashboard/product/stocks/stock_detail/${stock.company_id}/${stock.channel_id}/${stock.product_unit_id}`}
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "11px" }}
                            alt="eye"
                          />
                        </Link>
                        See Detail
                      </td>
                    </tr>
                  );
                })
              : ""}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Stock;
