import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { pickColor } from "../../../api/pickColor";
import Loaded from "../../../components/Loaded/Loaded";
// import Button from "../../../components/Button/Button";
import LoadedComponent from "../../../components/Loaded/LoadedComponent";
import { loadSalesRanking } from "../../../redux/action/target";

const TopPerformance = (
  { auth: { user, loading }, target: { salesRanking } },
  props
) => {
  const dispatch = useDispatch();
  const userData = user;
  // const filterRanking = props.filterRanking;
  let today = new Date();

  const startDate = moment(today).startOf("month").format("YYYY-MM-DD");
  const endDate = moment(today).endOf("month").format("YYYY-MM-DD");

  // const [filterRanking, setFilterRanking] = useState(1);

  const [topSales, setTopSales] = useState(null);
  const [topBum, setTopBum] = useState(null);
  const [topChannel, setTopChannel] = useState(null);
  //   console.log(user);
  useEffect(() => {
    if (userData) {
      dispatch(loadSalesRanking(1));
    }
  }, [userData, dispatch]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getTopPerformance = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/index-top-sales/target?start_at=${startDate}&end_at=${endDate}`,
          config
        );
        console.log(res.data);
        setTopSales(res.data.sales);
        setTopBum(res.data.bum);
        setTopChannel(res.data.channel);
      } catch (error) {
        console.error(error);
      }
    };
    getTopPerformance();
  }, [startDate, endDate]);

  return salesRanking ? (
    <div className="ranking">
      <div className="top-ranking mt-4 d-flex justify-content-between">
        <div className="d-flex align-items-center">
          <img
            src="/Asset/Icon/Top Sales.png"
            alt="report"
            style={{ width: "22px", height: "22px", marginRight: "13px" }}
          />
          <h3
            style={{
              fontStyle: "normal",
              fontWeight: "700",
              fontSize: "16px",
              lineHeight: "24px",
              margin: "0",
              color: "#313132",
            }}
          >
            Top Performance
          </h3>
        </div>
        {/* <div className="filter-rank">
          <span onClick={() => setFilterRanking(1)} className="me-2">
            <Button title="Value" icon="" btnStyle="primary-btn" />
          </span>
          <span onClick={() => setFilterRanking(2)}>
            <Button title="Target" icon="" btnStyle="outline-btn" />
          </span>
        </div> */}
      </div>

      {user.data.type === "DIRECTOR" && (
        <div className="mt-4 bum-table">
          <h2>BUM</h2>
          {topBum ? (
            <table className="data " style={{ margin: "10px 0" }}>
              <thead>
                <tr>
                  <th>No</th>
                  <th>BUM Name</th>
                  <th>Meet Goal</th>
                  <th>Revenue</th>
                  <th>Target</th>
                  <th>Channel</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {topBum.map((data, i) => {
                  const percentage = Math.round(
                    (data.value / data.target) * 100
                  );
                  return (
                    <tr>
                      <td>{i + 1}.</td>
                      <td>{data.model.name ? data.model.name : ""}</td>
                      <td
                        style={{
                          color: pickColor(percentage),
                          fontWeight: "bold",
                        }}
                      >
                        {data.percentage}
                      </td>
                      <td>{data.value}</td>
                      <td>{data.target}</td>
                      <td>
                        {data.model.channel ? data.model.channel.name : ""}
                      </td>
                      <td>
                        <Link
                          to={`/dashboard/top-performance/${
                            user ? user.data.company.id : "1"
                          }/${moment(startDate).format("YYYY-MM")}`}
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </Link>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          ) : (
            <Loaded />
          )}
        </div>
      )}

      {user.data.as === "BUM" || user.data.as === "Director" ? (
        <div className="mt-4 channel-table">
          <h2>Channel</h2>
          {topChannel ? (
            <table className="data" style={{ margin: "10px 0" }}>
              <thead>
                <tr>
                  <th>No</th>
                  <th>Channel Name</th>
                  <th>Meet Goal</th>
                  <th>Revenue</th>
                  <th>Target</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {topChannel.map((data, i) => {
                  const percentage = Math.round(
                    (data.value / data.target) * 100
                  );
                  return (
                    <tr>
                      <td>{i + 1}.</td>
                      <td>{data.model.name ? data.model.name : ""}</td>
                      <td
                        style={{
                          color: pickColor(percentage),
                          fontWeight: "bold",
                        }}
                      >
                        {data.percentage}
                      </td>
                      <td>{data.value}</td>
                      <td>{data.target}</td>
                      <td>
                        <Link
                          to={`/dashboard/top-channel/${
                            user ? user.data.company.id : "1"
                          }/${moment(startDate).format("YYYY-MM")}`}
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </Link>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          ) : (
            <Loaded />
          )}
        </div>
      ) : (
        ""
      )}

      <div className="mt-4 sales-table">
        <h2>Sales Name</h2>
        {topSales ? (
          <table className="data " style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>No</th>
                <th>BUM Name</th>
                <th>Meet Goal</th>
                <th>Revenue</th>
                <th>Target</th>
                <th>Channel</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {topSales.map((data, i) => {
                const percentage = Math.round((data.value / data.target) * 100);
                return (
                  <tr>
                    <td>{i + 1}.</td>
                    <td>{data.model.name}</td>
                    <td
                      style={{
                        color: pickColor(percentage),
                        fontWeight: "bold",
                      }}
                    >
                      {data.percentage}
                    </td>
                    <td>{data.value}</td>
                    <td>{data.target}</td>
                    <td>{data.model.channel ? data.model.channel.name : ""}</td>
                    <td>
                      <Link
                        to={`/dashboard/top-sales/sales/${
                          user ? user.data.company.id : "1"
                        }/${moment(startDate).format("YYYY-MM")}/0`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        ) : (
          <LoadedComponent />
        )}
      </div>
    </div>
  ) : (
    <LoadedComponent />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
});

export default connect(mapStateToProps)(TopPerformance);
