import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { mainUrl } from "../../../api/api";
import Button from "../../../components/Button/Button";
import parse from "html-react-parser";
import { useParams } from "react-router-dom";
import { isValidate } from "../../../lib/Validate";

const PromoDetail = () => {
  const [promo, setPromo] = useState(null);
  const { promo_id } = useParams();
  useEffect(() => {
    console.log(promo_id);
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getPromo = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/promos?filter[id]=${promo_id}`,
          config
        );
        console.log(res.data.data[0]);
        setPromo(res.data.data[0]);
      } catch (error) {
        console.error(error);
      }
    };

    getPromo();
  }, [promo_id]);
  return (
    promo && (
      <div className="PromoDetail" id="PromoDetail">
        <div
          style={{
            backgroundImage: `url('${promo.images[0].url}')`,
            height: "443px",
            width: "100%",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            borderRadius: "8px",
            backgroundPosition: "center",
          }}
        ></div>
        <div className="mt-3">
          <h4>Promo name</h4>
          <p>{!isValidate(promo.name) && promo.name}</p>

          <h4>Period</h4>
          <p>
            {!isValidate(promo.start_time) &&
              moment(promo.start_time).format("DD MMMM YYYY")}{" "}
            -{" "}
            {!isValidate(promo.end_time) &&
              moment(promo.end_time).format("DD MMMM YYYY")}
          </p>

          <h4>Term and Condition :</h4>
          <p>{!isValidate(promo.description) && parse(promo.description)}</p>
        </div>
        {/* <div>
          <div className="row">
            <div className="col-md-3">
              <img
                src="/Asset/Discount/New 2.png"
                alt="product"
                style={{ width: "100%" }}
              />
              <h5>Product Name</h5>
              <p>Rp 5.000.000 - Rp 10.000.000</p>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/Discount/New 2.png"
                alt="product"
                style={{ width: "100%" }}
              />
              <h5>Product Name</h5>
              <p>Rp 5.000.000 - Rp 10.000.000</p>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/Discount/New 2.png"
                alt="product"
                style={{ width: "100%" }}
              />
              <h5>Product Name</h5>
              <p>Rp 5.000.000 - Rp 10.000.000</p>
            </div>
            <div className="col-md-3">
              <img
                src="/Asset/Discount/New 2.png"
                alt="product"
                style={{ width: "100%" }}
              />
              <h5>Product Name</h5>
              <p>Rp 5.000.000 - Rp 10.000.000</p>
            </div>
          </div>
        </div>
        <Button
          title="Download Catalogue"
          btnStyle="primary-btn"
          btnWidth="100%"
          icon=""
        /> */}
      </div>
    )
  );
};

export default PromoDetail;
