import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { loadCarts } from "../../redux/action/product";
import NotificationContext from "../../store/notification-context";
import Button from "../Button/Button";
import "./SearchProductUnit.scss";

const SearchProductUnit = ({ product: { carts }, search }) => {
  const [productUnits, setProductUnits] = useState(null);
  const notificationCtx = useContext(NotificationContext);
  const dispatch = useDispatch();

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getProductUnits = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/product-units?filter[name]=${search}&perPage=12`,
          config
        );
        console.log(res.data.data);
        setProductUnits(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getProductUnits();
  }, [search]);

  const addCart = async (newCartId) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    notificationCtx.showNotification({
      title: "Adding Cart...",
      message: "Add Cart.",
      status: "pending",
    });

    const dataCart = {
      id: newCartId,
      quantity: 1,
    };
    const items = [...carts.data.items, dataCart];
    const data = { items: items };

    try {
      await axios.put(`${mainUrl}/carts`, data, config);
      // console.log(res.data.data);
      dispatch(loadCarts());
      notificationCtx.showNotification({
        title: "Success!",
        message: "Successfully Add Product to Cart!",
        status: "success",
      });
    } catch (error) {
      console.error(error);
      notificationCtx.showNotification({
        title: "Error!",
        message: error.message || "Something went wrong!",
        status: "error",
      });
    }
  };

  return (
    <div className="row mt-4" id="search-product-unit">
      <h3>Product by Product Unit</h3>
      {productUnits ? (
        productUnits.map((productUnit, idx) => {
          return (
            <div className="col-md-4 product-card" key={idx}>
              <div className="product-item d-flex justify-content-between p-3 mb-2 align-items-center">
                <div>
                  <h3>{productUnit.name}</h3>
                  <p>{formatCurrency(productUnit.price)}</p>
                </div>
                <div onClick={() => addCart(productUnit.id)}>
                  <Button
                    title={`+ Cart`}
                    icon=""
                    btnStyle="primary-btn"
                  ></Button>
                </div>
              </div>
            </div>
          );
        })
      ) : (
        <h3>Product is not found</h3>
      )}
    </div>
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(SearchProductUnit);
