import {
  PROMO_CATEGORIES_ERROR,
  PROMO_CATEGORIES_LOADED,
} from "../action/types";

const initialState = {
  loading: true,
  promoCategories: null,
};

export default function foo(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case PROMO_CATEGORIES_LOADED:
      return {
        ...state,
        loading: false,
        promoCategories: payload,
      };
    case PROMO_CATEGORIES_ERROR:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
}
