import React from "react";
import { formatCurrency } from "../../../api/format";
import "./ProductCart.scss";

const ProductCart = (props) => {
  const {
    item,
    idx,
    isCheck,
    handleCheck,
    handleDecreseQuantity,
    handleAddQuantity,
    handleDeleteCart,
  } = props;

  return (
    <div className="product-cart mt-4">
      <div className="d-flex justify-content-between">
        <div className="d-flex">
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              onChange={(e) => handleCheck(e, item)}
              id={item.id}
              checked={isCheck.includes(item)}

              // checked={
              //   cartActive ? cartActive[i].id.includes(item.id) : false
              // }
            />
          </div>

          <img src="/Asset/no image.jpg" alt="product" />
          <div className="desc">
            <h5>{item.name}</h5>
            <ul>
              <li>{item.covering.name}</li>
              <li>{item.colour.name}</li>
            </ul>
            <p>{formatCurrency(item.total_price)}</p>
            <div className="qty-form d-flex align-items-center text-center">
              <button
                className="decrease"
                onClick={() => handleDecreseQuantity(item, idx)}
              >
                -
              </button>
              <div className="qty-input">{item.quantity}</div>
              <button
                className="increase"
                // onClick={() => setQty(qty + 1)}
                onClick={() => handleAddQuantity(item, idx)}
              >
                +
              </button>
            </div>
          </div>
        </div>

        <div className="delete-item">
          <img
            src="/Asset/Times.png"
            alt="delete"
            onClick={() => handleDeleteCart(item.id)}
          />
        </div>
      </div>
    </div>
  );
};

export default ProductCart;
