import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";

const ProductByBrand = () => {
  const { brand_name, brand_id } = useParams();
  const [models, setModels] = useState(null);
  const [perPage, setPerPage] = useState(12);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getModels = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/models?filter[product_brand_id]=${brand_id}&perPage=${perPage}`,
          config
        );
        console.log(res.data.data);
        setModels(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getModels();
  }, [perPage, brand_id]);

  return models ? (
    <div className="product-by-brand" id="product-by-brand">
      <Header title={brand_name} back={true} />

      <div className="row">
        {models.map((model) => {
          return (
            <div className="col-md-2 product-card">
              <Link to={`/dashboard/product/product_detail/${model.id}`}>
                <img
                  src={model.images[0].url}
                  alt="product"
                  style={{ width: "100%", borderRadius: "8px" }}
                />
                <h3>{model.name}</h3>
                <p>Rp 1.000.000 s/d Rp 2.000.000</p>
              </Link>
            </div>
          );
        })}
        {models.length >= perPage && (
          <div>
            <div
              onClick={() => setPerPage(perPage + 12)}
              style={{ cursor: "pointer", color: "blue" }}
            >
              More...
            </div>
          </div>
        )}
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default ProductByBrand;
