import React, { useState } from "react";
import { formatCurrency } from "../../api/format";
import Popup from "../Popup/Popup";
import UploadImageCartDemand from "../Popup/Popup Components/UploadImg/UploadImageCartDemand";
import PopupLayout from "../Popup/PopupLayout";

const NewProduct = (props) => {
  const { cart_demand } = props;
  const [uploadImg, setUploadImg] = useState(false);
  const [unit_id, setUnit_id] = useState(null);

  console.log(cart_demand);

  const handleUploadImg = (unit_id) => {
    setUploadImg(!uploadImg);
    setUnit_id(unit_id);
  };

  return (
    <div>
      {/* <PopupLayout
        Popup={uploadImg}
        setPopup={setUploadImg}
        component={
          <UploadImageCartDemand item_id={unit_id} cart_demand={cart_demand} />
        }
      /> */}
      <Popup popup={uploadImg} setPopup={setUploadImg}>
        <UploadImageCartDemand item_id={unit_id} cart_demand={cart_demand} />
      </Popup>

      <h3>New Product</h3>
      {cart_demand.items.map((unit) => {
        return (
          <div className="d-flex mb-4" key={unit.id}>
            <img
              src={unit.image ? unit.image : "/Asset/no image.jpg"}
              alt="detailsku"
              style={{
                width: "122px",
                height: "122px",
                marginRight: "16px",
                borderRadius: "4px",
              }}
              onClick={() => handleUploadImg(unit.id)}
            />
            {/* {console.log(unit.images[0].url)} */}
            <div>
              <h3>{unit.name ? unit.name : ""}</h3>
              <p style={{ margin: "0" }}>
                {`${formatCurrency(unit.price)} x ${unit.quantity}`}
              </p>
            </div>

            {/* <PopupLayout
              Popup={uploadImg}
              setPopup={() => setUploadImg()}
              component={<UploadImageCartDemand unit_id={unit.id} />}
            /> */}
          </div>
        );
      })}
    </div>
  );
};

export default NewProduct;
