import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Activity from "../../../components/ActivityDetail/Activity";
import Header from "../../../components/Header/Header";
import LoadedComponent from "../../../components/Loaded/LoadedComponent";
import { isValidate } from "../../../lib/Validate";

const LeadDesignerActivities = () => {
  const { designer_id, acitivity_id } = useParams();
  const [activityDetail, setActivityDetail] = useState(false);
  const [customerActivities, setCustomerActivities] = useState(null);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [orderId, setOrderId] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getCustomerActivities = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/interior-designs/reports/${designer_id}/leads/${acitivity_id}`,
          config
        );
        console.log(res.data);
        setCustomerActivities(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getCustomerActivities();
  }, [designer_id, acitivity_id]);

  const handleClickDetail = (activity) => {
    setActivityDetail(!activityDetail);
    setCustomerActivity(activity);
    setOrderId(activity.order ? activity.order.id : null);
    console.log(activity.order);
  };
  return customerActivities ? (
    <div>
      <Header icon="" title="Designer - Activities" back={true} user={false} />

      <table className="data" style={{ margin: "10px 0" }}>
        <thead>
          <tr>
            <th>Date</th>
            <th>Invoice Number</th>
            <th>Follow Up</th>
            <th>Store Location</th>
            <th>Payment</th>
            <th>Quotation</th>
            <th>Sales</th>
            <th>Status</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {customerActivities.map((activity) => {
            return (
              <tr key={activity.id}>
                <td>
                  {!isValidate(activity.follow_up_datetime)
                    ? moment(activity.follow_up_datetime).format("MM/DD/YYYY")
                    : ""}
                </td>
                <td>
                  {!isValidate(activity.order)
                    ? activity.order.invoice_number
                    : ""}
                </td>
                <td>
                  {!isValidate(activity.follow_up_method)
                    ? activity.follow_up_method
                    : ""}
                </td>
                <td>
                  {!isValidate(activity.channel.name)
                    ? activity.channel.name
                    : ""}
                </td>
                <td>
                  {!isValidate(activity.order)
                    ? activity.order.payment_status
                    : ""}
                </td>
                <td>
                  {!isValidate(activity.order) ? activity.order.status : ""}
                </td>
                <td>
                  {!isValidate(activity.user.name) ? activity.user.name : ""}
                </td>
                <td>{!isValidate(activity.status) ? activity.status : ""}</td>
                <td>
                  <div
                    onClick={() => handleClickDetail(activity)}
                    className="activity-detail-btn"
                  >
                    <img
                      src="/Asset/Action Icon/eye.png"
                      style={{ marginRight: "5px" }}
                      alt="eye"
                    />
                    See Detail
                  </div>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  ) : (
    <LoadedComponent />
  );
};

export default LeadDesignerActivities;
