import React, { useEffect, useState } from "react";
import Button from "../../../components/Button/Button";
import Slider from "react-slick";
import { Link } from "react-router-dom";
import axios from "axios";
import { mainUrl } from "../../../api/api";
import { useParams } from "react-router-dom";
import { connect, useDispatch } from "react-redux";
import { temporaryCartLoad } from "../../../redux/action/product";
import { Redirect } from "react-router-dom";
import { useHistory } from "react-router-dom";
import NewArrivals from "../../../components/Product-Components/NewArrivals";


const ProductDetail = ({ product: { temporaryCart, carts } }) => {
  const { model_id } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const [productDetail, setProductDetail] = useState(null);
  const [productUnit, setProductUnit] = useState(null);
  const [cart, setCart] = useState();
  // console.log(temporaryCart);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getProductDetail = async () => {
      try {
        const res = await axios.get(`${mainUrl}/models/${model_id}`, config);
        console.log(res.data.data);
        setProductDetail(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getProductDetail();
  }, [model_id]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getProductUnit = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/product-units/${temporaryCart[0].id}`,
          config
        );
        console.log(res.data.data);
        setProductUnit(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    // console.log(temporaryCart);
    if (temporaryCart.length) {
      getProductUnit();
      setCart(temporaryCart);
      dispatch(temporaryCartLoad());
    }
  }, [temporaryCart]);

  const addCart = async () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const items = cart ? cart : carts && carts.data.items;
    carts.data.items.length > 0 &&
      cart &&
      carts.data.items
        .filter((item) => item.id !== cart.id)
        .map((data) => {
          items.push(data);
        });
    const data = { items: items };
    try {
      const res = await axios.put(`${mainUrl}/carts`, data, config);
      console.log(res.data.data);
    } catch (error) {}
  };



  const settingsImg = {
    customPaging: function (i) {
      return (
        <div>
          <img
            src={productDetail.images[0].url}
            style={{ width: "100%" }}
            alt={productDetail.images[0].url}
          />
        </div>
      );
    },
    dots: true,
    dotsClass: "slick-dots slick-thumb",
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
    productDetail && (
      <div className="product-detail" id="product-detail">
        <div className="product-detail-content row">
          <div className="col-md-5 product-img">
            <Slider {...settingsImg}>
              <div>
                <img
                  src={productDetail.images[0].url}
                  alt="productDetail"
                  style={{
                    width: "100%",
                  }}
                />
              </div>
              <div>
                <img
                  src={productDetail.images[0].url}
                  alt="productDetail"
                  style={{ width: "100%" }}
                />
              </div>
              <div>
                <img
                  src={productDetail.images[0].url}
                  alt="productDetail"
                  style={{ width: "100%" }}
                />
              </div>
              <div>
                <img
                  src={productDetail.images[0].url}
                  alt="productDetail"
                  style={{ width: "100%" }}
                />
              </div>
            </Slider>

            {/* <img
              src={productDetail.images[0].url}
              alt="productDetail"
              style={{ width: "100%" }}
            /> */}
            <div className="mt-3">
              <Link to={`/dashboard/product/product_option/${model_id}`}>
                <Button
                  title="Select Option"
                  btnStyle="primary-btn"
                  icon=""
                  btnWidth="100%"
                />
              </Link>
            </div>
          </div>
          <div className="desc col-md-7">
            <h4>{productDetail.name}</h4>
            {productUnit ? (
              <>
                <ul>
                  <li>{productUnit.name}</li>
                  <li>{productUnit.covering.name}</li>
                  <li>{productUnit.colour.name}</li>
                </ul>
                <h5>Rp. {productUnit.price}</h5>
              </>
            ) : (
              <h5>
                {productDetail.price_min} s/d {productDetail.price_max}
              </h5>
            )}

            <p>{productDetail.description}</p>
            <div className="d-flex">
              <Link
                className="me-2"
                onClick={() => addCart()}
                to="/dashboard/product/cart"
              >
                <Button btnStyle="primary-btn" title="Add To Chart" icon="" />
              </Link>
              <div>
                <Button btnStyle="outline-btn" title="Checkout" icon="" />
              </div>
            </div>
          </div>
        </div>

        {/* <div className="similar-product">
          <h3>Similar Product</h3>
          <div className="similar-product-carousel">
            <Slider {...settings}>
              {productData.map((data) => {
                return (
                  <div key={data.id}>
                    <div className="similar-product-carousel-card">
                      <img
                        src={data.imgUrl}
                        alt="product1"
                        style={{
                          margin: "0 auto",
                          width: "100%",
                        }}
                      />
                      <h4>{data.title}</h4>
                      <p>{data.price}</p>
                    </div>
                  </div>
                );
              })}
            </Slider>
          </div>
        </div> */}
        <NewArrivals />
      </div>
    )
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(ProductDetail);
