import moment from "moment";
import React from "react";
import { formatCurrency } from "../../api/format";
import { activityStatusColor } from "../../api/pickColor";
import { isValidate } from "../../lib/Validate";
import LoadedComponent from "../Loaded/LoadedComponent";

const ActivityDetails = (props) => {
  const { customerActivity } = props;
  return customerActivity ? (
    <div className="">
      <h3>Recent Activity</h3>
      <div className="d-flex justify-content-between">
        <div>
          <p>Created</p>
          <p>Last Update</p>
          <p>Activity Type</p>
          <p>Customer</p>
          <p>Sales</p>
          <p>Channel</p>
          <p>Status</p>
          <p>Priority</p>
        </div>
        <div className="text-end">
          <p>
            {!isValidate(customerActivity.follow_up_datetime)
              ? moment(customerActivity.follow_up_datetime).format(
                  "MM/DD/YYYY | LT"
                )
              : ""}
          </p>
          <p>
            {!isValidate(customerActivity.lead.updated_at)
              ? moment(customerActivity.lead.updated_at).format(
                  "MM/DD/YYYY | LT"
                )
              : ""}
          </p>
          <p>
            {!isValidate(customerActivity.follow_up_method)
              ? customerActivity.follow_up_method
              : ""}
          </p>
          <p>
            {customerActivity ? customerActivity.customer.first_name : ""}{" "}
            {customerActivity.customer.last_name
              ? customerActivity.customer.last_name
              : ""}
          </p>
          <p>
            {!isValidate(customerActivity.user.name)
              ? customerActivity.user.name
              : ""}
          </p>
          <p>
            {!isValidate(customerActivity.channel.name)
              ? customerActivity.channel.name
              : ""}
          </p>
          <p>
            <span
              style={{
                padding: "0 10px",
                background: "red",
                borderRadius: "4px",
                backgroundColor: `${
                  customerActivity.status &&
                  activityStatusColor(customerActivity.status)
                }`,
              }}
            >
              {!isValidate(customerActivity.status) && customerActivity.status}
            </span>
          </p>
          <p>
            {customerActivity.lead.status ? customerActivity.lead.status : ""}
          </p>
        </div>
      </div>
      <hr />
      <div>
        <p>Estimated Brands</p>
        {customerActivity.activity_brand_values &&
          customerActivity.activity_brand_values.map((brand) => {
            return (
              <div
                key={brand.id}
                className="d-flex justify-content-between align-items-center mb-2"
              >
                <div className="d-flex align-items-center">
                  <img
                    src={
                      brand.images[0].url
                        ? brand.images[0].url
                        : "/Asset/no image.jpg"
                    }
                    style={{
                      width: "45px",
                      marginRight: "10px",
                      objectFit: "contain",
                    }}
                  />
                  <p style={{ margin: "0" }}>{brand.name ? brand.name : ""}</p>
                </div>
                <div>
                  <p style={{ margin: "0" }}>
                    {brand.estimated_value
                      ? formatCurrency(brand.estimated_value)
                      : ""}
                  </p>
                </div>
              </div>
            );
          })}
      </div>
      <hr />
      <div className="d-flex justify-content-between">
        <div>
          <p>Estimated Value</p>
        </div>
        <div>
          <p>
            {customerActivity.estimated_value
              ? formatCurrency(customerActivity.estimated_value)
              : "Rp 0"}
          </p>
        </div>
      </div>
    </div>
  ) : (
    <LoadedComponent />
  );
};

export default ActivityDetails;
