import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";

const InteriorDesign = () => {
  const { company_id, month } = useParams();

  const [interiorDesign, setInteriorDesign] = useState(null);
  const [date, setDate] = useState(month);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getInteriorDesign = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/interior-designs/detail?company_id=${
            company_id && company_id
          }&start_at=${moment(date)
            .startOf("month")
            .format("YYYY-MM-DD")}&end_at=${moment(date)
            .endOf("month")
            .format("YYYY-MM-DD")}`,
          config
        );
        setInteriorDesign(res.data);
        console.log(res.data);
      } catch (error) {}
    };

    getInteriorDesign();
  }, [company_id, date]);
  return (
    interiorDesign && (
      <div>
        <Header
          title={"Interior Design"}
          back={true}
          notif={true}
          user={false}
        />
        <div className="filter-form d-flex justify-content-between">
          <div>
            <input
              type="text"
              id="search-sales"
              name="search-sales"
              className="form-control"
              placeholder="Search BUM"
              style={{ height: "41px", width: "327px" }}
            />
          </div>
          <div>
            <input
              type="month"
              id="start"
              name="start"
              className="form-control"
              placeholder="Start Month"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              style={{ height: "41px" }}
            />
          </div>
        </div>
        <div className="mt-4">
          <table className="data" style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>No.</th>
                <th>ID name</th>
                <th>Revenue</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {interiorDesign.map((data) => {
                return (
                  <tr>
                    <td>1.</td>
                    <td>{data.interior_design.name}</td>
                    <td>{data.total}</td>
                    <td>
                      <Link
                        to={`/dashboard/interior_design/${company_id}/${month}/${data.interior_design_id}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    )
  );
};

export default InteriorDesign;
