import React from "react";

const Skill = (props) => {
  return (
    <div className="bar mb-4 text-center" id="bar">
      <div className="row">
        <div className="col-2">{props.title}</div>
        <div
          className="col-8"
          style={{
            backgroundColor: "#DADADA",
            borderRadius: "8px",
            height: "10px",
            marginTop: "5px",
            padding: "0",
          }}
        >
          <div
            style={{
              backgroundColor: `${props.color}`,
              width: `${props.progress}%`,
              height: "10px",

              borderRadius: "8px",
            }}
          ></div>
        </div>
        <div className="col-2">{props.value}</div>
      </div>
    </div>
  );
};

export default Skill;
