import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { mainUrl } from "../../../../api/api";
import Header from "../../../../components/Header/Header";

const PaymentCategories = () => {
  const { orderId } = useParams();
  const [paymentCategories, setPaymentCategories] = useState(null);
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getPaymentCategories = async () => {
      try {
        const res = await axios.get(`${mainUrl}/payment-categories`, config);
        console.log(res.data.data);
        setPaymentCategories(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getPaymentCategories();
  }, []);
  return (
    paymentCategories && (
      <div className="payment text-center" id="payment">
        <Header title="Payment" back="true" icon="" />
        <div className="row" style={{ width: "80%" }}>
          {paymentCategories.map((pCategory) => {
            return (
              <div className="col-md-4" style={{}} key={pCategory.id}>
                <Link
                  to={`/dashboard/payment/${orderId}/${pCategory.name}/${pCategory.id}`}
                  style={{ textDecoration: "none", color: "#313132" }}
                >
                  <div
                    style={{
                      padding: "70px 40px",
                      border: "0.5px solid #C4C4C4",
                      boxSizing: "border-box",
                      borderRadius: "4px",
                      height: "90%",
                      width: "100%",
                    }}
                  >
                    <img
                      src={`${pCategory.images[0].url}`}
                      alt="cash"
                      style={{ width: "100%" }}
                    />
                  </div>
                  <p>{pCategory.name}</p>
                </Link>
              </div>
            );
          })}
          {/* <div className="col-md-4" style={{}}>
            <Link
              to={"/dashboard/product/payment/cash"}
              style={{ textDecoration: "none", color: "#313132" }}
            >
              <div
                style={{
                  padding: "70px 40px",
                  border: "0.5px solid #C4C4C4",
                  boxSizing: "border-box",
                  borderRadius: "4px",
                  height: "90%",
                  width: "100%",
                }}
              >
                <img
                  src="/Asset/Cash.png"
                  alt="cash"
                  style={{ width: "100%" }}
                />
              </div>
              <p>Cash</p>
            </Link>
          </div>
          <div className="col-md-4" style={{}}>
            <Link
              to={"/dashboard/product/payment/debit"}
              style={{ textDecoration: "none", color: "#313132" }}
            >
              <div
                style={{
                  padding: "70px 40px",
                  border: "0.5px solid #C4C4C4",
                  boxSizing: "border-box",
                  borderRadius: "4px",
                  height: "90%",
                  width: "100%",
                }}
              >
                <img
                  src="/Asset/Debit.png"
                  alt="debit"
                  style={{ width: "100%" }}
                />
              </div>
              <p>Debit</p>
            </Link>
          </div>
          <div className="col-md-4" style={{}}>
            <Link
              to={"/dashboard/product/payment/credit"}
              style={{ textDecoration: "none", color: "#313132" }}
            >
              <div
                style={{
                  padding: "70px 40px",
                  border: "0.5px solid #C4C4C4",
                  boxSizing: "border-box",
                  borderRadius: "4px",
                  height: "90%",
                  width: "100%",
                }}
              >
                <img
                  src="/Asset/Credit.png"
                  alt="credit"
                  style={{ width: "100%" }}
                />
              </div>
              <p>Credit</p>
            </Link>
          </div> */}
        </div>
      </div>
    )
  );
};

export default PaymentCategories;
