import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Activity from "../../../components/ActivityDetail/Activity";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";

const Settlement = () => {
  const { company_id, month } = useParams();

  const [settlement, setSettlement] = useState(null);
  const [filterSettlement, setFilterSettlement] = useState("");
  const [date, setDate] = useState(month);

  const [activityDetail, setActivityDetail] = useState(false);
  const [orderId, setOrderId] = useState(null);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [settlementActivityId, setSettlementActivityId] = useState(null);

  // const [commentsData, setCommentsData] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getSettlement = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/pelunasan?company_id=${
            company_id ? company_id : ""
          }&start_at=${moment(date)
            .startOf("month")
            .format("YYYY-MM-DD")}&end_at=${moment(date)
            .endOf("month")
            .format("YYYY-MM-DD")}`,
          config
        );
        setSettlement(res.data);
        console.log(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    getSettlement();
  }, [date, company_id]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getActivity = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities?filter[id]=${settlementActivityId}`,
          config
        );
        console.log(res.data.data[0]);
        setCustomerActivity(res.data.data[0]);
        // setActivityDetail(!activityDetail);
        setOrderId(res.data.data[0].order ? res.data.data[0].order.id : "");
      } catch (error) {
        console.error(error);
      }
    };
    getActivity();
  }, [settlementActivityId]);

  // useEffect(() => {
  //   const getActivity = async () => {
  //     try {
  //       const res = await axios.get(`${mainUrl}/activities?filter[id]=${}`)
  //     } catch (error) {
  //       console.error(error)
  //     }
  //   }
  // },[])

  // useEffect(() => {
  //   const config = {
  //     headers: {
  //       "Content-Type": "application/json",
  //       Authorization: `Bearer ${localStorage.token}`,
  //     },
  //   };
  //   const getCommentsActivity = async () => {
  //     try {
  //       const res = await axios.get(
  //         `${mainUrl}/activities/${
  //           customerActivity.id ? customerActivity.id : ""
  //         }/comments`,
  //         config
  //       );
  //       console.log(res.data.data);
  //       setCommentsData(res.data.data);
  //     } catch (error) {
  //       console.error(error);
  //     }
  //   };

  //   if (customerActivity) {
  //     getCommentsActivity();
  //   }
  // }, [customerActivity]);

  const handleClickDetail = (activity) => {
    setActivityDetail(!activityDetail);
    // setCustomerActivity(activity);

    setSettlementActivityId(activity.activity_id);
    // console.log(activity);
  };
  return settlement ? (
    <div>
      <Header title={"Settlement"} back={true} notif={true} />
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search BUM"
            onChange={(e) => setFilterSettlement(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No.</th>
              <th>Date</th>
              <th>Invoice</th>
              <th>Customer Name</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {settlement
              .filter((data) => {
                if (filterSettlement === "") {
                  return data;
                } else if (
                  data.invoice
                    .toLowerCase()
                    .includes(filterSettlement.toLowerCase())
                ) {
                  return data;
                } else if (
                  data.customer
                    .toLowerCase()
                    .includes(filterSettlement.toLowerCase())
                ) {
                  return data;
                }
              })
              .map((data, i) => {
                return (
                  <tr key={data.activity_id}>
                    <td>{i + 1}.</td>
                    <td>{data.date}</td>
                    <td>{data.invoice}</td>
                    <td>{data.customer}</td>
                    <td>
                      <div
                        onClick={() => handleClickDetail(data)}
                        className="activity-detail-btn"
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </div>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          // orderData={orderData}
          // user={user}
          // comments={commentsData}
          // settlementActivityId={settlementActivityId}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  ) : (
    <Loaded />
  );
};

export default Settlement;
