import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { pickColor } from "../../../api/pickColor";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import FollowUpPopup from "../../../components/Popup/Popup Component/FollowUpPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";

const SalesRevenueStoreLeader = () => {
  const { user_id, month, company_id } = useParams();
  const [salesRevenueStoreLeader, setSalesRevenueStoreLeader] = useState(null);

  const [filterStoreLeader, setFilterStoreLeader] = useState("");
  const [date, setDate] = useState(month);

  const [userId, setUserId] = useState(null);
  const [popup, setPopup] = useState(false);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesRevenueBUM = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${moment(
            date
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&filter[end_before]=${moment(date)
            .endOf("month")
            .format(
              "YYYY-MM-DD"
            )}&filter[reportable_type]=USER&filter[company_id]=&filter[supervisor_type_level]=1&filter[descendant_of]=${user_id}`,
          config
        );
        setSalesRevenueStoreLeader(res.data.data);
        console.log(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getSalesRevenueBUM();
  }, [user_id, date]);

  // const getFollowUp = async (id) => {
  //   let followUpData = null;
  //   const config = {
  //     headers: {
  //       "Content-Type": "application/json",
  //       Authorization: `Bearer ${localStorage.token}`,
  //     },
  //   };
  //   try {
  //     const res = await axios.get(
  //       `${mainUrl}/targets?filter[type]=ACTIVITY_COUNT`,
  //       config
  //     );
  //     console.log(res.data.data);
  //     followUpData = res.data.data;
  //     // return res.data.data.breakdown;
  //   } catch (error) {
  //     console.error(error);
  //   }

  //   // console.log(followUpData);
  //   return followUpData;
  //   // return followUpData.map((data) => {
  //   //   return <td style={{ color: "#E53935" }}>{data.value}</td>;
  //   // });
  // };

  const handleFollowUpPopup = (user_id) => {
    setPopup(true);
    setUserId(user_id);
  };
  return salesRevenueStoreLeader ? (
    <div>
      <PopupLayout
        Popup={popup}
        setPopup={() => setPopup()}
        component={
          <FollowUpPopup month={date} userId={userId} company_id={company_id} />
        }
      />
      <Header title={"Sales Revenue - Store Leader"} back={true} notif={true} />
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search Store Leader"
            onChange={(e) => setFilterStoreLeader(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>Store Leader Name</th>
              <th>Revenue</th>
              <th>Target</th>
              <th>Meet Goal</th>
              <th>Follow Up</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {salesRevenueStoreLeader
              .filter((data) => {
                if (filterStoreLeader === "") {
                  return data;
                } else if (
                  data.report.name
                    .toLowerCase()
                    .includes(filterStoreLeader.toLowerCase())
                ) {
                  return data;
                }
              })
              .map((data, i) => {
                const percentage = Math.round(
                  (data.value.value / data.target.value) * 100
                );
                return (
                  <tr key={data.id}>
                    <td>{i + 1}.</td>
                    <td>{data.report.name}</td>
                    <td>{data.value.format}</td>
                    <td>{data.target.format}</td>
                    <td
                      style={{
                        color: pickColor(percentage),
                        fontWeight: "bold",
                      }}
                    >
                      {percentage === Infinity || isNaN(percentage)
                        ? "0"
                        : percentage}{" "}
                      %
                    </td>
                    {/* {followUp &&
                        followUp.map((d) => {
                          return (
                            <td style={{ color: "#E53935" }}>{d.value}</td>
                          );
                        })} */}
                    {/* <td style={{ color: "#E53935" }}>1</td>
                      <td style={{ color: "#FFD13D" }}>20</td>
                      <td style={{ color: "#0553B7" }}>20</td>
                      <td style={{ color: "#000000" }}>10</td> */}
                    <td>
                      <div
                        onClick={() => handleFollowUpPopup(data.user.id)}
                        style={{
                          background: "#0553B7",
                          textAlign: "center",
                          width: "70%",
                          borderRadius: "8px",
                          color: "#fff",
                          padding: "5px",
                          cursor: "pointer",
                        }}
                      >
                        See Follow Up
                      </div>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/sales_revenue_sales/${company_id}/${data.user.id}/${date}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default SalesRevenueStoreLeader;
