import axios from "axios";
import React, { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../../api/api";
import { formatCurrency } from "../../../../api/format";
import Button from "../../../../components/Button/Button";
import Header from "../../../../components/Header/Header";
import Loaded from "../../../../components/Loaded/Loaded";

const ConfirmPayment = () => {
  const { orderId, payment_type_id } = useParams();
  const [orderData, setOrderData] = useState(null);
  const [paymentData, setPaymentData] = useState(null);
  const [paymentDetailActive, setPaymentDetailActive] = useState(false);
  const [redirect, setRedirect] = useState(false);

  const [amount, setAmount] = useState(0);
  const [reference, setReference] = useState("");
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    console.log(orderId);
    // setLoadOrder(false);
    const getOrderData = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/orders?filter[id]=${orderId}`,
          config
        );
        console.log(res.data.data[0]);
        setOrderData(res.data.data[0]);
        // setLoadOrder(true);
      } catch (error) {
        console.error(error);
      }
    };

    const getPayment = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/payments?filter[order_id]=${orderId}`,
          config
        );
        console.log(res.data.data[0]);
        setPaymentData(res.data.data[0]);
      } catch (error) {
        console.error(error);
      }
    };
    getOrderData();
    getPayment();
  }, [orderId]);

  const handleSubmit = async () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const data = {
      amount: parseInt(amount),
      reference: reference,
      payment_type_id: parseInt(payment_type_id),
      order_id: parseInt(orderId),
    };

    try {
      const res = await axios.post(`${mainUrl}/payments`, data, config);
      console.log(res.data.data);
      setAmount("");
      setReference("");
      setRedirect(true);
    } catch (error) {
      console.error(error);
    }

    // console.log(data);
  };
  return orderData ? (
    <div className="confirm-payment" id="confirm-payment">
      {redirect ? <Redirect to="/dashboard" /> : ""}
      <Header title="Confirm Payment" back={true} />
      <div>
        {/* <div className="product-card">
          <div className="d-flex justify-content-between">
            <h5>Product</h5>
          </div>
          {orderData.order_details &&
            orderData.order_details.map((item, i) => {
              return (
                <div
                  className="d-flex justify-content-between mt-3 mb-3"
                  key={item.id}
                >
                  <div className="d-flex">
                    <img
                      src={
                        item.images[0] ? item.images[0].url : "/Asset/New 5.jpg"
                      }
                      alt="product_img"
                    />
                    <div className="desc">
                      <h5>{item.product_unit.name}</h5>
                      <ul>
                        <li>{item.covering.name}</li>
                        <li>{item.colour.name}</li>
                      </ul>
                      <h5>{formatCurrency(item.total_price)}</h5>
                    </div>
                  </div>
                </div>
              );
            })}
        </div> */}
        <h5>Order Detail</h5>
        <table>
          <thead>
            <tr>
              <th>No</th>
              <th>Product Name</th>
              <th>Qty</th>
              <th className="text-end">Price</th>
            </tr>
          </thead>
          <tbody>
            {orderData.order_details &&
              orderData.order_details.map((item, i) => {
                return (
                  <tr key={i}>
                    <td>{i + 1}</td>
                    <td>{item.product_unit.name}</td>
                    <td>{item.quantity}</td>
                    <td className="text-end">
                      {formatCurrency(item.unit_price)}
                    </td>
                  </tr>
                );
              })}
            <tr style={{ borderTop: "2px solid #000" }}>
              <td></td>
              <td></td>
              <td>Price</td>
              <td className="text-end">
                {formatCurrency(orderData.original_price)}
              </td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td>Discount</td>
              <td className="text-end">
                - {formatCurrency(orderData.total_discount)}
              </td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td>Packing</td>
              <td className="text-end">
                {formatCurrency(orderData.packing_fee)}
              </td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td>Shipping</td>
              <td className="text-end">
                {formatCurrency(orderData.shipping_fee)}
              </td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td>Additional Discount</td>
              <td className="text-end">
                - {formatCurrency(orderData.additional_discount)}
              </td>
            </tr>
            <tr
              style={{
                borderTop: "2px solid #000",
              }}
            >
              <td></td>
              <td></td>
              <td>Sub Total</td>
              <td className="text-end">
                -{" "}
                {orderData.total_price
                  ? formatCurrency(orderData.total_price)
                  : formatCurrency(orderData.original_price)}
              </td>
            </tr>
          </tbody>
        </table>

        <h5>Payment Info</h5>
        <table>
          <thead>
            <tr>
              <th>Payment Info</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Payment Status</td>
              <td></td>
              <td></td>
              <td className="text-end">{orderData.payment_status}</td>
            </tr>
            <tr>
              <td>Total Paid</td>
              <td></td>
              <td></td>
              <td className="text-end">
                {formatCurrency(orderData.amount_paid)}
              </td>
            </tr>
            <tr>
              <td>Total Unpaid</td>
              <td></td>
              <td></td>
              <td className="text-end">
                {formatCurrency(orderData.total_price - orderData.amount_paid)}
              </td>
            </tr>
            {}
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td
                className="text-end"
                onClick={() => setPaymentDetailActive(!paymentDetailActive)}
              >
                See Payments Detail
              </td>
            </tr>
            {paymentDetailActive && (
              <tr>
                <td>ID</td>
                <td></td>
                <td></td>
                <td className="text-end">
                  {paymentData ? paymentData.id : "-"}
                </td>
              </tr>
            )}
            {paymentDetailActive && (
              <tr>
                <td>Amount:</td>
                <td></td>
                <td></td>
                <td className="text-end">
                  {paymentData ? paymentData.amount : "-"}
                </td>
              </tr>
            )}
            {paymentDetailActive && (
              <tr>
                <td>Payment Type:</td>
                <td></td>
                <td></td>
                <td className="text-end">
                  {paymentData ? paymentData.payment_type.name : "-"}
                </td>
              </tr>
            )}
            {paymentDetailActive && (
              <tr>
                <td>Reference:</td>
                <td></td>
                <td></td>
                <td className="text-end">
                  {paymentData ? paymentData.status : "-"}
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      <div className="notes mt-3">
        <h6>
          Amount<span>*</span>
          (Rp)
        </h6>
        <div className="mt-2 d-flex">
          <input
            className="form-control"
            type="number"
            placeholder="0"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            aria-label="default input example"
          />
        </div>
      </div>
      <div className="notes mt-3">
        <h6>Reference (optional)</h6>
        <div className="mt-2">
          <textarea
            className="form-control"
            type="text"
            placeholder="Input your reference here"
            value={reference}
            onChange={(e) => setReference(e.target.value)}
            aria-label="default input example"
          />
        </div>
      </div>

      <div className="mt-3" onClick={() => handleSubmit()}>
        <Button title="pay" btnStyle="primary-btn" btnWidth="100%" icon="" />
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default ConfirmPayment;
