import React from "react";
import { formatCurrency } from "../../../api/format";
import Button from "../../Button/Button";

const SummaryBilling = (props) => {
  const { price, cartDemands, tempOrder, handleSubmitOrder } = props;
  return (
    <div className="billing-detail">
      <h5>Billing Detail</h5>
      <div className="d-flex justify-content-between">
        <p>Price</p>
        <p>{formatCurrency(price)}</p>
      </div>
      {cartDemands && (
        <div className="d-flex justify-content-between">
          <p>Product Non Existing</p>
          <p>
            {cartDemands.total_price && formatCurrency(cartDemands.total_price)}
          </p>
        </div>
      )}
      {tempOrder &&
        tempOrder.order_discounts.map((discount) => {
          return (
            <div className="d-flex justify-content-between">
              <p>{discount.discount.name}</p>
              <p>
                -{" "}
                {discount.discount.type === "PERCENTAGE"
                  ? `${discount.discount.value}%`
                  : formatCurrency(discount.discount.value)}
              </p>
            </div>
          );
        })}
      {tempOrder && (
        <div className="d-flex justify-content-between">
          <p>Discount</p>
          <p>- {formatCurrency(tempOrder.total_discount)}</p>
        </div>
      )}
      {tempOrder && (
        <div className="d-flex justify-content-between">
          <p>Packing</p>
          <p>{formatCurrency(tempOrder.packing_fee)}</p>
        </div>
      )}
      {tempOrder && (
        <div className="d-flex justify-content-between">
          <p>Shipping</p>
          <p>{formatCurrency(tempOrder.shipping_fee)}</p>
        </div>
      )}
      {tempOrder && (
        <div className="d-flex justify-content-between">
          <p>Additional Discount</p>
          <p>- {formatCurrency(tempOrder.additional_discount)}</p>
        </div>
      )}
      <hr />
      {/* <div className="d-flex justify-content-between">
              <p>
                <b>Total</b>
              </p>
              <p>
                <b>Rp 0</b>
              </p>
            </div>
            <div className="d-flex justify-content-between">
              <p>50% discount all product</p>
              <p>- Rp 0</p>
            </div>
            <hr /> */}
      <div className="d-flex justify-content-between">
        <p>
          <b>Sub Total</b>
        </p>
        <p>
          <b>
            {tempOrder
              ? formatCurrency(tempOrder.total_price)
              : formatCurrency(price)}
          </b>
        </p>
      </div>
      <div className="btn-payment">
        <div onClick={handleSubmitOrder}>
          <Button
            title="Place Order"
            btnStyle="primary-btn"
            icon=""
            btnWidth="100%"
          />
        </div>
      </div>
    </div>
  );
};

export default SummaryBilling;
