import {
  SMS_MOBILE_SIDEBAR_ACTIVE,
  SMS_MOBILE_SIDEBAR_HIDE,
} from "../action/types";

const initialState = {
  smsMobileSideBar: false,
};

export default function foo(state = initialState, action) {
  const { type } = action;

  switch (type) {
    case SMS_MOBILE_SIDEBAR_ACTIVE:
      return {
        ...state,
        smsMobileSideBar: true,
      };
    case SMS_MOBILE_SIDEBAR_HIDE:
      return {
        ...state,
        smsMobileSideBar: false,
      };
    default:
      return state;
  }
}
