import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { pickColor } from "../../../api/pickColor";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import LoadedComponent from "../../../components/Loaded/LoadedComponent";
import FollowUpPopup from "../../../components/Popup/Popup Component/FollowUpPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";

const SalesRevenueBum = ({ auth: { user, loading } }, props) => {
  const { company_id, month } = useParams();

  const [popup, setPopup] = useState(false);

  const [SalesRevenueBUM, setSalesRevenueBUM] = useState(null);
  const [filterBum, setFilterBum] = useState("");
  const [date, setDate] = useState(month);
  // const [followUpData, setFollowUpData] = useState([]);

  const [userId, setUserId] = useState(null);
  // let followUpData = [];

  useEffect(() => {
    // let salesRevenueData = [];
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesRevenueBUM = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/targets?filter[type]=DEALS_INVOICE_PRICE&filter[start_after]=${moment(
            date
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&filter[end_before]=${moment(date)
            .endOf("month")
            .format(
              "YYYY-MM-DD"
            )}&filter[reportable_type]=USER&filter[supervisor_type_level]=2&filter[company_id]=${company_id}`,
          config
        );
        setSalesRevenueBUM(res.data.data);
        console.log(res.data.data);
        // salesRevenueData = res.data.data;
      } catch (error) {
        console.error(error);
      }
    };

    getSalesRevenueBUM();
  }, [company_id, date]);
  let temp = [];

  const handleFollowUpPopup = (user_id) => {
    setPopup(true);
    setUserId(user_id);
  };

  return SalesRevenueBUM ? (
    <div>
      <PopupLayout
        Popup={popup}
        setPopup={() => setPopup()}
        component={
          <FollowUpPopup month={date} userId={userId} company_id={company_id} />
        }
      />
      <Header title={"Sales Revenue - BUM"} back={true} notif={true} />
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search BUM"
            onChange={(e) => setFilterBum(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>
      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>BUM Name</th>
              <th>Revenue</th>
              <th>Target</th>
              <th>Meet Goal</th>
              <th>Follow Up</th>
              <th>Action</th>
            </tr>
            {/* <tr>
              <th style={{ borderRadius: "0px" }}>Hot</th>
              <th>Warm</th>
              <th>Cold</th>
              <th style={{ borderRadius: "0px" }}>Close</th>
            </tr> */}
          </thead>
          <tbody>
            {SalesRevenueBUM ? (
              SalesRevenueBUM.filter((data) => {
                if (filterBum === "") {
                  return data;
                } else if (
                  data.report.name
                    .toLowerCase()
                    .includes(filterBum.toLowerCase())
                ) {
                  return data;
                }
              }).map((data, i) => {
                const percentage = Math.round(
                  (data.value.value / data.target.value) * 100
                );
                console.log(temp[i]);
                return (
                  <tr key={data.id}>
                    <td>{i + 1}.</td>
                    <td>{data.report.name}</td>
                    <td>{data.value.format}</td>
                    <td>{data.target.format}</td>
                    <td
                      style={{
                        color: pickColor(percentage),
                        fontWeight: "bold",
                      }}
                    >
                      {console.log(percentage)}
                      {percentage === Infinity || isNaN(percentage)
                        ? "0"
                        : percentage}{" "}
                      %
                    </td>
                    <td>
                      <div
                        onClick={() => handleFollowUpPopup(data.user.id)}
                        style={{
                          background: "#0553B7",
                          textAlign: "center",
                          width: "70%",
                          borderRadius: "8px",
                          color: "#fff",
                          padding: "5px",
                          cursor: "pointer",
                        }}
                      >
                        See Follow Up
                      </div>
                    </td>
                    <td>
                      <Link
                        to={`/dashboard/sales_revenue_store_leader/${company_id}/${data.user.id}/${date}`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "5px" }}
                          alt="eye"
                        />
                        See Detail
                      </Link>
                    </td>
                  </tr>
                );
              })
            ) : (
              <LoadedComponent />
            )}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  target: state.target,
});

export default connect(mapStateToProps)(SalesRevenueBum);
