import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import LoadedComponent from "../Loaded/LoadedComponent";

const AllBrands = ({ product: { brands } }) => {
  return brands ? (
    <div className="brand">
      <h3>All Brand</h3>
      <div className="row">
        {brands ? (
          brands.data.map((brand) => {
            return (
              <div className="col-md-4" key={brand.id}>
                <Link
                  to={`/dashboard/product/product_by_brand/${
                    brand.name && brand.name
                  }/${brand.id && brand.id}`}
                  style={{ textDecoration: "none", color: "#313132" }}
                >
                  <div className="card">
                    <img
                      src={brand.images[0].url && brand.images[0].url}
                      //   src="/Asset/Discount/Discount 3.jpg"
                      alt=""
                      className="card-image"
                    />
                  </div>
                  <h5>{brand.name && brand.name}</h5>
                </Link>
              </div>
            );
          })
        ) : (
          <h3>Empty</h3>
        )}
      </div>
    </div>
  ) : (
    <LoadedComponent />
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(AllBrands);
