import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { Redirect, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { formatCurrency } from "../../../api/format";
import Button from "../../../components/Button/Button";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import Popup from "../../../components/Popup/Popup";
import AdditionalFeePopup from "../../../components/Popup/Popup Component/AdditionalFeePopup";
import BillingPopup from "../../../components/Popup/Popup Component/BillingPopup";
// import ComporationPopUp from "../../../components/Popup/Popup Component/ComporationPopUp";
import DiscountPopup from "../../../components/Popup/Popup Component/DiscountPopup";
import SelectStockPopup from "../../../components/Popup/Popup Component/SelectStockPopup";
import PopupLayout from "../../../components/Popup/PopupLayout";
import SummaryBilling from "../../../components/Product-Components/Checkout-Components/SummaryBilling";
import { loadActiveCarts } from "../../../redux/action/product";
import NotificationContext from "../../../store/notification-context";

const Checkout = ({ product: { activeCarts } }) => {
  const dispatch = useDispatch();
  const { lead_id } = useParams();

  // notification
  const notificationCtx = useContext(NotificationContext);

  const [refreshAP, setRefreshAP] = useState(false);
  const [isDone, setIsDone] = useState(false);

  const [orderIsVerified, setOrderIsVerified] = useState(false);

  let price = 0;
  for (let i = 0; i < activeCarts.items.length; i++) {
    price = price + activeCarts.items[i].total_price;
  }

  const [billingPopup, setBillingPopup] = useState(false);
  const [discountPopup, setDiscountPopup] = useState(false);
  const [additionalPopup, setAdditionalPopup] = useState(false);
  const [selectStockPopup, setSelectStockPopup] = useState(false);

  const [lead, setLead] = useState(null);
  const [addresses, setAddresses] = useState(null);
  const [interiorDesigns, setInteriorDesigns] = useState(null);
  const [alert, setAlert] = useState(null);

  const [allDiscount, setAllDiscount] = useState([]);

  const [discount_ids, setDiscount_ids] = useState([]);
  const [packingFee, setPackingFee] = useState(null);
  const [shippingFee, setShippingFee] = useState(null);
  const [additionalDiscount, setAdditionalDiscount] = useState(null);
  const [discountType, setDiscountType] = useState(null);
  const [shippingAddressId, setShippingAddressId] = useState(null);
  const [billingAddressId, setBillingAddressId] = useState(null);
  const [note, setNote] = useState(null);
  const [expectedShippingDatetime, setExpectedShippingDatetime] =
    useState(null);
  const [quotationValidUntilDatetime, setQuotationValidUntilDatetime] =
    useState(null);

  const [tempOrder, setTempOrder] = useState(null);

  const [cartDemands, setCartDemands] = useState(null);

  useEffect(() => {
    const unloadCallback = (event) => {
      event.preventDefault();
      event.returnValue = "";
      return "";
    };

    window.addEventListener("beforeunload", unloadCallback);
    return () => window.removeEventListener("beforeunload", unloadCallback);
  }, []);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getLead = async () => {
      try {
        const res = await axios.get(`${mainUrl}/leads/${lead_id}`, config);
        console.log(res.data.data);
        setLead(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    const getInteriorDesign = async () => {
      try {
        const res = await axios.get(`${mainUrl}/interior-designs`, config);
        console.log(res.data.data);
        setInteriorDesigns(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getLead();
    getInteriorDesign();
  }, [lead_id]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getAddress = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/addresses?filter[customer_id]=${lead.customer.id}`,
          config
        );
        console.log(res.data.data);
        setAddresses(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    if (lead) {
      getAddress();
    }
  }, [lead]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const data = {
      items: activeCarts.items ? activeCarts.items : null,
      discount_ids: discount_ids.length !== 0 ? discount_ids : null,
      shipping_address_id: shippingAddressId
        ? parseInt(shippingAddressId)
        : null,
      billing_address_id: billingAddressId ? parseInt(billingAddressId) : null,
      lead_id: lead_id ? parseInt(lead_id) : null,
      note: note,
      shipping_fee: shippingFee,
      packing_fee: packingFee,
      additional_discount: additionalDiscount,
      discount_type: parseInt(discountType),
      expected_shipping_datetime: expectedShippingDatetime,
      quotation_valid_until_datetime: quotationValidUntilDatetime,
    };
    console.log(data);

    const updateOrder = async () => {
      try {
        const res = await axios.post(`${mainUrl}/orders/preview`, data, config);
        console.log(res.data.data);
        setTempOrder(res.data.data);
        setOrderIsVerified(true);
      } catch (error) {
        console.error(error);
        setOrderIsVerified(false);

        if (error.response) {
          console.log(error.response.data.errors.lead_id[0]);
          setAlert(error.response.data.errors.lead_id[0]);
          // console.log(error.response.status);
          // console.log(error.response.headers);
        }
      }
    };

    if (
      activeCarts &&
      shippingAddressId &&
      billingAddressId &&
      lead_id &&
      expectedShippingDatetime &&
      quotationValidUntilDatetime
    ) {
      updateOrder();
    }
  }, [
    activeCarts,
    discount_ids,
    shippingAddressId,
    billingAddressId,
    lead_id,
    note,
    shippingFee,
    packingFee,
    additionalDiscount,
    discountType,
    expectedShippingDatetime,
    quotationValidUntilDatetime,
  ]);

  const handleSubmitOrder = async () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    notificationCtx.showNotification({
      title: "Creating Order...",
      message: "Add Order.",
      status: "pending",
    });

    const data = {
      items: activeCarts.items ? activeCarts.items : null,
      discount_ids: discount_ids.length !== 0 ? discount_ids : null,
      shipping_address_id: shippingAddressId
        ? parseInt(shippingAddressId)
        : null,
      billing_address_id: billingAddressId ? parseInt(billingAddressId) : null,
      lead_id: lead_id ? parseInt(lead_id) : null,
      note: note,
      shipping_fee: shippingFee,
      packing_fee: packingFee,
      additional_discount: additionalDiscount,
      discount_type: parseInt(discountType),
      expected_shipping_datetime: expectedShippingDatetime,
      quotation_valid_until_datetime: quotationValidUntilDatetime,
      expected_price: tempOrder?.total_price && tempOrder.total_price,
    };
    console.log(orderIsVerified);
    if (orderIsVerified) {
      try {
        const res = await axios.post(`${mainUrl}/orders`, data, config);
        console.log(res.data.data);
        setIsDone(true);
        notificationCtx.showNotification({
          title: "Success!",
          message: "Successfully Create Order!",
          status: "success",
        });
        handleDeleteCarts();
      } catch (error) {
        console.error(error);
        notificationCtx.showNotification({
          title: "Error!",
          message: error.message || "Something went wrong!",
          status: "error",
        });
      }
    } else {
      console.log("this order is not verified from preview");
      notificationCtx.showNotification({
        title: "Error!",
        message:
          "This order is not verified from preview" || "Something went wrong!",
        status: "error",
      });
    }
  };

  const handleAddQuantity = (item, i) => {
    console.log(activeCarts);
    const newData = activeCarts.items.map((obj) => {
      if (obj === item) {
        return { ...obj, quantity: item.quantity + 1 };
      }
      return obj;
    });
    console.log(newData);
    const newCart = { items: newData };
    console.log(newCart);
    dispatch(loadActiveCarts(newData));
  };

  const handleAddStatus = (item, i, status) => {
    let is_ready = status === "true" ? true : false;
    console.log(activeCarts);
    const newData = activeCarts.items.map((obj) => {
      if (obj === item) {
        return { ...obj, is_ready };
      }
      return obj;
    });
    console.log(newData);
    const newCart = { items: newData };
    console.log(newCart);
    dispatch(loadActiveCarts(newData));
  };

  const handleDecreseQuantity = (item, i) => {
    const newData = activeCarts.items.map((obj) => {
      if (obj === item) {
        return { ...obj, quantity: item.quantity - 1 };
      }
      return obj;
    });
    console.log(newData);
    const newCart = { items: newData };
    console.log(newCart);
    dispatch(loadActiveCarts(newData));
  };

  const handleDeleteCart = (item) => {
    const newData = activeCarts.items.filter((obj) => obj !== item);
    console.log(newData);
    dispatch(loadActiveCarts(newData));
  };

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getDemandCarts = async () => {
      try {
        const res = await axios.get(`${mainUrl}/cart-demands`, config);
        console.log(res.data.data);
        setCartDemands(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getDemandCarts();
  }, [refreshAP]);

  // Delete All Carts
  const handleDeleteCarts = async () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const data = {
      items: [],
    };
    try {
      await axios.put(`${mainUrl}/carts`, data, config);
    } catch (error) {
      console.error(error);
    }
  };

  const handleDeleteDemandCart = async (cartId) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    try {
      const res = await axios.delete(
        `${mainUrl}/cart-demands/${cartId}`,
        config
      );
      console.log(res.data);
      setRefreshAP(!refreshAP);
    } catch (error) {
      console.error(error);
    }
  };

  return addresses ? (
    <div className="checkout" id="checkout">
      {isDone && <Redirect to={"/dashboard"} />}
      <Popup popup={billingPopup} setPopup={() => setBillingPopup()}>
        <BillingPopup />
      </Popup>

      <Popup popup={discountPopup} setPopup={() => setDiscountPopup()}>
        <DiscountPopup
          setDiscount_ids={setDiscount_ids}
          discount_ids={discount_ids}
          setPopup={setDiscountPopup}
          setAllDiscount={setAllDiscount}
          allDiscount={allDiscount}
        />
      </Popup>

      <Popup popup={additionalPopup} setPopup={() => setAdditionalPopup()}>
        <AdditionalFeePopup
          setPackingFee={setPackingFee}
          setShippingFee={setShippingFee}
          setAdditionalDiscount={setAdditionalDiscount}
          setDiscountType={setDiscountType}
        />
      </Popup>

      <Popup popup={selectStockPopup} setPopup={() => setSelectStockPopup()}>
        <SelectStockPopup refreshAP={refreshAP} setRefreshAP={setRefreshAP} />
      </Popup>

      <Header back="true" title="checkout" icon="" />
      {alert && (
        <div class="alert alert-danger" role="alert">
          {alert}
        </div>
      )}
      <div className="row">
        <div className="col-md-8">
          <div className="billing">
            <div className="d-flex justify-content-between">
              <h5>Shipping To</h5>
              <img
                src="/Asset/Edit.svg"
                alt="edit"
                onClick={() => setBillingPopup(!billingPopup)}
                className="pointer"
              />
            </div>
            <select
              className="form-select"
              name="color"
              // value={filterCompany}
              onChange={(e) => setShippingAddressId(e.target.value)}
            >
              <option value="">Click to select a lead / prospect</option>
              {addresses.map((address) => {
                return (
                  <option value={address.id} key={address.id}>
                    {`${address.address_line_1}, ${address.city} , ${address.province}, ${address.postcode}, ${address.phone}`}
                  </option>
                );
              })}
            </select>
          </div>

          <hr />

          <div className="shipping">
            <div className="d-flex justify-content-between">
              <h5>Billing To</h5>
              <img src="/Asset/Edit.svg" alt="edit" />
            </div>

            <select
              className="form-select"
              name="color"
              // value={filterCompany}
              onChange={(e) => setBillingAddressId(e.target.value)}
            >
              <option value="">Click to select a lead / prospect</option>
              {addresses.map((address) => {
                return (
                  <option value={address.id} key={address.id}>
                    {`${address.address_line_1}, ${address.city} , ${address.province}, ${address.postcode}, ${address.phone}`}
                  </option>
                );
              })}
            </select>
          </div>

          <hr />

          <div className="interior-design">
            <div className="d-flex justify-content-between">
              <h5>Interior Design</h5>
            </div>
            <div className="mt-2">
              <select
                className="form-select"
                name="companys"
                // style={{ marginLeft: "10px", height: "41px" }}
                // value={filterCompany}
                // onChange={(e) => setFilterCompany(e.target.value)}
              >
                <option value="">Select Company</option>
                {interiorDesigns &&
                  interiorDesigns.map((interiorDesign) => {
                    return (
                      <option value={interiorDesign.id} key={interiorDesign.id}>
                        {interiorDesign.name}
                      </option>
                    );
                  })}
              </select>
            </div>
          </div>

          <hr />

          <div className="product-card">
            <div className="d-flex justify-content-between">
              <h5>Product</h5>
            </div>
            {activeCarts &&
              activeCarts.items.map((item, i) => {
                return (
                  <div
                    className="d-flex justify-content-between mt-3 mb-3"
                    key={item.id}
                  >
                    <div className="d-flex">
                      <img src="/Asset/no image.jpg" alt="product_img" />
                      <div className="desc">
                        <h5>{item.name}</h5>
                        <ul>
                          <li>{item.covering.name}</li>
                          <li>{item.colour.name}</li>
                        </ul>
                        <h5>{formatCurrency(item.total_price)}</h5>
                        <select
                          className="form-select"
                          name="companys"
                          // style={{ marginLeft: "10px", height: "41px" }}
                          // value={filterCompany}
                          onChange={(e) =>
                            handleAddStatus(item, i, e.target.value)
                          }
                        >
                          <option value={true}>Select Status</option>
                          <option value={true}>Ready</option>
                          <option value={false}>Indent</option>
                        </select>
                      </div>
                    </div>
                    <div className="d-flex">
                      <div className="qty-form d-flex align-items-center text-center">
                        <button
                          className="decrease"
                          onClick={() => handleDecreseQuantity(item, i)}
                        >
                          -
                        </button>
                        <div className="qty-input">{item.quantity}</div>
                        <button
                          className="increase"
                          // onClick={() => setQty(qty + 1)}
                          onClick={() => handleAddQuantity(item, i)}
                        >
                          +
                        </button>
                      </div>
                      {/* <h5>Plaza Indonesia</h5> */}
                      <div>
                        <img
                          src="/Asset/Times.png"
                          alt="delete"
                          style={{ width: "24px" }}
                          onClick={() => handleDeleteCart(item)}
                        />
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>

          <div>
            <h5>Tambah Product</h5>
            {cartDemands &&
              cartDemands.items.map((item) => {
                return (
                  <div
                    className="d-flex align-items-center justify-content-between"
                    style={{
                      border: ".5px solid #cacaca",
                      padding: "10px",
                      margin: "5px 0",
                      borderRadius: "4px",
                    }}
                  >
                    <div>
                      <h5>{item.name}</h5>
                      <p>{formatCurrency(item.price)}</p>
                      <p>Qty : {item.quantity}</p>
                    </div>
                    <div>
                      <button
                        className="btn btn-danger"
                        style={{ padding: "8px 16px" }}
                        onClick={() => handleDeleteDemandCart(item.id)}
                      >
                        Delete
                      </button>
                    </div>
                  </div>
                );
              })}

            <div
              className="pointer"
              onClick={() => setSelectStockPopup(!selectStockPopup)}
            >
              <Button
                title="+ Add New Product"
                icon=""
                btnStyle={"primary-btn"}
              />
            </div>
          </div>

          <div className="row mt-3">
            <div
              className="col-6 pointer"
              onClick={() => setDiscountPopup(!discountPopup)}
            >
              <div className="discount d-flex justify-content-between align-items-center">
                <div>
                  {allDiscount.length !== 0 ? (
                    allDiscount.map((discount, idx) => {
                      return (
                        <div
                          className="p-2"
                          style={{
                            border: "1px solid #000",
                            marginBottom: "5px",
                            borderRadius: "4px",
                          }}
                          key={idx}
                        >
                          <div className="d-flex align-items-center">
                            <img src="/Asset/Discount.svg" alt="discount" />
                            <h5 style={{ margin: "0 5px" }}>
                              {discount ? discount.name : "Discount"}
                            </h5>
                          </div>

                          {discount && (
                            <div className="mt-2">
                              <p>{discount.description}</p>
                            </div>
                          )}
                        </div>
                      );
                    })
                  ) : (
                    <div>
                      <div className="d-flex align-items-center">
                        <img src="/Asset/Discount.svg" alt="discount" />
                        <h5 style={{ margin: "0 5px" }}>Discount</h5>
                      </div>
                    </div>
                  )}
                </div>
                <img src="/Asset/Arrow Icon/RightArrow.png" alt="rightarrow" />
              </div>
            </div>
            <div
              className="col-6 pointer"
              onClick={() => setAdditionalPopup(!additionalPopup)}
            >
              <div className="discount d-flex justify-content-between align-items-center">
                <div>
                  <div className="d-flex align-items-center">
                    <img src="/Asset/Delivery.svg" alt="delivery" />
                    <h5 style={{ margin: "0 5px" }}>Addtional Fee</h5>
                  </div>

                  <div className="mt-2">
                    <p>{packingFee && `Packing fee: ${packingFee}`}</p>
                    <p>{shippingFee && `Shipping fee: ${shippingFee}`}</p>
                    <p>
                      {additionalDiscount &&
                        `Additional Discount: ${additionalDiscount}`}
                    </p>
                  </div>
                </div>

                <img src="/Asset/Arrow Icon/RightArrow.png" alt="rightarrow" />
              </div>
            </div>
          </div>
          {/* <div className="mt-3">
            <input
              className="form-control"
              type="text"
              placeholder="Default input"
              aria-label="default input example"
            />
          </div> */}

          <hr />

          <div className="delivery-date mt-3">
            <h5>Expected Delivery Date</h5>

            <div className="mt-2">
              <input
                className="form-control"
                type="date"
                placeholder="Default input"
                aria-label="default input example"
                onChange={(e) => setExpectedShippingDatetime(e.target.value)}
              />
            </div>
          </div>

          <div className="delivery-date mt-3">
            <h5>Valid Quotation Date</h5>

            <div className="mt-2">
              <input
                className="form-control"
                type="date"
                placeholder="Default input"
                aria-label="default input example"
                onChange={(e) => setQuotationValidUntilDatetime(e.target.value)}
              />
            </div>
          </div>

          <div className="notes mt-3">
            <h5>Notes (Optional)</h5>

            <div className="mt-2">
              <textarea
                className="form-control"
                type="text"
                placeholder="Default input"
                onChange={(e) => setNote(e.target.value)}
                aria-label="default input example"
              />
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <SummaryBilling
            price={price}
            cartDemands={cartDemands}
            tempOrder={tempOrder}
            handleSubmitOrder={handleSubmitOrder}
          />
        </div>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  product: state.product,
});

export default connect(mapStateToProps)(Checkout);
