import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { mainUrl } from "../../api/api";
import LoadedComponent from "../Loaded/LoadedComponent";

const CommentList = (props) => {
  const { activity_id, refresh } = props;
  const [comments, setComments] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getCommentsActivity = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities/${activity_id ? activity_id : ""}/comments`,
          config
        );
        console.log(res.data.data);
        setComments(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    if (activity_id) {
      getCommentsActivity();
    }
  }, [activity_id, refresh]);
  return comments ? (
    <div>
      <h3>Activity Comments</h3>
      {comments.map((comment) => {
        return (
          <div className="d-flex pb-3" key={comment.id}>
            <div
              className="d-flex justify-content-center align-items-center"
              style={{
                padding: "5px",
                borderRadius: "50%",
                margin: 0,
                background: "#17949D",
                color: "#C4C4C4",
                width: "50px",
                height: "45px",
              }}
            >
              <h3 style={{ margin: 0 }}>{comment.user.initial}</h3>
            </div>
            <div style={{ margin: "5px 10px", width: "100%" }}>
              <div className="d-flex justify-content-between">
                <h3>{comment.user.name}</h3>
                <p>{moment(comment.created_at).format("DD MMMM YYYY | LT")}</p>
              </div>
              <div
                style={{
                  padding: "20px",
                  backgroundColor: "#eaeaea",
                  borderRadius: "8px",
                }}
              >
                <p>{comment.content}</p>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  ) : (
    <LoadedComponent />
  );
};

export default CommentList;
