import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import parse from "html-react-parser";

const Promo = () => {
  const [promos, setPromos] = useState(null);
  const { category_id, category_name } = useParams();
  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getPromos = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/promos?filter[promo_category_id]=${category_id}`,
          config
        );
        console.log(res.data.data);
        setPromos(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getPromos();
  }, [category_id]);

  return promos ? (
    <div className="promo" id="promo">
      <Header title="Promo" back={true} />

      <div className="row">
        {promos.map((promo) => {
          return (
            <div className="col-md-4" key={promo.id}>
              <Link
                to={`/dashboard/promo/promo_detail/${promo.id}`}
                style={{ textDecoration: "none", color: "#313132" }}
              >
                <div className="card">
                  <img
                    src={promo.images[0].url}
                    //   src="/Asset/Discount/Discount 3.jpg"
                    alt=""
                    className="card-image"
                  />
                </div>
                <h4 className="mt-2">{promo.name}</h4>
                <p>
                  {promo.description
                    ? parse(promo.description.substring(0, 75))
                    : ""}
                  ...
                </p>
              </Link>
            </div>
          );
        })}
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default Promo;
