import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { formatCurrency } from "../../../api/format";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";

const BrandCategory = () => {
  const { brand_category_name, brand_id, company_id, month } = useParams();

  const [filterMonth, setFilterMonth] = useState(month);
  const [salesEstimationData, setSalesEstimationData] = useState(null);
  const [filterSales, setFilterSales] = useState("");

  

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesEstimation = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/sales-estimation/${brand_id}?start_at=${moment(
            filterMonth
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&end_at=${moment(filterMonth)
            .endOf("month")
            .format("YYYY-MM-DD")}`,
          config
        );
        console.log(res.data);
        const salesEstimation = [];
        for (const [key, value1] of Object.entries(res.data)) {
          let data = [];
          for (const [key, value] of Object.entries(value1)) {
            data.push(value);
          }
          salesEstimation.push({ data: data });
        }
        console.log(salesEstimation);
        setSalesEstimationData(salesEstimation);
      } catch (error) {
        console.error(error);
      }
    };

    getSalesEstimation();
  }, [brand_id, filterMonth]);


  const sortUp = () => {
    // salesEstimationData
    // salesEstimationData.sort(function(a, b){return a-b});
    setSalesEstimationData(salesEstimationData.sort());
  };

  return salesEstimationData ? (
    <div>
      <Header
        title={`Brand Category - ${brand_category_name}`}
        back={true}
        notif={true}
      />
      {/* {salesRevenue.length !== 0 && ( */}
      <div className="filter-form d-flex justify-content-between">
        <div>
          <input
            type="text"
            id="search-sales"
            name="search-sales"
            className="form-control"
            placeholder="Search Sales"
            onChange={(e) => setFilterSales(e.target.value)}
            style={{ height: "41px", width: "327px" }}
          />
        </div>
        <div>
          <input
            type="month"
            id="start"
            name="start"
            className="form-control"
            placeholder="Start Month"
            value={filterMonth}
            onChange={(e) => setFilterMonth(e.target.value)}
            style={{ height: "41px" }}
          />
        </div>
      </div>

      <div className="mt-4">
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th onClick={() => sortUp()}>Sales</th>
              <th>Customer Name</th>
              <th>Brand</th>
              <th>Order Value</th>
              <th>Estimated Value</th>
            </tr>
          </thead>
          <tbody>
            {salesEstimationData && salesEstimationData
              .filter((data) => data.data[0].sales.includes(filterSales))
              .map((data, i) => {
                return (
                  <tr key={i}>
                    <td>{i + 1}.</td>
                    <td>{data.data[0].sales}</td>
                    <td>{data.data[0].customer}</td>
                    <td>
                      <ul>
                        {data.data.map((d, i) => {
                          return <li key={i}>{d.brand}</li>;
                        })}
                      </ul>
                    </td>
                    <td>
                      <ul>
                        {data.data.map((d, i) => {
                          return (
                            <li key={i}>{formatCurrency(d.order_value)}</li>
                          );
                        })}
                      </ul>
                    </td>
                    <td>
                      <ul>
                        {data.data.map((d, i) => {
                          return (
                            <li key={i}>{formatCurrency(d.estimated_value)}</li>
                          );
                        })}
                      </ul>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
      
    </div>
  ) : (
    <Loaded />
  );
};

export default BrandCategory;
