import React from "react";
import "./Button.scss";

const Button = (props) => {
  return (
    <button
      className={`btn ${props.btnStyle}`}
      style={{ width: !props.btnWidth ? "150px" : props.btnWidth }}
      disabled={props.disabled}
    >
      {props.icon && <img src={props.icon} alt="user" className="btn-icon" />}
      {props.title}
    </button>
  );
};

export default Button;
