import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";

import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";

const InteriorDesignDetail = () => {
  const { company_id, month, interiorDesignId } = useParams();

  const [interiorDesignDetail, setInteriorDesignDetail] = useState(null);
  const [date, setDate] = useState(month);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getInteriorDesignDetail = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/interior-designs/detail/${interiorDesignId}`,
          config
        );
        setInteriorDesignDetail(res.data);
        console.log(res.data);
      } catch (error) {}
    };

    getInteriorDesignDetail();
  }, [date, company_id, interiorDesignId]);
  return interiorDesignDetail ? (
    <div>
      <Header title={"Interior Design"} back={true} notif={true} user={false} />
      {/* <div className="filter-form d-flex justify-content-between">
          <div>
            <input
              type="text"
              id="search-sales"
              name="search-sales"
              className="form-control"
              placeholder="Search BUM"
              style={{ height: "41px", width: "327px" }}
            />
          </div>
          <div>
            <input
              type="month"
              id="start"
              name="start"
              className="form-control"
              placeholder="Start Month"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              style={{ height: "41px" }}
            />
          </div>
        </div> */}
      <div className="mt-4">
        <h3>Interior Design Detail</h3>
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No.</th>
              <th>Date</th>
              <th>Channel</th>
              <th>Sales</th>
              <th>Revenue</th>
            </tr>
          </thead>
          <tbody>
            {interiorDesignDetail.map((data) => {
                return (
                  <tr>
                    <td>1.</td>
                    <td>{moment(data.date).format("DD-MMM-YY")}</td>
                    <td>{data.channel}</td>
                    <td>{data.sales}</td>
                    <td>{data.total}</td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default InteriorDesignDetail;
