import axios from "axios";
import moment from "moment";
import React, { Fragment, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { mainUrl } from "../../api/api";
import { formatCurrency } from "../../api/format";
import { loadChannels } from "../../redux/action/target";
import Button from "../Button/Button";
import FilterLeadsReport from "../FilterForm/FilterLeadsReport";
import Header from "../Header/Header";
import Activity from "../ActivityDetail/Activity";

const ReportLeadDetail = () => {
  const params = useParams();
  const { user_type, user_id, detail_type, start, end } = params;

  const [reportDataDeal, setReportDataDeal] = useState(null);
  const [reportDataHot, setReportDataHot] = useState(null);
  const dispatch = useDispatch();
  // filter
  const [filterCompany, setFilterCompany] = useState();
  const [activityDetail, setActivityDetail] = useState(false);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [orderId, setOrderId] = useState(null);
  const handleClickDetail = async (activity) => {
    try {
      const res = await axios.get(
        `${mainUrl}/activities?filter[id]=${activity.activity.id}`,
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      );
      setCustomerActivity(res.data.data[0]);
      console.log(res.data);
    } catch (error) {
      console.error(error);
    }
    setActivityDetail(!activityDetail);
    // setCustomerActivity(activity);
    setOrderId(activity.order ? activity.order.id : null);
    console.log(activity.activity);
  };
  // const [filterChannels, setFilterChannels] = useState();
  const [filterDate, setFilterDate] = useState();
  const [filterStartDate, setFilterStartDate] = useState(
    start !== "now" ? start : ""
  );
  const [filterEndDate, setFilterEndDate] = useState(end !== "now" ? end : "");
  // const [filterSupervisior, setFilterSupervisor] = useState();
  useEffect(() => {
    dispatch(loadChannels(filterCompany));
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getReportDeal = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-leads/closing-deals?start_date=${
            filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
          }&end_date=${
            filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
          }&company_id=${filterCompany ? filterCompany : ""}&${
            user_type === "bum"
              ? "supervisor_id=" + parseInt(user_id)
              : user_type === "channel"
              ? "channel_id=" + parseInt(user_id)
              : user_type === "sales"
              ? "sales_id=" + parseInt(user_id)
              : ""
          }`,
          config
        );
        setReportDataDeal(res.data);
      } catch (error) {
        console.error(error);
      }
    };
    const getReportHot = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/dashboard/report-leads/hot?start_date=${
            filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
          }&end_date=${
            filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
          }&company_id=${filterCompany ? filterCompany : ""}&${
            user_type === "bum"
              ? "supervisor_id=" + parseInt(user_id)
              : user_type === "channel"
              ? "channel_id=" + parseInt(user_id)
              : user_type === "sales"
              ? "sales_id=" + parseInt(user_id)
              : ""
          }`,
          config
        );
        console.log(res.data);
        setReportDataHot(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    if (detail_type === "hot") {
      getReportHot();
    } else if (detail_type === "deal") {
      getReportDeal();
    }
  }, [
    filterDate,
    dispatch,
    user_type,
    user_id,
    detail_type,
    filterCompany,
    filterStartDate,
    filterEndDate,
  ]);

  const exportReportDeal = async () => {
    axios
      .get(
        `${mainUrl}/dashboard/report-leads/closing-deals?start_date=${
          filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
        }&end_date=${
          filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
        }&company_id=${filterCompany ? filterCompany : ""}&${
          user_type === "bum"
            ? "supervisor_id=" + parseInt(user_id)
            : user_type === "channel"
            ? "channel_id=" + parseInt(user_id)
            : user_type === "sales"
            ? "sales_id=" + parseInt(user_id)
            : ""
        }&is_export=1`,
        {
          method: "GET",
          responseType: "blob", // important
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      )
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute(
          "download",
          `Report Brands detail ${filterDate}.xlsx`
        );
        document.body.appendChild(link);
        link.click();
      })
      .catch((err) => {
        console.log();
      });
  };

  const exportReportHot = async () => {
    axios
      .get(
        `${mainUrl}/dashboard/report-leads/hot?start_date=${
          filterStartDate ? moment(filterStartDate).format("YYYY-MM-DD") : ""
        }&end_date=${
          filterEndDate ? moment(filterEndDate).format("YYYY-MM-DD") : ""
        }&company_id=${filterCompany ? filterCompany : ""}&${
          user_type === "bum"
            ? "supervisor_id=" + parseInt(user_id)
            : user_type === "channel"
            ? "channel_id=" + parseInt(user_id)
            : user_type === "sales"
            ? "sales_id=" + parseInt(user_id)
            : ""
        }&is_export=1`,
        {
          method: "GET",
          responseType: "blob", // important
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.token}`,
          },
        }
      )
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `ReportLeads Hot ${filterDate}.xlsx`);
        document.body.appendChild(link);
        link.click();
      })
      .catch((err) => {
        console.log();
      });
  };

  return (
    <div>
      <Header
        icon=""
        title={`Report Leads - ${
          detail_type === "hot"
            ? reportDataHot && reportDataHot.length !== 0
              ? user_type === "bum"
                ? reportDataHot[0].bum
                : user_type === "channel"
                ? reportDataHot[0].channel
                : user_type === "sales"
                ? reportDataHot[0].name
                : ""
              : ""
            : reportDataDeal && reportDataDeal.length !== 0
            ? user_type === "bum"
              ? reportDataDeal[0].bum
              : user_type === "channel"
              ? reportDataDeal[0].channel
              : user_type === "sales"
              ? reportDataDeal[0].name
              : ""
            : ""
        }`}
        back={false}
        user={false}
      />

      <FilterLeadsReport
        company={false}
        channel={false}
        supervisor={false}
        download={detail_type === "hot" ? exportReportHot : exportReportDeal}
        setFilterStartDate={setFilterStartDate}
        setFilterEndDate={setFilterEndDate}
      />
      {detail_type === "hot" ? (
        <>
          {/* <div>
            <div
              onClick={() => exportReportHot()}
              style={{ width: "fit-content" }}
            >
              <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
            </div>
          </div> */}
          <div style={{ overflow: "scroll" }}>
            <table className="" style={{ margin: "10px 0" }}>
              <thead>
                <tr>
                  <th>No.</th>
                  <th>Date</th>
                  <th>Name</th>
                  <th>Channel</th>
                  <th>Quotation</th>
                  <th>Estimated Value</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {reportDataHot &&
                  reportDataHot.map((data, idx) => {
                    return (
                      <tr key={idx}>
                        <td>{idx + 1}</td>
                        <td>
                          {moment(data?.activity.created_at).format(
                            "YYYY-MM-DD"
                          )}
                        </td>
                        <td>{data?.customer?.first_name}</td>
                        <td>{data.channel}</td>

                        <td>
                          {data.quotation && formatCurrency(data.quotation)}
                        </td>
                        <td>
                          {data.estimated_value &&
                            formatCurrency(data.estimated_value)}
                        </td>
                        <td>
                          <div
                            onClick={() => handleClickDetail(data)}
                            className="activity-detail-btn"
                          >
                            <img
                              src="/Asset/Action Icon/eye.png"
                              style={{ marginRight: "5px" }}
                              alt="eye"
                            />
                            See Detail
                          </div>
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
        </>
      ) : detail_type === "deal" ? (
        <>
          {/* <div>
            <div
              onClick={() => exportReportDeal()}
              style={{ width: "fit-content" }}
            >
              <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
            </div>
          </div> */}
          <table className="" style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>No.</th>
                <th>Date</th>
                <th>Name</th>
                <th>Channel</th>
                <th>Invoice Number</th>
                <th>Invoice Price</th>
                <th>Amount Paid</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {reportDataDeal &&
                reportDataDeal.map((data, idx) => {
                  return (
                    <tr key={idx}>
                      <td>{idx + 1}</td>
                      <td>
                        {moment(data?.activity.created_at).format("YYYY-MM-DD")}
                      </td>
                      <td>{data?.customer?.first_name}</td>
                      <td>{data.channel}</td>
                      <td>{data.invoice_number}</td>
                      <td>
                        {data.invoice_price &&
                          formatCurrency(data.invoice_price)}
                      </td>
                      <td>
                        {data.amount_paid && formatCurrency(data.amount_paid)}
                      </td>
                      <td>
                        <div
                          onClick={() => handleClickDetail(data)}
                          className="activity-detail-btn"
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </div>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </>
      ) : (
        ""
      )}

      {/* {reportByLeads.last_page && (
        <div className="d-flex justify-content-between">
          <ReactPaginate
            previousLabel={"Prev"}
            nextLabel={"Next"}
            breakLabel={"..."}
            pageCount={reportByLeads.last_page}
            marginPagesDisplayed={3}
            // pageRangeDisplayed={3}
            onPageChange={handlePageClick}
            containerClassName={"pagination"}
            pageClassName={"page-item"}
            pageLinkClassName={"page-link"}
            previousClassName={"page-item"}
            previousLinkClassName={"page-link"}
            nextClassName={"page-item"}
            nextLinkClassName={"page-link"}
            breakClassName={"page-item"}
            breakLinkClassName={"page-link"}
            activeClassName={"active"}
          />
          <div
            onClick={() => exportReportsByLeads()}
            style={{ width: "fit-content" }}
          >
            <Button icon="" title="Export excel" btnStyle={"outline-btn"} />
          </div>
        </div>
      )} */}
      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  );
};

export default ReportLeadDetail;
