import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Header from "../../../components/Header/Header";
import { isValidate } from "../../../lib/Validate";

const CustomerDesigner = ({ target: { designers } }) => {
  return (
    <div className="customers" id="customers">
      <Header icon="" title="Customers - Designer" back={false} user={false} />
      <div>
        <table className="data" style={{ margin: "10px 0" }}>
          <thead>
            <tr>
              <th>No</th>
              <th>Designer Name</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {designers &&
              designers.data.map((designer, idx) => {
                return (
                  <tr key={designer.id}>
                    <td>{idx + 1}</td>
                    <td>{!isValidate(designer) && designer.name}</td>

                    <td>
                      <Link
                        to={`/dashboard/customers/designer/${designer.id}/leads`}
                      >
                        <img
                          src="/Asset/Action Icon/eye.png"
                          style={{ marginRight: "11px" }}
                          alt="eye"
                        />
                        See Details
                      </Link>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
        {/* {pageData.last_page && (
      <div>
        <ReactPaginate
          previousLabel={"Prev"}
          nextLabel={"Next"}
          breakLabel={"..."}
          pageCount={Math.ceil(pageData.last_page)}
          marginPagesDisplayed={3}
          // pageRangeDisplayed={3}
          onPageChange={handlePageClick}
          containerClassName={"pagination"}
          pageClassName={"page-item"}
          pageLinkClassName={"page-link"}
          previousClassName={"page-item"}
          previousLinkClassName={"page-link"}
          nextClassName={"page-item"}
          nextLinkClassName={"page-link"}
          breakClassName={"page-item"}
          breakLinkClassName={"page-link"}
          activeClassName={"active"}
        />
      </div>
    )} */}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  target: state.target,
});

export default connect(mapStateToProps)(CustomerDesigner);
