import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../../../api/api";
import { formatCurrency } from "../../../api/format";
import Activity from "../../../components/ActivityDetail/Activity";
import Header from "../../../components/Header/Header";
import Loaded from "../../../components/Loaded/Loaded";
import NoData from "../../../components/Loaded/NoData";

const SalesRevenue = ({ auth: { user, loading } }) => {
  const { user_id, channel_id, company_id, month } = useParams();

  const [salesRevenue, setSalesRevenue] = useState(null);
  const [filterSales, setFilterSales] = useState("");
  const [date, setDate] = useState(month);

  const [activityDetail, setActivityDetail] = useState(false);
  const [customerActivity, setCustomerActivity] = useState(null);
  const [orderId, setOrderId] = useState(null);
  // const [orderData, setOrderData] = useState(null);
  const [commentsData, setCommentsData] = useState(null);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getSalesRevenue = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities?filter[user_id]=${user_id}&filter[channel_id]=${channel_id}&filter[company_id]=${company_id}&filter[follow_up_datetime_before]=${moment(
            date
          )
            .endOf("month")
            .format("YYYY-MM-DD")}&filter[follow_up_datetime_after]=${moment(
            date
          )
            .startOf("month")
            .format("YYYY-MM-DD")}&filter[has_payment]=true`,
          config
        );
        setSalesRevenue(res.data.data);
        console.log(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getSalesRevenue();
  }, [user_id, channel_id, company_id, date]);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    // const getOrderData = async () => {
    //   try {
    //     const res = await axios.get(
    //       `${mainUrl}/orders?filter[id]=${orderId}`,
    //       config
    //     );
    //     console.log(res.data.data[0]);
    //     setOrderData(res.data.data[0]);
    //   } catch (error) {
    //     console.error(error);
    //   }
    // };

    const getCommentsActivity = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/activities/${
            customerActivity.id ? customerActivity.id : ""
          }/comments`,
          config
        );
        console.log(res.data.data);
        setCommentsData(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    // if (orderId) {
    //   getOrderData();
    // }

    if (customerActivity) {
      getCommentsActivity();
    }
  }, [orderId, customerActivity]);

  const handleClickDetail = (activity) => {
    setActivityDetail(!activityDetail);
    setCustomerActivity(activity);
    setOrderId(activity.order ? activity.order.id : "");
    // return(

    // )
    console.log(activity);
  };
  return salesRevenue ? (
    <div>
      <Header title={"Sales Revenue - Sales"} back={true} notif={true} />
      {salesRevenue.length !== 0 && (
        <div className="filter-form d-flex justify-content-between">
          <div>
            <input
              type="text"
              id="search-sales"
              name="search-sales"
              className="form-control"
              placeholder="Search customer name/inv number"
              onChange={(e) => setFilterSales(e.target.value)}
              style={{ height: "41px", width: "327px" }}
            />
          </div>
          <div>
            <input
              type="month"
              id="start"
              name="start"
              className="form-control"
              placeholder="Start Month"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              style={{ height: "41px" }}
            />
          </div>
        </div>
      )}

      {salesRevenue.length === 0 ? (
        <NoData />
      ) : (
        <div className="mt-4">
          <table className="data" style={{ margin: "10px 0" }}>
            <thead>
              <tr>
                <th>No</th>
                <th>Invoice</th>
                <th>Customer Name</th>
                <th>Revenue</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {salesRevenue
                .filter((data) => {
                  if (filterSales === "") {
                    return data;
                  } else if (
                    data.order.invoice_number
                      .toLowerCase()
                      .includes(filterSales.toLowerCase())
                  ) {
                    return data;
                  } else if (
                    `${data.customer.first_name} ${
                      data.customer.last_name ? data.customer.last_name : ""
                    }`
                      .toLowerCase()
                      .includes(filterSales.toLowerCase())
                  ) {
                    return data;
                  }
                })
                .map((data, i) => {
                  return (
                    <tr key={data.id}>
                      <td>{i + 1}.</td>
                      <td>{data.order.invoice_number}</td>
                      <td>{`${data.customer.first_name} ${
                        data.customer.last_name ? data.customer.last_name : ""
                      }`}</td>
                      <td>{formatCurrency(data.order.total_price)}</td>

                      <td>
                        <div
                          onClick={() => handleClickDetail(data)}
                          className="activity-detail-btn"
                        >
                          <img
                            src="/Asset/Action Icon/eye.png"
                            style={{ marginRight: "5px" }}
                            alt="eye"
                          />
                          See Detail
                        </div>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      )}
      {customerActivity ? (
        <Activity
          activityDetail={activityDetail}
          setActivityDetail={setActivityDetail}
          customerActivity={customerActivity}
          // orderData={orderData}
          // user={user}
          comments={commentsData}
          orderId={orderId}
        />
      ) : (
        ""
      )}
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(SalesRevenue);
